//
// Buttons
// --------------------------------------------------



// Alternate buttons
// --------------------------------------------------
.btn {
  position: relative;
  display: inline-block;
  margin-bottom: 0; // For input.btn
  font-weight: $btn-font-weight;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid transparent;
  white-space: nowrap;
  outline:none !important;
  @include button-size($padding-base-vertical,  $padding-base-horizontal, $font-size-base, $line-height-base, $btn-border-radius-base);
  @include user-select(none);

  &,
  &:active, 
  &.active {
    &:focus,
    &.focus {
      @include tab-focus;
    }
  }

  &:hover,   
  &:focus,
  &.focus {
    color: $btn-default-color;
    text-decoration: none;
  }

  &:active,  
  &.active {
    outline: 0;
    background-image: none;
    @include box-shadow($btn-shadow-active);
  }
     
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: $cursor-disabled;
    @include box-shadow(none);
  } 
  >span{
    margin-left:5px;
  }    
  .oasicon, 
  .glyphicon{     
    font-size:15px;
    top:3px;
  }

  // [converter] extracted a& to a.btn
}

a.btn {
  &.disabled,
  fieldset[disabled] & {
    pointer-events: none; // Future-proof disabling of clicks on `<a>` elements
  }
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  @include button-variant($btn-default-color, $btn-default-bg, $btn-default-border,
  $btn-default-color-hover, $btn-default-bg-hover, $btn-default-border-hover,
  $btn-default-color-hover, $btn-default-bg-hover,
  $btn-default-bg-to, $btn-default-bg-hover-to, $btn-default-bg-hover-to, 
  $btn-default-clicked-color,
  $btn-default-shadow-opacity, $btn-default-shadow-opacity-hover,$btn-default-shadow-opacity-active);
  .badge{
    color: $btn-default-badge-color;
    background-color: $btn-default-badge-bg;
   }
  
}

.btn-primary {
  @include button-variant($btn-primary-color, $btn-primary-bg, $btn-primary-border,
    $btn-primary-color-hover, $btn-primary-bg-hover, $btn-primary-border-hover,
    $btn-primary-color-hover, $btn-primary-bg-hover,
    $btn-primary-bg-to, $btn-primary-bg-hover-to, $btn-primary-bg-hover-to, 
    $btn-primary-clicked-color,
    $btn-primary-shadow-opacity, $btn-primary-shadow-opacity-hover,$btn-primary-shadow-opacity-active)
}


.btn-info {
  @include button-variant($btn-info-color, $btn-info-bg, $btn-info-border,
    $btn-info-color-hover, $btn-info-bg-hover, $btn-info-border-hover,
    $btn-info-color-hover, $btn-info-bg-hover,
    $btn-info-bg-to, $btn-info-bg-hover-to, $btn-info-bg-hover-to, 
    $btn-info-clicked-color,
    $btn-info-shadow-opacity, $btn-info-shadow-opacity-hover,$btn-info-shadow-opacity-active);
}

// Success appears as green
.btn-success {
  @include button-variant($btn-success-color, $btn-success-bg, $btn-success-border,
    $btn-success-color-hover, $btn-success-bg-hover, $btn-success-border-hover,
    $btn-success-color-hover, $btn-success-bg-hover,
    $btn-success-bg-to, $btn-success-bg-hover-to, $btn-success-bg-hover-to, 
    $btn-success-clicked-color,
    $btn-success-shadow-opacity, $btn-success-shadow-opacity-hover,$btn-success-shadow-opacity-active);
}

// Warning appears as orange
.btn-warning  {
  @include button-variant($btn-warning-color, $btn-warning-bg, $btn-warning-border,
    $btn-warning-color-hover, $btn-warning-bg-hover, $btn-warning-border-hover,
    $btn-warning-color-hover, $btn-warning-bg-hover,
    $btn-warning-bg-to, $btn-warning-bg-hover-to, $btn-warning-bg-hover-to, 
    $btn-warning-clicked-color,
    $btn-warning-shadow-opacity, $btn-warning-shadow-opacity-hover,$btn-warning-shadow-opacity-active);
}
// Danger and error appear as red
.btn-danger {
  @include button-variant($btn-danger-color, $btn-danger-bg, $btn-danger-border,
    $btn-danger-color-hover, $btn-danger-bg-hover, $btn-danger-border-hover,
    $btn-danger-color-hover, $btn-danger-bg-hover,
    $btn-danger-bg-to, $btn-danger-bg-hover-to, $btn-danger-bg-hover-to, 
    $btn-danger-clicked-color,
    $btn-danger-shadow-opacity, $btn-danger-shadow-opacity-hover,$btn-danger-shadow-opacity-active);
} 


// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  color: $link-color;
  font-weight: normal;
  border-radius: 0;

  &,
  &:active,
  &.active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    @include box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: $btn-link-disabled-color;
      text-decoration: none;
    }
  }
}


// Button Sizes
// --------------------------------------------------

//同表单元素区分，表单改了原先尺寸
$btn-padding-large-vertical:    	10px;

.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  @include button-size($btn-padding-large-vertical, $padding-large-horizontal, $font-size-large, $line-height-large, $btn-border-radius-large);
  .oasicon,
  .glyphicon{
    font-size:17px;
  }
}
.btn-sm {
  // line-height: ensure proper height of button next to small input
  @include button-size($padding-small-vertical, $padding-small-horizontal, $font-size-small, $line-height-small, $btn-border-radius-small);
  .oasicon,
  .glyphicon{
    top: 2px;
    font-size:13px;
  }
}
.btn-xs {
  @include button-size($padding-xs-vertical, $padding-xs-horizontal, $font-size-small, $line-height-small, $btn-border-radius-small);
  .oasicon,
  .glyphicon{
    top: 2px;
    font-size:12px;
  }
}


// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}

.oas-switch-btn{
   float: left;
   height: 30px;
   background: #3c9ce6;
   border-radius: 14px;
   padding:0 2px;
   >li{
     float: left;
     margin:3px 0 0 0;
     &:first-child{
       >a{
         margin:0;
       }
     }
     &.active{
       >a{
         background: #fff;
         color:#1f7ec7;
         z-index: 11;
       }
     }
     >a{
       float: left;
       position: relative;
       height: 24px;
       line-height: 24px;
       color: #90c8f3;
       padding:0 12px;
       border-radius: 14px;
       text-decoration: none;
     }
   }
}

//2017-07-26 add
.oas-switch-form-btn{
        
    position: relative;
    display: inline-block;
    height: 30px;
    line-height: 28px;
    min-width: 44px;
    vertical-align: middle;
    border-radius: 20px;
    border: 1px solid $gray-7;
    @include background-linear-gradient-to-bottom($gray-5,$gray-7);
    box-shadow: 0 10px 10px rgba(161,171,179,.5) inset;
    cursor: pointer;

    label{
        max-width: none;
        margin-bottom: 0;
        cursor: pointer;
    }

    .switch-inner{
        position: relative;
        display: block;
        padding-left: 28px;
        padding-right: 10px;
        color: #fff;
        font-size: 14px;

        i{
            top: 3px;
            font-size: 18px;
            color: #fff;
        }
      
    }

    .switch-close{
      text-align: right;
    }
    .switch-open{
        display: none;
        text-align: left;
    }

    [type="radio"]{
        display: none;
    }
    
    &.oas-switch-checked{
        border-color: $blue-7;
        @include background-linear-gradient-to-bottom($blue-5,$blue-7);
        box-shadow: 0 10px 10px $blue-7 inset;

        &:after{
            left: 100%;
            margin-left: - 25px;
        };
        
        .switch-inner{
            padding-left: 10px;
            padding-right: 28px;
        }
        .switch-open{
            display: block;
        }
        .switch-close{
            display: none;
        }
    }

    &:after{
        position: absolute;
        top: 50%;
        left: 3px;
        content: "";
        width: 22px;
        height: 22px;
        margin-top:  -11px;
        border-radius: 50%;
        @include background-linear-gradient-to-bottom($gray-2,$gray-4);
        cursor: pointer;
        box-shadow: 0 5px 5px rgba(255,255,255,.1) inset;
        -webkit-transition: all .36s cubic-bezier(.78,.14,.15,.86);
        transition: all .36s cubic-bezier(.78,.14,.15,.86);
        z-index: 2;
    }
    input[type="radio"] + span{
        padding-left: 0;
    }
    input[type="radio"] + span:before, 
    input[type="radio"] + span:after{

        display: none;
    }
    input[type="radio"]:checked + span:after{
        display: none;
    }   

}

.btn-label {
  color: $btn-label-color;
  background-color: $btn-label-bg;
  border-color: $btn-label-border;
  &.in-dark{
      color:$btn-label-in-dark-color;
      &.disabled,
      &[disabled],
      fieldset[disabled] & {
        &,
        &:hover,
        &:focus,
        &.focus,
        &:active,
        &.active {
          color:$brand-disable;
          border-color: transparent;
        }
      }
  }
  &:hover{
      color: $btn-label-color-hover;
      border-color: $btn-label-border-hover;
  }
  &:active{
    box-shadow: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
      &,
      &:hover,
      &:focus,
      &.focus,
      &:active,
      &.active {
        color: $btn-label-disabled-color;
        border-color: $btn-label-disabled-border;
      }
  }
}



