//
// Buttons group dropdown(设置按钮组的dropdown样式) 
// --------------------------------------------------


// Alternate buttons
// --------------------------------------------------

  @include buttons-dropdown-menu(".btn-default",$dp-btn-color,$dp-btn-default-border-color, $dp-btn-default-bg,$dp-btn-color-hover,$dp-btn-default-shadow-color);
  @include btn-group-border(".btn-default",$dp-btn-default-border-color,$btn-group-default-border-left-color,$btn-group-default-border-right-color);
  .btn-default ~ .dropdown-menu{
    border-color: $dp-btn-info-border-color;
  }

  @include buttons-dropdown-menu(".btn-primary",$dp-btn-color,$dp-btn-primary-border-color, $dp-btn-primary-bg,$dp-btn-color-hover,$dp-btn-primary-shadow-color);
  @include btn-group-border(".btn-primary",$dp-btn-primary-border-color,$btn-group-primary-border-left-color,$btn-group-primary-border-right-color);

// Success appears as green

  @include buttons-dropdown-menu(".btn-success",$dp-btn-color,$dp-btn-success-border-color, $dp-btn-success-bg,$dp-btn-color-hover,$dp-btn-success-shadow-color);
  @include btn-group-border(".btn-success",$dp-btn-success-border-color,$btn-group-success-border-left-color,$btn-group-success-border-right-color);

// Info appears as blue-green

  @include buttons-dropdown-menu(".btn-info",$dp-btn-color,$dp-btn-info-border-color, $dp-btn-info-bg,$dp-btn-color-hover,$dp-btn-info-shadow-color);
  @include btn-group-border(".btn-info",$dp-btn-info-border-color,$btn-group-info-border-left-color,$btn-group-info-border-right-color);

// Warning appears as orange

  @include buttons-dropdown-menu(".btn-warning",$dp-btn-color,$dp-btn-warning-border-color, $dp-btn-warning-bg,$dp-btn-color-hover,$dp-btn-warning-border-color);
  @include btn-group-border(".btn-warning",$dp-btn-warning-border-color,$btn-group-warning-border-left-color,$btn-group-warning-border-right-color);

// Danger and error appear as red

  @include buttons-dropdown-menu(".btn-danger",$dp-btn-color,$dp-btn-danger-border-color, $dp-btn-danger-bg,$dp-btn-color-hover,$dp-btn-danger-shadow-color);
  @include btn-group-border(".btn-danger",$dp-btn-danger-border-color,$btn-group-danger-border-left-color,$btn-group-danger-border-right-color);
