    transform: translate(0, -35px); }
  50% {
    -webkit-transform: translate(0, 35px);
    transform: translate(0, 35px); }
  100% {
    -webkit-transform: translate(0, -35px);
    transform: translate(0, -35px); } }

.ld.ld-wander-px-v {
  -webkit-animation: ld-wander-px-v 1s infinite ease-out;
  animation: ld-wander-px-v 1s infinite ease-out; }
