$radio-size:                            14px;
$checkbox-size:                         14px;
$radio-dot-size:                        6px;
$rc-border-color:                       $gray-5; // 正常color
$rc-main-color:                         $blue-6; // 已选color
$rc-disabled-color:                     $gray-4; // 禁用color
$rc-disabled-checked-color:             $gray-6; // 已选禁用border颜色

$radio-checked-bg-from:                 $blue-6;
$radio-checked-bg-to:                   $blue-8;
$radio-checked-border:                  $blue-7;

$checkbox-border-radius:                2px;
$checkbox-bg-from:                      $gray-3;
$checkbox-bg-to:                        $gray-4;
$checkbox-inset-color:                  rgba(255,255,255, .4);

$checkbox-checked-bg-from:              $blue-5;
$checkbox-checked-bg-to:                $blue-7;
$checkbox-checked-inset-color:          rgba(255,255,255, .4);

$checkbox-disabled-checked-bg-from:      $gray-6;
$checkbox-disabled-checked-bg-to:        $gray-7;
$checkbox-disabled-checked-inset-color:  rgba(255,255,255, .4);

$single-muti-select-bgc: #fff;
$single-muti-select-color: $blue-6;
$single-muti-select-border-hover: $blue-6;