
//== Labels
//
//##

// Default label background color
$label-default-bg:            					$gray-4;
$label-default-bg-hover:            			$gray-5;
$label-default-icon:                            $gray-7;
// Primary label background color
$label-primary-bg:            					$blue-6;
$label-primary-bg-hover:            			$blue-7;
$label-primary-icon:                            #fff;
// Success label background color
$label-success-bg:            					$green-6;
$label-success-bg-hover:            			$green-7;
$label-success-icon:                            #fff;
// Info label background color
$label-info-bg:               					$blue-5;
$label-info-bg-hover:               			$blue-6;
$label-info-icon:                               #fff;
// Warning label background color
$label-warning-bg:            					$orange-6;
$label-warning-bg-hover:            			$orange-7;
$label-warning-icon:                            #fff;
// Danger label background color
$label-danger-bg:             					$red-6;
$label-danger-bg-hover:             			$red-7;
$label-danger-icon:                             #fff;

// Default label text color
$label-default-color:                            $text-1;
$label-color:                 					#fff;
// Default text color of a linked label
$label-link-hover-color:      					#fff;




