.oas-tree{
	.oastree-root{
		position: relative;
		white-space: nowrap;
		line-height: 26px;
		height: 28px;
		&.node-disabled{
			&>a{
				cursor: not-allowed;
				i{
					color: $tree-disabled-span-color;
				}
				&.current{
				    background-color: transparent;
				    border: 1px solid $tree-border-color;
				}
				span{
					color: $tree-disabled-span-color !important;
				}
			}
		}
		a{
			position: relative;
			top: 2px;
			text-decoration: none;
			display: inline-block;
			padding: 0 3px;
			vertical-align: middle;
			i{
				position: relative;
				top: 4px;
				color: $tree-icon-color;
				font-size: 18px;
			}
			&.current{
			    background-color: $tree-chosen-bgc;
			    border: 1px $tree-chosen-bgc solid;
			    line-height: 24px;
			}
			span{
				font-size: 14px;
				color: $tree-span-color;
				vertical-align: middle;
			}
		}
		&>span{
			position: absolute;
			display: none;
			width: 1px;
			height: 8px;
			left: 13px;
			top: 29px;
			background-color: $tree-line-color;
			&.lg{
				height: 14px;
			}
			&.show{
				display: inline-block;
			}
			&>i{
				position: absolute;
				display: inline-block;
				width: 13px;
				height: 1px;
				left: 0;
				bottom: 0;
				background-color: $tree-line-color;
			}
		}
	}
	li{
		white-space: nowrap;
		list-style: none;
		line-height: normal;
		&.node-disabled{
			&>a{
				cursor: not-allowed;
				i{
					color: $tree-disabled-span-color;
				}
				&.current{
				    background-color: transparent;
				    border: 1px solid $tree-border-color;
				}
				span{
					color: $tree-disabled-span-color !important;
				}
			}
		}
		&>input{
			position: relative;
			top: 3px;
			left: 2px;
			&.chk-hide{
				display: none;
			}
		}
		&>a{
			text-decoration: none;
			display: inline-block;
			padding: 0 2px;
			vertical-align: middle;
			margin-left: -9px;
			i{
				position: relative;
				top: 2px;
				color: $tree-icon-color;
				font-size: 14px;
			}
			&.current{
			    background-color: $tree-chosen-bgc;
			    border: 1px $tree-chosen-bgc solid;
			    height: 24px;
			    line-height: 22px;
			}
			span{
				color: $tree-span-color;
				font-size: 12px;
				&.bigger{
					color: $tree-span-big-color;
				}
			}
		}
		&>span{
			line-height: 0;
		    margin: 0;
		    width: 26px;
		    height: 24px;
		    display: inline-block;
		    vertical-align: middle;
		    border: 0 none;
		    cursor: pointer;
		    outline: none;
		    background-color: transparent;
		    background-attachment: scroll;
		    background-repeat: no-repeat;
		    background-image: url("../images/tree_icons.png");
		    &.root-open{
		    	background-position: -52px -78px;
		    }
		    &.root-close{
		    	background-position: -26px -78px;
		    }
		    &.roots-open{
		    	background-position: -52px 0;
		    }
		    &.roots-close{
		    	background-position: -26px 0;
		    }
		    &.roots-docu{
		    	background-position: 0 0;
		    }
		    &.root-docu{
		    	background-position: 0 -78px;
		    }
			&.bottom-open{
		    	background-position: -52px -52px;
		    }
		    &.bottom-close{
		    	background-position: -26px -52px;
		    }
		    &.center-open{
		    	background-position: -52px -26px;
		    }
		    &.center-close{
		    	background-position: -26px -26px;
		    }
		    &.center-docu{
		    	background-position: 0 -26px;
		    }
		    &.bottom-docu{
		    	background-position: 0 -52px;
		    }
		}
		&.node-hide{
			ul{
				display: none;
			}
		}
		ul{
			&.ul-hide{
				display: none;
			}
			&.line{
				background: url(../images/line.png) 0 0 repeat-y;
			}
			padding: 0 0 0 26px;
		}
	}
	.highLabel{
		font-style: normal;
		font-weight: bold;
		color: $tree-highLabel-color;
		font-size: 12px;
		top: 0;
	}
	.treeContainer{
		.oas-no-data{
			margin-top: 20px;
		}
	}
	.searchInput{
		width: 200px;
	}
}

.oas-tree{
	.beauty-check{
		margin-bottom: 3px;
	}
}