.oas-query {
    // GROUPS
    .rules-group-container {
        position: relative;
        border-radius: $group-border-radius;
        border: 1px solid $group-border-color;
        background: $group-background-color;
        padding: $group-padding;
        padding-bottom: #{$group-padding - $item-vertical-spacing};
        margin-bottom: 5px;
    }

    .btn {
        padding: 2px 10px;
    }

    .rules-group-header {
        margin-bottom: $group-padding;
        .btn.active {
            @include background-linear-gradient-to-bottom($query-con-bg-active-from, $query-con-bg-active-to);
            border-color: $query-con-border-active;
            box-shadow: none;
        }
    }

    .rules-group-body {
        .rules-list {
            height: 100%;
            position: relative;
            padding: 0 0 0 #{nth($ticks-position, 1) + nth($ticks-position, 2)};
            margin: 0;
            padding-top: 1px;
        }
        // 分叉样式
        .rules-list>* {
            &::before,
            &::after {
                content: '';
                position: absolute;
                left: #{-1 * nth($ticks-position, 2)};
                width: nth($ticks-position, 2);
                height: calc(50% + #{$item-vertical-spacing});
                border-color: $ticks-color;
                border-style: solid;
            }

            &::before {
                top: #{-2 * $ticks-width};
                border-width: 0 0 $ticks-width $ticks-width;
            }

            &::after {
                top: 50%;
                border-width: 0 0 0 $ticks-width;
            }

            &:first-child::before {
            top: #{-$group-padding - $ticks-width - 2};
            height: calc(50% + #{$group-padding + $item-vertical-spacing});
            }

            &:last-child::before {
            border-radius: 0 0 0 #{2 * $ticks-width};
            }

            &:last-child::after {
            display: none;
            }
        }

        .rule-container {
            position: relative;
            box-shadow: 0 0 1px $rule-shadow;
            background: $rule-background-color;
            border-radius: $rule-border-radius;
            border: solid 1px $rule-border-color;
            padding: $rule-padding;
            margin: 4px 0;  
            .rule-filter-container,
            .rule-operator-container,
            .rule-value-container {
                height: 30px;
                line-height: 28px;
                display: inline-block;
                margin: 0 8px 0 0;
                vertical-align: middle;
            }

            .rule-value-container {
                padding-left: 10px;
                &.seperator {
                    border-left: 1px solid $rule-value-separator-color;
                }
                .oas-calendar-startTime, .oas-calendar-endTime{
                    height: 28px;   
                }
                > .form-control {
                    margin-right: 10px;
                }
            }
            .rule-value {
                padding-left: 10px;
                vertical-align: 1px;
            }
        }
    }
}