// Alerts

@mixin alert-variant($text-color, $border-color, $box-shadow, $alert-close-color, $alert-close-hover-color) {
  
  border: solid 1px $border-color;
  @if $box-shadow != false {
    box-shadow: $box-shadow;
  }
  color: $text-color;
  border: solid 1px $border-color;
  .oasicon {
    color: $text-color;
  }
  .alert-link {
    color: $blue-6;
  }
  .close {
    color: $alert-close-color;
    &:hover {
      color: $alert-close-hover-color;
    }
  }
}
