//
// oas-tabsCard
// --------------------------------------------------
/**/
.oas-tabsCard{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1060;
  border: 1px solid #bdbdbd;
  border-radius: 3px;
  box-shadow: 1px 3px 7px #bdbdbd;
  background-color: #fff;
  padding:38px 0 46px 0;
  min-width: 200px;
  .tabsCard-loading{
    min-height: 123px;
    height: 100%;
    background: url("../images/loading.gif") center center no-repeat;
  }
}
.oas-tabsCard-title{
   position: absolute;
   left:0;
   right:0;
   top:0;
   h3{
     height: 38px;
     line-height: 38px;
     padding: 0 20px;
     border-bottom: 1px solid #e4e4e4;
     color: #212121;
     font-size: 16px;
     font-weight: bold;
   }
   .title-btn{
     float: right;
     margin-right: 10px;
     font-size: 18px;
     margin-top: 8px;
     color: #bdbdbd; 
     text-decoration: none;
     &:hover{
      color: #01579b;
     }
   }  
}
.oas-tabsCard-content{
  padding: 10px;
  overflow-y:auto;
  min-height: 123px;
  min-width: 168px 
}
.oas-tabsCard-tabs{
  .oas-tabs-hd{
    height: 36px;
    line-height: 36px;
    border: 1px solid #e0e0e0;
    background-color: #fbfbfb;
    ul{
      float: left;
      margin-top: -1px;
/*      margin-left: -1px;*/
    }
    li{
      float: left;
      border: 1px solid transparent;
      border-bottom-width:0;
      border-radius: 3px 3px 0 0; 
      &:first-child{
        position:relative;
        left:-1px;
      }
       &:last-child{
        position:relative;
        right:-1px;
      }
      a{
       display: block;
       min-width: 93px;
       padding: 0 10px;
       line-height: 35px;
       text-align: center;
       color: #212121;
       text-decoration: none;
       &:hover{
         color: #01579b;
       }
      }
      &.current{
        background-color: #e8f2fe;
        border-color: #e0e0e0;
        border-top-color: #9e9e9e;  
      } 
    }
  } 
}
.oas-tabsCard-top{
    position: relative;
    height: 84px;
    padding: 0 10px 0 90px;
    background-color: #e8f2fe;
    .tab-img{
      float:left;
      width: 65px;
      height: 65px;
      margin-top: 8px;
      margin-left: -80px;
      background-position: center;
      background-repeat: no-repeat;
    }
    .tab-texts{
      padding-top: 18px;
      li{
        margin-bottom: 3px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    .tab-text{
      font-size: 18px;
      .text-key{
        color: #616161;
      }
    }
    .tab-subtext{
      color: #616161;
    }
    .tab-ramark{
      position: absolute;
      top: 10px;
      right: 10px;
      width: 77px;
      height: 63px;
      background-repeat: no-repeat;
      background-position: center;
    }
  } 
.oas-tabsCard-main{
  margin-top: 10px;
  min-height: 60px;
}
.oas-tabsCard-footer{
  position:absolute;
  left: 0;
  right:0;
  bottom:0;
  height: 46px;
  line-height: 46px;
  padding: 0 10px;
  border-top: 1px solid #e4e4e4;
  background-color: #fbfbfb;
  border-radius:  0 0 3px 3px;
  button{
    float: right;
    margin-top: 7px;
    margin-right: 10px;
    &:first-child{
      margin-right: 0;
    }
  }
}