.oas-vertical-menu{
    width: 194px;
    box-shadow: $vmenu-box-shadow;
    .whole-switch{
        height: 30px;
        background-color: $vmenu-whole-switch-bgc;
        cursor: pointer;
        text-align: center;
        line-height: 30px;
        color: $vmenu-open-menu-icon-color;
    }
    .firstlevel-menu{
        >li{
            width: 194px;
            background: $vmenu-firstlevel-menu-bgc-to;
            background:-webkit-linear-gradient($vmenu-firstlevel-menu-bgc-from, $vmenu-firstlevel-menu-bgc-to );
            background:-moz-linear-gradient($vmenu-firstlevel-menu-bgc-from, $vmenu-firstlevel-menu-bgc-to);
            background:-o-linear-gradient($vmenu-firstlevel-menu-bgc-from, $vmenu-firstlevel-menu-bgc-to);
            background:linear-gradient($vmenu-firstlevel-menu-bgc-from, $vmenu-firstlevel-menu-bgc-to);
            opacity: .7;
            // background-color: $vmenu-firstlevel-menu-bgc;
            border-top: 1px solid $vmenu-firstlevel-menu-border-color;
            >a{
                >div{
                    padding: 0 12px 0 20px;
                    height: 40px;
                    >.menu-icon{
                        margin-right: 6px;
                        float: left;
                        position: relative;
                        top: 11px;
                        display: block;
                        width: 18px;
                        height: 18px;
                    }
                    .firstlevel-label{
                        float: left;
                        font-size: 14px;
                        line-height: 40px;
                        font-weight: bold;
                        color: $vmenu-firstlevel-menu-label-color;
                    }
                    .oas-vm-icon{
                        float: right;
                        line-height: 40px;
                        font-size: 12px;
                        color:$vmenu-firstlevel-menu-icon-color;
                    }
                }
            }
        }
    }
    .vmClose{
        display: none;
    }
    .secondlevel-menu,.thirdlevel-menu{
        // box-shadow: $vmenu-box-shadow;
        >li{
            background-color: $vmenu-second-third-level-menu-bgc;
            >a{
                >div{
                    height: 36px;
                    >.menu-icon{
                        margin-right: 6px;
                        float: left;
                        position: relative;
                        top: 9px;
                        display: block;
                        width: 18px;
                        height: 18px;
                    }
                    >.oas-vm-label{
                        height: 36px;
                        line-height: 36px;
                        float: left;
                        color: $vmenu-second-third-level-menu-label-color;
                        font-size: 13px;
                    }
                    >.oas-vm-icon{
                        line-height: 36px;
                        font-size: 12px;                    
                    }
                }
            } 
        }
    }
    .secondlevel-menu{
        >li{
            >a{
                >div{
                    padding: 0 12px 0 40px;
                    .oas-vm-icon{
                        float: right;
                        color: $vmenu-secondlevel-menu-icon-color;
                    }
                }
            }
        }
    }
    .thirdlevel-menu{
        >li{
            >a{
                >div{
                    padding: 0 12px 0 60px;
                }
            }
        }
    }
    .menucontainer{
        cursor: pointer;
        &.active{
            >.oas-vm-label{
                color: $vmenu-menucontainer-color-active;
                }
        }
        &:hover{
            // background: $vmenu-menucontainer-hover-bgc-color;
            @include background-linear-gradient-to-bottom($vmenu-menucontainer-hover-bgc-from, $vmenu-menucontainer-hover-bgc-to);
            >.oas-vm-label{
                color: $vmenu-menucontainer-label-color-hover;
            }
            >.oas-vm-icon{
                color:$vmenu-menucontainer-icon-color-hover;
                
            }
        }
    }
    .nochild{
        >div{
            .oas-vm-icon{
                display: none;
            }
        }
    }
    &.default{                
        .oas-vm-icon{
            transition: transform .2s ease-in;
            transform: rotate(0);
        }   
        .close-animation{
            >.oas-vm-icon{
                transform: rotate(180deg);
            }
        }
    }

    &.right{
        ul{
            min-width: 194px;
            >li{
                position: relative;
                &:hover{
                    >ul{
                        display: block;
                    }
                }
            }
            
        }
        .secondlevel-menu,.thirdlevel-menu{
            display: none;
            position: absolute;
            top: 0;
            left: 100%;
            margin-left: 2px;
            &:before{
                content: '';
                position: absolute;
                height: 100%;
                width: 1px;
                left: -1px;
            }
            >li{
                >a{
                    >div{
                        padding-left: 20px;
                    }
                }
            }
        }
        li{
            &:hover{
                // background: $vmenu-menucontainer-hover-bgc-color;
                 @include background-linear-gradient-to-bottom($vmenu-menucontainer-hover-bgc-from, $vmenu-menucontainer-hover-bgc-to);
                >a{
                    >.menucontainer{
                        >.oas-vm-label{
                            color: $vmenu-menucontainer-label-color-hover;
                        }
                        >.oas-vm-icon{
                            color:$vmenu-menucontainer-icon-color-hover;
                            
                        }
                    }  
                }
            }
        }
    }
    &.dark{
        box-shadow: $vmenu-box-shadow-dark;
        background-color: $vmenu-bgc-dark;
        .whole-switch{
            background-color: $vmenu-whole-switch-bgc-dark;
            color: $vmenu-open-menu-icon-color-dark;
        }
        .firstlevel-menu{
            >li{
                background: $vmenu-firstlevel-menu-bgc-to-dark;
                background:-webkit-linear-gradient($vmenu-firstlevel-menu-bgc-from-dark, $vmenu-firstlevel-menu-bgc-to-dark );
                background:-moz-linear-gradient($vmenu-firstlevel-menu-bgc-from-dark, $vmenu-firstlevel-menu-bgc-to-dark);
                background:-o-linear-gradient($vmenu-firstlevel-menu-bgc-from-dark, $vmenu-firstlevel-menu-bgc-to-dark);
                background:linear-gradient($vmenu-firstlevel-menu-bgc-from-dark, $vmenu-firstlevel-menu-bgc-to-dark);
                opacity: .7;
                // background-color: $vmenu-firstlevel-menu-bgc;
                border-top: 1px solid $vmenu-firstlevel-menu-border-color-dark;
                >a{
                    >div{
                        .firstlevel-label{
                            color: $vmenu-firstlevel-menu-label-color-dark;
                        }
                        .oas-vm-icon{
                            color:$vmenu-firstlevel-menu-icon-color-dark;
                        }
                    }
                }
            }
        }
        .secondlevel-menu,.thirdlevel-menu{
            // box-shadow: $vmenu-box-shadow;
            >li{
                background-color: $vmenu-second-third-level-menu-bgc-dark;
                >a{
                    >div{
                        >.oas-vm-label{
                            color: $vmenu-second-third-level-menu-label-color-dark;
                        }
                    }
                } 
            }
        }
        .secondlevel-menu{
            >li{
                >a{
                    >div{
                        .oas-vm-icon{
                            color: $vmenu-secondlevel-menu-icon-color-dark;
                        }
                    }
                }
            }
        }
        .menucontainer{
            cursor: pointer;
            &.active{
                >.oas-vm-label{
                    color: $vmenu-menucontainer-color-active-dark;
                }
            }
            &:hover{
                // background: $vmenu-menucontainer-hover-bgc-color;
                @include background-linear-gradient-to-bottom($vmenu-menucontainer-hover-bgc-from-dark, $vmenu-menucontainer-hover-bgc-to-dark);
                >.oas-vm-label{
                    color: $vmenu-menucontainer-label-color-hover-dark;
                }
                >.oas-vm-icon{
                    color:$vmenu-menucontainer-icon-color-hover-dark;
                    
                }
            }
        }
        &.right{
            li{
                &:hover{
                    // background: $vmenu-menucontainer-hover-bgc-color;
                    @include background-linear-gradient-to-bottom($vmenu-menucontainer-hover-bgc-from-dark, $vmenu-menucontainer-hover-bgc-to-dark);
                    >a{
                        >.menucontainer{
                            >.oas-vm-label{
                                color: $vmenu-menucontainer-label-color-hover-dark;
                            }
                            >.oas-vm-icon{
                                color:$vmenu-menucontainer-icon-color-hover-dark;
                            }
                        }  
                    }
                }
            }
        }
    }
}