//
// Alerts
// --------------------------------------------------


// Base styles
// -------------------------

.alert {
  position: relative;
  padding: $alert-padding;
  margin-bottom: $line-height-computed;
  border: 1px solid transparent;
  border-radius: $alert-border-radius;

  // Headings for larger alerts
  h4 {
    margin-top: 0;
    // Specified for the h4 to prevent conflicts of changing $headings-color
    color: inherit;
  }

  // Provide class for links that match alerts
  .alert-link {
    font-weight: $alert-link-font-weight;
  }

  // Improve alignment and spacing of inner content
  > p,
  > ul {
    margin-bottom: 0;
  }

  > p + p {
    margin-top: 5px;
  }

  .oasicon {
      margin-right: $padding-base-horizontal;  //10px
      vertical-align: -2px;
      font-size: 18px;
  }
}

// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.alert-dismissable, // The misspelled .alert-dismissable was deprecated in 3.2.0.
.alert-dismissible {
  // padding-right: ($alert-padding + 20);

  // Adjust close link position
  .close {
    position: absolute;
    top: 50%;
    margin-top: -15px;
    right: 15px;
    font-size: 30px;
    opacity: 1 !important;
    filter: alpha(opacity=100);
    text-shadow: none;
    font-weight: lighter;

    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif, "微软雅黑";
  }
}

// Alternate styles
//  
// Generate contextual modifier classes for colorizing the alert.

.alert-success {
  @include alert-variant($alert-success-text, $alert-success-border, $alert-success-shadow, $alert-success-close, $alert-success-close-hover);
  @include background-linear-gradient-to-bottom($alert-success-bg-from, $alert-success-bg-to);
}

.alert-info {
  @include alert-variant($alert-info-text, $alert-info-border, $alert-info-shadow, $alert-info-close, $alert-info-close-hover);
  @include background-linear-gradient-to-bottom($alert-info-bg-from, $alert-info-bg-to);
}

.alert-warning {
  @include alert-variant($alert-warning-text, $alert-warning-border, $alert-warning-shadow, $alert-warning-close, $alert-warning-close-hover);
  @include background-linear-gradient-to-bottom($alert-warning-bg-from, $alert-warning-bg-to);
}

.alert-danger {
  @include alert-variant($alert-danger-text, $alert-danger-border, $alert-danger-shadow, $alert-danger-close, $alert-danger-close-hover);
  @include background-linear-gradient-to-bottom($alert-danger-bg-from, $alert-danger-bg-to);
}

