//
// tabs
// --------------------------------------------------
// @import "compass/utilities/sprites";
// $tabs_icon:sprite-map("tabs/*.png", $spacing:10px);
/**/
.oas-tabs{
  /*A级分栏*/
  &.styleA{
    width: $tabs-a-item-width;
    border: 1px solid $tabs-a-border-color;
    background-color: $tabs-a-bg-color;
    .tab-items{
      li{
        position: relative;
        height: $tabs-a-item-height;
        a{
          position: absolute;
          top: 0;
          right: -1px;
          bottom: 0;
          left: -1px;
          padding-top: 15px;
          text-align: center;
          text-decoration: none;
          >.oasicon{
            font-size: $tabs-a-item-icon-size;
            color: $tabs-a-item-normal-icon-color;
          }
          >.tabs-name{
            margin-top: 3px;
            font-size: $tabs-a-item-font-size;
            color: $tabs-a-item-normal-font-color;
          }
        }
        &:hover{
          a{
            >.oasicon{
              color: $tabs-a-item-hover-font-color;
            }
            >.tabs-name{
               color: $tabs-a-item-hover-font-color;
            }
          }
        }
        &.current{
          a{
            // position: relative;
            // background: image-url($tabsA_triangle);
            background-repeat: no-repeat;
            @include background-linear-gradient-to-bottom($tabs-a-item-normal-bg-color-from, $tabs-a-item-normal-bg-color-to);
            >.oasicon{
              color: $tabs-a-item-current-font-color;
            }
            >.tabs-name{
               color: $tabs-a-item-current-font-color;
            }
            &::after{
              content: '';
              position: absolute;
              right: 0;
              top: 42px;
              background: image-url($tabsA_triangle);
              width: 10px;
              height: 16px;
            }
          }
/*          &:after{
            content: "";
            position: absolute;
            top:50%;
            right: -1px;
            margin-top: - $tabs-a-item-triange-width;
            border: $tabs-a-item-triange-width solid transparent;
            border-right-color: #fff; 
          }*/
        } 
      }
    }
  }
  &.styleB{
    background-color: $tabs-b-bg-color;
    .tab-items{
      font-size: 0;
      border-bottom: 1px solid $tabs-b-item-border-color;
      li{
        position: relative;
        top:1px;
        display: inline-block;
        *display: inline;
        *zoom: 1;
        margin-right: $tabs-b-item-diff;
        border: $tabs-b-item-border-width solid $tabs-b-item-border-color;
        border-top-left-radius: $tabs-b-border-radius;
        border-top-right-radius: $tabs-b-border-radius;
        a{
          display: block;
          padding:$tabs-b-item-padding-vertical $tabs-b-item-padding-horizontal;
          // background-color: $tabs-b-item-normal-bg-color;
          @include background-linear-gradient-to-bottom($tabs-b-item-normal-bg-color-from, $tabs-b-item-normal-bg-color-to);
          font-size: 0;

          >.oasicon{
            font-size: $tabs-b-item-icon-size;
            color: $tabs-b-item-normal-icon-color;
            margin-right: $tabs-b-item-icon-font-diff;
            top:0;
            vertical-align: middle;
          }
          >.tabs-name{
            position: relative;
            display: inline-block;
            font-size: $tabs-b-item-font-size;
            color: $tabs-b-item-normal-font-color;
            vertical-align: middle;
            &.hasNotification{
              &:after{
                position: absolute;
                content: '';
                display: inline-block;
                width: 6px;
                height: 6px;
                top: 2px;
                right: -6px;
                border-radius: 6px;
                background-color: $tabs-b-circle-color;
              }
            }
          }
          >.badge{
            font-size: 14px;
            margin-left: 5px;
          }
          
        }
        &:hover{
          a{
            @include background-linear-gradient-to-bottom($tabs-b-item-current-bg-color-from-current, $tabs-b-item-current-bg-color-to-current);
            >.oasicon{
              color: $tabs-b-item-hover-icon-color;
            }
            >.tabs-name{
              color: $tabs-b-item-hover-font-color;
            }
          }
        }
        &.current{
          border-color: $tabs-b-item-current-border-color;
          a{
            @include background-linear-gradient-to-bottom($tabs-b-item-current-bg-color-from-current, $tabs-b-item-current-bg-color-to-current);
             cursor:default;
            >.oasicon{
              color: $tabs-b-item-current-icon-color;
            }
            >.tabs-name{
               color: $tabs-b-item-current-font-color;
            }            
          }
        } 
      }
    }
    .expand-content{
      float: right;
      margin-right: 10px;
      padding-top: 7px;
      font-size: 0;
      a{
        float: right;
        padding:$tabs-b-operator-padding-vertical 0 $tabs-b-operator-padding-vertical - 1;
        >.oasicon{
          float: left;
          top: 0;
          font-size: $tabs-b-item-icon-size;
          color: $tabs-b-item-normal-operator-icon-olor;
          vertical-align: middle;
        }
        >.operator-text{
          display: inline-block;
          font-size: $tabs-b-item-font-size;
          height: $tabs-b-item-font-size + 2;
          line-height: $tabs-b-item-font-size + 2;
          color: $tabs-b-item-normal-operator-font-color;
          vertical-align: middle;
        }
        &:first-child{
          .operator-text{
            border-right-width: 0;
          }
        }
        &.menu-setting{
          .oasicon{
            margin-right: $tabs-b-item-icon-font-diff;
          }
          .operator-text{
            padding-right: 10px;
          }
        }
        &.menu-assist{
          text-align: right;
          .oasicon{
            margin-left: $tabs-b-item-icon-font-diff;
          }
          .operator-text{
            padding-left: 10px;
          }
        }
        &:hover{
           >.oasicon,>.operator-text{
               color: $tabs-b-item-hover-operator-color;
           }
        }
      }
    } 
    &.large{
      .tab-items{
        a{
          padding:$tabs-b-item-padding-vertical-large $tabs-b-item-padding-horizontal;
        }
      }
      .expand-content{
        padding-top: 16px;
      }
    }
    &.small{
      .tab-items{
        font-size: 12px;
        a{
          padding:$tabs-b-item-padding-vertical-small $tabs-b-item-padding-horizontal;
          >i{
            font-size: 14px;
          }
          .badge{
            font-size: 12px;
          }
        }
      }
      .expand-content{
        padding-top: 2px;
        .operator-text{
          font-size: 12px;
        }
        i{
          font-size: 14px;
        }
      }
    }
  }
  /*C级分栏*/
  &.styleC{

    @include tabsStyleC-variant($tabs-c-a-type,$tabs-c-a-border-width,$tabs-c-a-border-color,$tabs-c-a-shadow-width,$tabs-c-a-shadow-color,$tabs-c-a-item-height,$tabs-c-a-item-padding-vertical
      ,$tabs-c-a-item-padding-horizontal,$tabs-c-a-item-icon-font-diff,$tabs-c-a-item-diff,$tabs-c-a-item-font-size,$tabs-c-a-item-icon-size,$tabs-c-a-item-normal-icon-color
      ,$tabs-c-a-item-normal-font-color,$tabs-c-a-item-normal-bg-color,$tabs-c-a-item-current-icon-color,$tabs-c-a-item-current-font-color
      ,$tabs-c-a-item-current-bg-color-from,$tabs-c-a-item-current-bg-color-to,$tabs-c-a-item-normal-operator-color,$tabs-c-a-item-hover-operator-color,
      $tabs-c-a-item-image-normal,$tabs-c-a-item-image-current,$tabs-c-a-circle-color,$tabs-c-a-item-padding-vertical-large,$tabs-c-a-item-padding-vertical-middle);

    @include tabsStyleC-variant($tabs-c-b-type,$tabs-c-b-border-width,$tabs-c-b-border-color,$tabs-c-b-shadow-width,$tabs-c-b-shadow-color,$tabs-c-b-item-height,$tabs-c-b-item-padding-vertical
      ,$tabs-c-b-item-padding-horizontal,$tabs-c-b-item-icon-font-diff,$tabs-c-b-item-diff,$tabs-c-b-item-font-size,$tabs-c-b-item-icon-size,$tabs-c-b-item-normal-icon-color
      ,$tabs-c-b-item-normal-font-color,$tabs-c-b-item-normal-bg-color,$tabs-c-b-item-current-icon-color,$tabs-c-b-item-current-font-color
      ,$tabs-c-b-item-current-bg-color-from,$tabs-c-b-item-current-bg-color-to,$tabs-c-b-item-normal-operator-color,$tabs-c-b-item-hover-operator-color,
      $tabs-c-b-item-image-normal,$tabs-c-b-item-image-current,$tabs-c-b-circle-color,$tabs-c-b-item-padding-vertical-large,$tabs-c-b-item-padding-vertical-middle);

    @include tabsStyleC-variant($tabs-c-c-type,$tabs-c-c-border-width,$tabs-c-c-border-color,$tabs-c-c-shadow-width,$tabs-c-c-shadow-color,$tabs-c-c-item-height,$tabs-c-c-item-padding-vertical
      ,$tabs-c-c-item-padding-horizontal,$tabs-c-c-item-icon-font-diff,$tabs-c-c-item-diff,$tabs-c-c-item-font-size,$tabs-c-c-item-icon-size,$tabs-c-c-item-normal-icon-color
      ,$tabs-c-c-item-normal-font-color,$tabs-c-c-item-normal-bg-color,$tabs-c-c-item-current-icon-color,$tabs-c-c-item-current-font-color
      ,$tabs-c-c-item-current-bg-color-from,$tabs-c-c-item-current-bg-color-to,$tabs-c-c-item-normal-operator-color,$tabs-c-c-item-hover-operator-color,
      $tabs-c-c-item-image-normal,$tabs-c-c-item-image-current,$tabs-c-c-circle-color,$tabs-c-c-item-padding-vertical-large,$tabs-c-c-item-padding-vertical-middle);

    @include tabsStyleC-variant($tabs-c-d-type,$tabs-c-d-border-width,$tabs-c-d-border-color,$tabs-c-d-shadow-width,$tabs-c-d-shadow-color,$tabs-c-d-item-height,$tabs-c-d-item-padding-vertical
      ,$tabs-c-d-item-padding-horizontal,$tabs-c-d-item-icon-font-diff,$tabs-c-d-item-diff,$tabs-c-d-item-font-size,$tabs-c-d-item-icon-size,$tabs-c-d-item-normal-icon-color
      ,$tabs-c-d-item-normal-font-color,$tabs-c-d-item-normal-bg-color,$tabs-c-d-item-current-icon-color,$tabs-c-d-item-current-font-color
      ,$tabs-c-d-item-current-bg-color-from,$tabs-c-d-item-current-bg-color-to,$tabs-c-d-item-normal-operator-color,$tabs-c-d-item-hover-operator-color,
      $tabs-c-d-item-image-normal,$tabs-c-d-item-image-current,$tabs-c-d-circle-color,$tabs-c-d-item-padding-vertical-large,$tabs-c-d-item-padding-vertical-middle);
  }
  /*D级分栏*/
  &.styleD{
    @include tabsStyleD-variant($tabsD_A_type,$tabsD_A_borderWidth,$tabsD_A_item_marginLeft,$tabsD_A_item_marginRight,$tabsD_A_item_fontSize
      ,$tabsD_A_item_iconSize,$tabsD_A_item_iconFontDiff,$tabsD_A_item_normal_iconColor,
      $tabsD_A_item_normal_fontColor,$tabsD_A_item_current_iconColor,$tabsD_A_item_current_fontColor,
      $tabsD_A_assit_fontSize,$tabsD_A_assit_fontColor,$tabsD_A_item_paddingVertical,$tabsD_A_item_normal_operatorColor,
      $tabsD_A_item_hover_operatorColor,$tabsD_A_circle_color,$tabsD_A_circle_color_default,$tabsD_A_borderColor);
    @include tabsStyleD-variant($tabsD_B_type,$tabsD_B_borderWidth,$tabsD_B_item_marginLeft,$tabsD_B_item_marginRight,$tabsD_B_item_fontSize
      ,$tabsD_B_item_iconSize,$tabsD_B_item_iconFontDiff,$tabsD_B_item_normal_iconColor,
      $tabsD_B_item_normal_fontColor,$tabsD_B_item_current_iconColor,$tabsD_B_item_current_fontColor,
      $tabsD_B_assit_fontSize,$tabsD_B_assit_fontColor,$tabsD_B_item_paddingVertical,$tabsD_B_item_normal_operatorColor,
      $tabsD_B_item_hover_operatorColor,$tabsD_B_circle_color,$tabsD_B_circle_color_default,$tabsD_B_borderColor);
    @include tabsStyleD-variant($tabsD_C_type,$tabsD_C_borderWidth,$tabsD_C_item_marginLeft,$tabsD_C_item_marginRight,$tabsD_C_item_fontSize
      ,$tabsD_C_item_iconSize,$tabsD_C_item_iconFontDiff,$tabsD_C_item_normal_iconColor,
      $tabsD_C_item_normal_fontColor,$tabsD_C_item_current_iconColor,$tabsD_C_item_current_fontColor,
      $tabsD_C_assit_fontSize,$tabsD_C_assit_fontColor,$tabsD_C_item_paddingVertical,$tabsD_C_item_normal_operatorColor,
      $tabsD_C_item_hover_operatorColor,$tabsD_C_circle_color,$tabsD_C_circle_color_default,$tabsD_C_borderColor);
    @include tabsStyleD-variant($tabsD_D_type,$tabsD_D_borderWidth,$tabsD_D_item_marginLeft,$tabsD_D_item_marginRight,$tabsD_D_item_fontSize
      ,$tabsD_D_item_iconSize,$tabsD_D_item_iconFontDiff,$tabsD_D_item_normal_iconColor,
      $tabsD_D_item_normal_fontColor,$tabsD_D_item_current_iconColor,$tabsD_D_item_current_fontColor,
      $tabsD_D_assit_fontSize,$tabsD_D_assit_fontColor,$tabsD_D_item_paddingVertical,$tabsD_D_item_normal_operatorColor,
      $tabsD_D_item_hover_operatorColor,$tabsD_D_circle_color,$tabsD_D_circle_color_default,$tabsD_D_borderColor);
  }
  &.styleE{
    width: $tabsE_width;
    .tabs-operator-btn{
      position: relative;
      height: $tabsE_operator_height;
      line-height: $tabsE_operator_height;
      background-color: $tabsE_operator_normal_bgColor;
      cursor: pointer;
      &.pre{
        border-bottom-width: 0; 
      }
      &.next{
        border-top-width: 0; 
      }
      .triangle{
        position: absolute;
        top:50%;
        left:50%;
        width: 0;
        height: 0;
        margin-top: -($tabsE_operator_triangleWidth/2);
        margin-left:-($tabsE_operator_triangleWidth/2);
        border:($tabsE_operator_triangleWidth/2) solid transparent;
        &.up{
          margin-top: -($tabsE_operator_triangleWidth/4*3);
          border-bottom:($tabsE_operator_triangleWidth/2) solid $tabsE_operator_normal_iconColor;
        }
        &.down{
          margin-top: -($tabsE_operator_triangleWidth/4);
          border-top:($tabsE_operator_triangleWidth/2) solid $tabsE_operator_normal_iconColor;
        }     
      }
      &:hover{
        background-color: $tabsE_operator_current_bgColor;
        .triangle{
          &.up{
            border-bottom-color:$tabsE_operator_current_iconColor;
          }
          &.down{
            border-top-color:$tabsE_operator_current_iconColor;
          }
        }
      }
    }
    li{
      position: relative;
      a{
        display: block;
        height: $tabsE_item_height;
        padding:0 $tabsE_item_paddingHorizontal;
        line-height: $tabsE_item_height - 2;
        background-color: $tabsE_item_normal_bgColor;
        border-bottom: 1px solid $tabsE_item_normal_borderColor;
        text-decoration: none;
        color:$tabsE_item_normal_fontColor;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
        .tab-icon,.tab-name{
          vertical-align: middle;
        }
        >.tab-icon{
          font-size: $tabsE_item_iconSize;
          color: $tabsE_item_normal_iconColor;
          top:0;
          margin-right: $tabsE_item_iconFontDiff;
        }
        >.tab-name{
          font-size: $tabsE_item_fontSize;
          color: $tabsE_item_normal_fontColor;
        }
      }
      &:first-child{
        a{
          border-top: 1px solid $tabsE_item_normal_borderColor;
        }
      }
      &:hover{
        a{
          background-color: $tabsE_item_hover_bgColor;
          color:$tabsE_item_hover_fontColor;
          >.tab-icon,>.tab-name{
            color: $tabsE_item_hover_fontColor;
          }
        }
      }
      &.current{
        a{
          background-color: $tabsE_item_current_bgColor;
          @include background-linear-gradient-to-bottom($tabsE_item_current_bgColor-from, $tabsE_item_current_bgColor-to);
          // border-color: $tabsE_item_current_bgColor;
          color:$tabsE_item_current_fontColor;
          >.tab-icon,>.tab-name{
            color: $tabsE_item_current_fontColor;
          }
        }
        &:after{
          content: '';
          position: absolute;
          top: 50%;
          right: 0;
          width: 0;
          height: 0;
          margin-top: -($tabsE_triangleWidth/2);
          margin-right: - $tabsE_triangleWidth;
          border:($tabsE_triangleWidth/2) solid transparent;
          border-left-color: $tabsE_item_current_bgColor;
        }
      }
    }
  }
  &.styleG{
    border-bottom: 1px solid $tabsG_borderColor;
    .tab-items{
      position: relative;
      display: inline-block;
      *display: inline;
      *zoom: 1;
      top: 1px;
      font-size: 0;
      vertical-align: middle;
      li{
        display: inline-block;
        *display: inline;
        *zoom: 1;
        margin-right: $tabsG_item_marginRight;
        border-bottom: 2px solid transparent;       
        a{
          display: block;
          height: 29px;
          line-height: 29px;          
          font-size: 14px;
          color: $tabsG_item_aColor;
          font-weight: bold;
          text-decoration: none;
        }
        &.current{
          border-bottom-color: $tabsG_item_current_borderColor;
          a{            
            color: $tabsG_item_current_aColor;
            cursor: default;              
          }
        }
      }
    }
    .tab-assit-text {
      display: inline-block;
      font-size: 14px;
      color: $text-4;
      vertical-align: middle;
    }
    
  }
}