
//== Tooltips
//
//##

// Tooltip max width
$tooltip-max-width:           200px;
// Tooltip text color
$tooltip-color:               #fff;
// Tooltip background color
$tooltip-bg:                  $gray-9;
$tooltip-opacity:             .9;

// Tooltip arrow width
$tooltip-arrow-width:         7px;
// Tooltip arrow color
$tooltip-arrow-color:         $gray-9;
$tooltip-arrow-wrap-color:    $gray-9;
$tooltip-border-width:        1px;
$tooltip-border-color:        $gray-9;
$tooltip-border-style:        solid; 