//
// Searchbox
// --------------------------------------------------

.oas-search-box{
	background: $seachbox-bg;
	.search-box-hd{
		position:relative;
		padding: 5px 80px 1px 95px;
		@include background-linear-gradient-to-bottom($seachbox-hd-bg-from, $seachbox-hd-bg-to);
		border:1px solid $seachbox-hd-border;
		label{
			position: absolute;
			left: 0;
			top: 7px;
			width: 85px;
			padding: 3px 0;
			color: $seachbox-hd-label;
			font-size: 14px;
			text-align: center;
		}
		.division{
			position: absolute;
			left: 85px;
			top:6px;
			width: 1px;
			height: 27px;
			background: $search-division-bg;
		}
		.search-term-wrap{
			min-height: 33px;
		}
		.search-term{
			border-radius: 2px;
			float: left;
			display: inline-block;
			position: relative;
			font-weight: normal;
			color: $search-term-color;
			padding: 3px 30px 3px 10px;
			background: $search-term-bg;
			border:1px solid $search-term-border;
			margin-bottom: 5px;
			margin-right: 5px;
			.search-term-close{
				text-decoration: none !important;
				position: absolute;
				top:5px;
				right: 8px;
				color:$term-close-color;
				&:hover{
					color:$term-close-color-hover;
				}
			}
		}
		.searchbox-toggle{
			color: #000;
			position: absolute;
			right: 18px;
			top:10px;
			font-weight: normal;
		}
	}
	.search-box-bd{
		padding:0 10px;
		border:1px solid $seachbox-bd-border;
		border-top: 0;
		box-shadow: $seachbox-bd-shadow;
		.seachbox-item{
			padding:15px 0 10px;
			border-top: 1px dashed $seachbox-item-border;
			&:first-child{
				border-top:0;
			}
		}
	}
}