
//== Navs
//
//##

//=== Shared nav styles
$nav-link-padding:                          	10px 15px !default;
$nav-link-hover-bg:                         	$gray-lighter !default;

$nav-disabled-link-color:                   	$gray-light !default;
$nav-disabled-link-hover-color:             	$gray-light !default;

//== Tabs
$nav-tabs-color:                            	$text-1 !default;
$nav-tabs-border-color:                     	$gray-3 !default;
$nav-tabs-bg:                               	$gray-1 !default;
$nav-tabs-link-hover-border-color:          	$gray-lighter !default;
$nav-tabs-hover-bg:                         	$gray-3 !default;
$nav-tabs-active-link-hover-bg:             	$body-bg !default;
$nav-tabs-active-link-hover-color:          	$gray !default;
$nav-tabs-active-link-hover-border-color:   	$gray-4 !default;

$nav-tabs-justified-link-border-color:          $gray-4 !default;
$nav-tabs-justified-active-link-border-color:   $body-bg !default;

//== Pills
$nav-pills-border-color:                    	$gray-3 !default;
$nav-pills-border-radius:                   	$border-radius-base !default;
$nav-pills-active-link-hover-bg:            	$link-hover-color !default;
$nav-pills-active-link-hover-color:         	$component-active-color !default;
$nav-pills-active-border-color:             	$blue-6;
$nav-pills-hover-bg:              	        	#fff;
$nav-pills-hover-color:                     	$link-hover-color;
$nav-pills-hover-border-color:              	$link-hover-color;