//
// Connection choose
// --------------------------------------------------
.oas-connection-item{
	position:relative;
	padding:0 90px 0 100px;
	>.labels{
		position:absolute;
		left: 0;
		top:0;
		width:100px;
		text-align: right;
		color: $choose-btn-color;
		margin-bottom: 0;
		padding: $padding-small-vertical $padding-small-horizontal;
		.oasicon-swerve{
			position:relative;
			top:-6px;
			top:-3px\0;
			font-size:22px;
			color:$swerve-color;
			font-weight:normal;
		}
	}
	.oas-choose-btn{
		border-radius: 3px;
		display: inline-block;
		margin-right: 5px;
		margin-bottom: 6px;
		line-height:1.5;
		padding: $padding-small-vertical $padding-base-horizontal;
		color: $choose-btn-color;
		text-decoration: none;
		cursor: pointer;
		&:hover{
			color: $choose-btn-color-hover;
			background-color:$choose-btn-bg-hover;
		}
		&.active{
			color: $choose-btn-color-active;
			background-color:$choose-btn-bg;
		}
		input[type=checkbox]{
			display: none;
			position: relative;
			top: 2px;
			margin:0 5px 0 0;
			// & + span:before,
			// & + span:after {
			// 	display: none;
			// }
		}
	}
	.mixchoose-btn{
		position: absolute;
		right: 0px;
		top: 0;
		.glyphicon {
			top: 1px;
			left: -2px;
			font-size: 10px;
		}
	}
	.mixchoose-quit{
		display: none;
		position: absolute;
		right: 0px;
		top: 6px;
	}
	&.oas-mixchoose{
		.mixchoose-btn{
			display: none;
		}
		.mixchoose-quit{
			display: block;
		}
		.oas-choose-btn{
			padding-left:0;
			padding-right:0;
			margin-right:15px;
			margin-bottom:0;
			&:hover{
				color:$choose-btn-color;
				background:none;
			}
			&.active{
				color:$choose-btn-color;
				background:none;
			}
			// input[type=checkbox]{
			// 	display:inline-block; 
			// }
			// input[type=checkbox] + span:before,
			// input[type=checkbox] + span:after {
			// 	display: block;
			// }
		}
		.mixchoose-confirm{
			display:block;
			margin-bottom: 10px;
		}
	}
	.mixchoose-confirm{
		display:none;
	}
}