



 .oas-slider-icon-wrapper {
  position: relative;
  padding: 0px 30px;
  &.wrap-vertical {
    height: 100%;
    padding: 30px 0;
  }
}

.oas-slider-input-wrapper {
  padding:0 80px 0 0;
  &.wrap-vertical{
    height: 100%;
    padding: 46px 0 0;
  }
}


.oas-slider {
  position: relative;
  margin: 10px 6px;
  padding: 4px 0;
  height: 12px;
  cursor: pointer;

  &-with-marks {
    margin-bottom: 28px;
  }

  &-rail {
    position: absolute;
    width: 100%;
    height: 4px;
    border-radius: 2px;
    background-color: $slider-rail-bg;
    transition: background-color 0.3s ease;
  }

  &-track {
    position: absolute;
    height: 4px;
    border-radius: 4px;
    @include background-linear-gradient-to-right($slider-track-bg-from, $slider-track-bg-to);
  }

  &-step {
    position: absolute;
    width: 100%;
    height: 4px;
    background: transparent;
  }

  &-handle {
    position: absolute;
    margin-left: -7px;
    margin-top: -5px;
    width: 14px;
    height: 14px;
    cursor: pointer;
    border-radius: 50%;
    border: solid 2px $slider-border-color;
    background-color: #fff;
    transition: border-color 0.3s ease, transform .3s cubic-bezier(0.18, 0.89, 0.32, 1.28);
    i.hov-inner {
      display: none;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -4px;
      margin-top: -4px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $slider-border-color;
    }
    &:hover {
      border: solid 2px $slider-border-color;
      transform: scale(1.2);
      transform-origin: center center;
      i.hov-inner {
        display: block;
      }
    }

    &:active {
      box-shadow: 0 0 2px 0 $slider-border-color;
      i.hov-inner {
        display: block;
      }
    }
  }

  &-label {
      display: none;
      padding: 6px 6px;
      position: absolute;
      top: -40px;
      font-size: 14px;
      background: #fff;
      box-shadow: 0 2px 6px 2px rgba(0,0,0,.3);
      white-space: nowrap;
      line-height: 1;
      font-weight: bold;
      border-radius: 3px;
      z-index: 10;
      &-arrow {
        position: absolute;
        top: 22px;
        left: 50%;
        width: 22px;
        height: 18px;
        background: image-url($arrow_down);
        margin-left: -11px;
      }
  }

  &-mark {
    position: absolute;
    top: 14px;
    left: 0;
    width: 100%;
    font-size: 12px;
  }

  &-mark-text {
    width: 30%;
    margin-left: -15%;
    position: absolute;
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    cursor: pointer;
    color: $text-3;

    &-active {
      color: $text-3;
    }
  }

  &-dot {
    position: absolute;
    top: -2px;
    margin-left: -4px;
    width: 8px;
    height: 8px;
    border: 2px solid $slider-dot-border;
    background-color: #fff;
    cursor: pointer;
    border-radius: 50%;
    vertical-align: middle;
    &:first-child {
      margin-left: -4px;
    }
    &:last-child {
      margin-left: -4px;
    }
    &-active {
      border-color: $slider-dot-border-active;
    }
  }

  &-disabled {
    cursor: not-allowed;
    .oas-slider-track {
      background:  $slider-disabled-color !important;
    }
    .oas-slider-handle,
    .oas-slider-dot {
      border-color:  $slider-disabled-color !important;
      background-color: #fff;
      cursor: not-allowed;
      box-shadow: none;
      i.hov-inner {
        display: none !important;
      }
    }
    .oas-slider-mark-text,
    .oas-slider-dot {
      cursor: not-allowed !important;
    }
    .oas-input-number {
      border-color: $slider-disabled-color !important;
      .oasicon {
        color:$slider-disabled-color !important;
      }
    }
    .opt-icon {
      color: $slider-disabled-color;
      &:active {
        box-shadow: none!important;
      } 
    }
  }

  // input输入框放入滑块内
  .oas-input-number {
    // position: relative;
    position: absolute;
    right: -100px;
    top: -8px;
    padding: 4px 7px;
    width: 100%;
    cursor: text;
    color: $text-3;
    background-color: #fff;
    background-image: none;
    -webkit-transition: all .3s;
    transition: all .3s;
    margin: 0;
    padding: 0;
    font-size: 12px;
    height: 28px;
    display: inline-block;
    border: 1px solid $slider-border-color;
    border-radius: 4px;
    width: 80px;
    &:hover {
      border-color: $slider-border-color;
      .oas-input-number-handler-wrap {
        opacity: 1;
      }
    }
    .oas-input-number-handler-wrap {
      border-left: 1px solid $slider-disabled-color;
      width: 22px;
      height: 100%;
      background: #fff;
      position: absolute;
      top: 0;
      right: 0;
      opacity: 0;
      border-radius: 0 4px 4px 0;
      -webkit-transition: opacity .24s linear .1s;
      transition: opacity .24s linear .1s;
      z-index: 2;
      .oas-input-number-handler {
        text-align: center;
        line-height: 0;
        height: 50%;
        overflow: hidden;
        color: $text-3;
        position: relative;
        -webkit-transition: all .1s linear;
        transition: all .1s linear;
        display: block;
        width: 100%;
        font-weight: 700;
        &:hover {
          .oas-input-number-handler-down-inner, .oas-input-number-handler-up-inner {
            color: $slider-border-color;
          }
        }
        &.oas-input-number-handler-up {
          cursor: pointer;
          &:hover {
              height: 60%!important;
          }
        }
        &.oas-input-number-handler-down {
          border-top: 1px solid $slider-disabled-color;
          top: -1px;
          cursor: pointer;
        }

        .oas-input-number-handler-down-inner,
        .oas-input-number-handler-up-inner {
          -webkit-filter: none;
          filter: none;
          font-style: normal;
          vertical-align: baseline;
          text-align: center;
          text-transform: none;
          line-height: 1;
          text-rendering: optimizeLegibility;
          -webkit-font-smoothing: oasialiased;
          line-height: 12px;
          -webkit-user-select: none;
          user-select: none;
          position: absolute;
          -webkit-transition: all .1s linear;
          transition: all .1s linear;
          display: inline-block;
          font-size: 26px;
          -webkit-transform: scale(.58333333) rotate(0deg);
          transform: scale(.58333333) rotate(0deg);
          zoom: 1;
          right: -2px;
          color: $text-3;
        }
        .oas-input-number-handler-up-inner {
          top: 50%;
          margin-top: -7px;
        }
        .oas-input-number-handler-down-inner {
            top: 50%;
            margin-top: -6px;
        }
      }
    }
    .oas-input-number-input {
      width: 100%;
      text-align: left;
      outline: 0;
      -moz-appearance: textfield;
      height: 26px;
      -webkit-transition: all .3s linear;
      transition: all .3s linear;
      color: $text-3;
      background-color: #fff;
      border: 0;
      border-radius: 4px;
      padding: 0 7px;
      display: block;
    }
  }
  // 按钮
  .opt-icon {
    position: absolute;
    top: -3px;
    width: 18px;
    height: 18px;
    line-height: 1;
    font-size: 18px;

    &:first-child {
      left: -30px;
      color: $slider-opt-icon;
      &:hover {
        color: $slider-opt-icon-active;
      }
    }
    &:last-child {
      right: -30px;
      color: $slider-opt-icon;
      &:hover {
        color: $slider-opt-icon-active;
      }
    }
    &:active {
      box-shadow: 0 0 2px 0 $slider-opt-icon-active;
    }
  }
  &.oas-slider-vertical {
    width: 12px;
    height: 100%;
    margin: 6px 10px;
    padding: 0 4px;

    .oas-slider-rail {
        height: 100%;
        width: 4px;
    }

    .oas-slider-track {
        width: 4px;
        @include background-linear-gradient-to-bottom($slider-track-bg-to, $slider-track-bg-from);
    }

    .oas-slider-handle {
        margin-left: -5px;
        margin-bottom: -7px;
    }

    .oas-slider-mark {
        top: 0;
        left: 12px;
        width: 18px;
        height: 100%;
    }

    .oas-slider-mark-text {
        width: auto;
        margin-left: 0;
        height: 20px;
        line-height: 20px;
        margin-bottom: -10px;
        left: 4px;
        white-space: nowrap;
    }

    .oas-slider-step {
        width: 4px;
        height: 100%;
    }

    .oas-slider-dot {
        top: auto;
        left: 2px;
        margin-bottom: -4px;
    }

    .oas-slider-label {
        top: auto;
    }

    .opt-icon {
      top: auto;
      left: -3px;
      &:first-child {
        bottom: -30px;    
      }
      &:last-child {
        right: auto;
        top: -30px;
        
      }
    }
    .oas-input-number {
      right: -34px;
      top: -46px;
    }
  }
}
