

//== Grid system
//
//## Define your custom responsive grid.

// Number of columns in the grid.
$grid-columns:              12;
// Padding between columns. Gets divided in half for the left and right.
$grid-gutter-width:         30px;
// Navbar collapse
// Point at which the navbar becomes uncollapsed.
$grid-float-breakpoint:     $screen-sm-min;
// Point at which the navbar begins collapsing.
$grid-float-breakpoint-max: ($grid-float-breakpoint - 1);


//===oas-grid

$oas-grid-text-color:               $text-1;

$oas-grid-thead-bg:                 $gray-1;
$oas-grid-thead-bg-to:              $gray-2;
$oas-grid-thead-icon:               $gray-7;
$oas-grid-thead-icon-to:            $blue-6;
$oas-grid-thead-left-split-line:    rgba(255,255,255,1);
$oas-grid-thead-right-split-line:   rgba(0,0,0,.10);

$oas-grid-thead-sorted-bg:          $gray-2;
$oas-grid-thead-sorted-bg-to:       $gray-4;


$oas-grid-filter-input-border:      $blue-5;

$oas-grid-tbody-bg:                 #fff;
$oas-grid-tbody-level-bg:           $gray-1;
$oas-grid-tbody-bg-hover:           $blue-2;
$oas-grid-tbody-bg-active:          $blue-1;
$oas-grid-tbody-bg-odd:             $gray-2;
$oas-grid-tbody-bg-disabled:        $gray-3;



$oas-grid-border:                   $gray-4;

$oas-grid-front-fixed-shadow:        6px 0 6px 1px rgba(0,0,0,.2);
$oas-grid-finale-fixed-shadow:      -6px 0 6px 1px rgba(0,0,0,.2); 