/**/
#oas-bd{
	&.has-left-menu,.has-left-menu{
		position: relative;
		padding-left: $left-menu-width+1;
		overflow-y:auto;
		overflow-x:hidden;
		height: 100%;
	}
}
.oas-left-menu{
	position: absolute;
	left: 0;
	top:0;
	bottom:0;
	width:$left-menu-width;
	background: $menu-bg;
	border-right: 1px solid $left-menu-border;
	&.hover{
		border-right: 1px solid $menu-li-hover-color;
	}
	.menu-title{
		height: 38px;
		line-height: 38px;
		font-size: 16px;
		background: $menu-title-bg;
		color:$menu-title-color;
		text-align: center;
		font-weight: bold;
	}
	.oas-menu-toggle{
		position: absolute;
		top:50%;
		right:-14px;
		width: 14px;
		height: 90px;
		margin-top: -45px;
		background: image-url($fold);
		z-index:100;
		&:hover{
			background: image-url($fold_hover);
		}

	}
	&.menu-open{
		.oas-menu-toggle{
			right:-1px;
			background: image-url($unfold);
			&:hover{
				background: image-url($unfold_hover);
			}
		}
	}
	// li {
	>ul>li{
		a{
			position:relative;
			display:block;
			height:32px;
			line-height:32px;
			padding:0 10px;
			color:$menu-li-color;
			font-size:14px;
			text-decoration: none;
			text-align: center;
		}
		&:hover{
			a{
				background: $menu-li-hover-bg;
				color:$menu-li-hover-color;
			}
		}
		&.active{
			a{
				width: 100%;
				color:$menu-li-active-color;
				background-color: $menu-li-active-bg;
				&:after{
					content: '';
					position: absolute;
					right: 0px;
					top: 10.5px;
					width: 5px;
					height: 11px;
					background: image-url($menu_current);
				}
			}
		}
	}
}