//
// Formtips
// --------------------------------------------------


// Base class
.oas-formtip {
  position: absolute;
  z-index: $zindex-tooltip;
  display: block;
  // Our parent element can be arbitrary since formtip are by default inserted as a sibling of their target element.
  // So reset our font and text properties to avoid inheriting weird values.
  @include reset-text;
  font-size: $font-size-small;

  &.in     { @include opacity($formtip-opacity); }
  &.top    { margin-top:  -3px; padding: $formtip-arrow-width 0; }
  &.right  { margin-left:  3px; padding: 0 $formtip-arrow-width; }
  &.bottom { margin-top:   3px; padding: $formtip-arrow-width 0; }
  &.left   { margin-left: -3px; padding: 0 $formtip-arrow-width; }
  &.oas-error{
    .formtip-inner{
       background-color: $formtip-bg-error;
       border-color:$formtip-border-color-error;
    }
  }
}

// Wrapper for the formtip content
.formtip-inner {
  padding: 7px;
  color: $formtip-color;
  text-align: left;
  background-color: $formtip-bg;
  border-radius: $border-radius-base;
  border: $formtip-border-width $formtip-border-style $formtip-border-color;
  word-break: break-all;
}

// Arrows
.formtip-arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}
.formtip-arrow:after {
  content:" ";
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  border-width: 7px 7px 7px 7px;
}
// Note: Deprecated .top-left, .top-right, .bottom-left, and .bottom-right as of v3.3.1
.oas-formtip {
  &.top .formtip-arrow {
    bottom: 0;
    left: 50%;
    margin-left: -$formtip-arrow-width;
    border-width: $formtip-arrow-width $formtip-arrow-width 0;
    border-top-color: $formtip-arrow-color;
    &:after {
      left: 50%;
      top:-$formtip-arrow-width - 1.5;
      margin-left: -$formtip-arrow-width;
      border-top-color: $formtip-arrow-wrap-color;
      border-bottom-width:0px;
    }
  }
  &.oas-error.top .formtip-arrow{
      border-top-color: $formtip-arrow-color-error;
      &:after{
        border-top-color: $formtip-arrow-wrap-color-error;
      }
  }
  &.bottom .formtip-arrow {
    top: 0;
    left: 50%;
    margin-left: -$formtip-arrow-width;
    border-width: 0 $formtip-arrow-width $formtip-arrow-width;
    border-bottom-color: $formtip-arrow-color;
    &:after {
      left: 50%;
      bottom:-$formtip-arrow-width - 1.5;
      margin-left: -$formtip-arrow-width;
      border-bottom-color: $formtip-arrow-wrap-color;
      border-top-width:0px;
    }
  }
  &.oas-error.bottom .formtip-arrow{
      border-bottom-color: $formtip-arrow-color-error;
      &:after{
        border-bottom-color: $formtip-arrow-wrap-color-error;
      }
  }
}
