
// Button variants

@mixin background-linear-gradient-to-bottom($from-color,$to-color: false){

	@if $to-color != false and $support-gradient == true {
		background-image: -webkit-linear-gradient(top,$from-color,$to-color);
		background-image: linear-gradient(to bottom,$from-color,$to-color)
	}

	background-color: $from-color;

}

@mixin background-linear-gradient-to-right($from-color,$to-color: false){

	@if $to-color != false and $support-gradient == true {
		background-image: -webkit-linear-gradient(left,$from-color,$to-color);
		background-image: linear-gradient(to right,$from-color,$to-color)
	}

	background-color: $from-color;

}

@mixin border-linear-gradient-to-bottom($from-color,$to-color: false){
	
	@if type-of($to-color) != false and $support-gradient == true {
		border-image: -webkit-linear-gradient(top,$from-color,$to-color) 1;
		border-image: linear-gradient(to bottom,$from-color,$to-color) 1
	}

	border-color: $from-color;

}


@mixin background-linear-gradient-3-to-bottom($from-color,$middle-color: false,$to-color: false){

	@if $middle-color != false and $to-color != false and $support-gradient == true {
		background-image: -webkit-linear-gradient(top,$from-color,$middle-color,$to-color);
		background-image: linear-gradient(to bottom,$from-color,$middle-color,$to-color)
	}

	background-color: $from-color;

}

@mixin border-linear-gradient-to-bottom($from-color,$middle-color: false,$to-color: false){
	
	@if $middle-color != false and $to-color != false and $support-gradient == true {
		border-image: -webkit-linear-gradient(top,$from-color,$middle-color,$to-color) 1;
		border-image: linear-gradient(to bottom,$from-color,$middle-color,$to-color) 1
	}

	border-color: $from-color;

}