/*
 * 穿梭框样式
 */
.oas-transfer-list {
  float: left;
  color:$transfer-text;
  font-size: 14px;
  background:#fff;
  position: relative;
  //头部样式
  .oas-transfer-list-head {
    padding-left: 10px;
    border: 1px solid $transfer-border-color;
    border-radius: 2px 2px 0 0;
    height: 40px;
    line-height: 40px;

    @include background-linear-gradient-to-bottom($transfer-bg-from, $transfer-bg-to);
    
    .title {
      display: inline-block;
      width: 100%;
      @include text-overflow();
      &.hasBusiness {
        width: calc(100% - 82px);
      }
    }
    .btn {
        position: absolute;
        max-width: 72px;
        height: 28px;
        line-height: 26px;
        padding: 0 5px;
        float: right;
        top: 5px;
        right: 5px;
        @include text-overflow();
    }
  }
  // 搜索栏
  .oas-transfer-list-search {
    width:auto;
    padding:5px;
    position:relative;
    border-left: 1px solid $transfer-border-color;
    border-right: 1px solid $transfer-border-color;
  }

  .oas-transfer-list-body {
    border-left: 1px solid $transfer-border-color;
    border-right: 1px solid $transfer-border-color;
    padding: 2px;
    overflow-y:auto;
    .treeContainer {
      overflow: initial;
    }
    .oas-transfer-item-wrap {
      .oas-transfer-item {
        position: relative;
        padding-left: 8px;
        height: 30px;
        line-height: 30px;
        cursor:pointer;
        &.uni {
          @include text-overflow();
          padding-right: 20px;
        }
        .beauty-check span {
            @include text-overflow();
        }
        .rm-item {
          display: none;
          font-size: 16px;
          position: absolute;
          top: 7px; 
          right: 10px;  
          color: $transfer-delicon-color;
          &:hover {
            color: $transfer-delicon-color-hover;
          }
        }
        label {
          width: 100%;
        }
        &:hover {
          background: $transfer-item-hover;
          .rm-item {
            display: block;
          }
        } 
      }
    }
  }

  .oas-transfer-list-foot {
    padding-left: 10px;
    border: 1px solid $transfer-border-color;
    border-radius: 0 0 2px 2px;
    height: 40px;
    line-height: 40px;
    background: $transfer-bg;
    i.sta {
      font-style: normal;
    }
    i.all, i.clear {
      float: right;
      margin-right: 10px;
      font-style: normal;
      color: $transfer-disabled-color;
      cursor: not-allowed;
      &.on {
        color: $transfer-color;
        cursor: pointer;
        &:hover {
          color: $transfer-color-hover;
        }
      }
      
    }
  }

  .oas-no-data {
    width: 100%;
    position: absolute;
    top:50%;
    margin-top: -74px!important;
  }
}

.oas-transfer-operation {
  padding: 150px 10px 0;
  float: left;
  .transfer-btn {
    display: inline-block;
    max-width: 56px;
    vertical-align: -4px;
    @include text-overflow();
  }
  i.arrow {
    display: block;
    width: 18px;
    height: 26px;
    background: image-url($uni_arrow);
  }
  button {
    display: block;
    padding: 4px 0;
    &.to-left {
      .oasicon {
        left: 3px;
      }
      span {
        margin-left: 0;
        margin-right: 5px;
      }
    }
    
    i.oasicon {
      top: 2px;
      right: 3px;
      font-size: 14px;
    }
    &+button {
      margin-top: 10px;
    }
  }
}
