.oas-hd{
    min-width: 1024px;
    height: 40px;
    background-color: $hnav-bgc;
    .oas-hd-logo{
        float: left;
        margin-left: 20px;
        margin-top: 7px;
        width: 36px;
        height: 26px;
        margin-right: 8px;
    }
    .oas-hd-sys-name{
        float: left;
        margin-right: 64px;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        color: $hnav-sys-color;
        text-shadow: 5px 2px 6px $text-1;
    }
    >ul{
        position: relative;
        border: 1px solid;
        border-top: 0px;
        border-bottom: 0px;
        border-left: 0px;
        @include border-linear-gradient-to-bottom($hn-border-linear-gradient-right-first,$hn-border-linear-gradient-right-second,$hn-border-linear-gradient-right-third);
        &:after{
            content:'';
            position: absolute;
            right: 0px;
            width:1px;
            height: 40px;
            @include background-linear-gradient-3-to-bottom($hn-border-linear-gradient-left-first,$hn-border-linear-gradient-left-second,$hn-border-linear-gradient-left-third);
        }
        &.first-level-nav{
            float:left;
            >li{
                position: relative;
                float: left;
                min-width: 106px;
                min-width: 67px\9;
                height: 40px;
                padding: 0 20px 0 22px; 
                line-height: 40px;
                border-bottom: 3px solid transparent;
                cursor: pointer;
                >a{
                    &::before{
                        content: '';
                        position: absolute;
                        top:0;
                        right:0;
                        bottom:0;
                        left:0;
                    }
                    >span{
                        font-size: 16px;
                        color: $hnav-fistlevel-color;
                    }
                }
                &:before{
                    content:'';
                    position: absolute;
                    top: 0;
                    left: -1px;
                    width:1px;
                    height: 40px;
                    @include background-linear-gradient-3-to-bottom($hn-border-linear-gradient-left-first,$hn-border-linear-gradient-left-second,$hn-border-linear-gradient-left-third);
                }
                &:after{
                    content:'';
                    position: absolute;
                    top: 0;
                    left: 0px;
                    width:1px;
                    height: 40px;
                     @include background-linear-gradient-3-to-bottom($hn-border-linear-gradient-right-first,$hn-border-linear-gradient-right-second,$hn-border-linear-gradient-right-third);
                }
                &.active{
                    border-bottom-color: $hnav-fistlevel-border-bottom-color-active;
                    >a{
                        >span{
                            color: $hnav-fistlevel-color-active;
                        }
                    }
                }
                &:hover{
                    border-bottom-color: $hnav-fistlevel-border-bottom-color-active;
                    @include background-linear-gradient-to-bottom($hnav-fistlevel-hover-bgc-from, $hnav-fistlevel-hover-bgc-to);
                    >ul{
                        display: block;
                    }          
                }
            }
        }
    }
    .second-level-nav{
        position: absolute;
        left: 0;
        top: 40px;
        display: none; 
        min-width: 106px;
        box-shadow: 0 1px 6px rgba(0,0,0,.2);
        background-color: $hnav-secondlevel-gbc;
        z-index: 99999;

        border: 1px solid;
        border-color: transparent;
        border-color: $hn-ie-border-color;
        >li{
            position: relative;
            padding: 0 10px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            font-size: 14px;
            color: $hnav-secondlevel-color;
            white-space: nowrap;
            >a{
                &::before{
                    content: '';
                    position: absolute;
                    top:0;
                    right:0;
                    bottom:0;
                    left:0;
                }
                >span{
                    color: $hnav-secondlevel-color;
                }
            }
            &:hover{
                >a{
                    >span{
                        color: $hnav-secondlevel-color-active;
                    }
                }
                >ul{
                    display: block;
                }
            }
            &.active{
                >ul{
                    display: block;
                }
                >a{
                    >span{
                        color: $hnav-secondlevel-color-active;
                    }
                }
            }
        } 
    }
    .third-level-nav{
        position: absolute;
        left: 100%;
        margin-left: 2px;
        top: 0;
        display: none; 
        min-width: 106px;
        box-shadow: 0 1px 6px rgba(0,0,0,.2);
        background-color: $hnav-thirdlevel-gbc;
        z-index: 999;
        border: 1px solid;
        border-color: transparent;
        border-color: $hn-ie-border-color;
        >li{
            position: relative;
            padding: 0 10px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            font-size: 14px;
            color: $hnav-thirdlevel-color;
            white-space: nowrap;
            >a{
                &::before{
                    content: '';
                    position: absolute;
                    top:0;
                    right:0;
                    bottom:0;
                    left:0;
                }
                >span{
                    color: $hnav-thirdlevel-color;
                }
            }
            &:hover{
                >a{
                    >span{
                        color: $hnav-thirdlevel-color-active;
                    }
                }
                @include background-linear-gradient-to-right($hnav-thirdlevel-hover-bgc-from, $hnav-thirdlevel-hover-bgc-to);
            }
            &.active{
                @include background-linear-gradient-to-right($hnav-thirdlevel-hover-bgc-from, $hnav-thirdlevel-hover-bgc-to);
                >a{
                    >span{
                        color: $hnav-thirdlevel-color-active;
                    }
                }
            }
        }
        // 扩大区域
        &:before{
            content:'';
            position: absolute;
            left: -5px;
            height: 100%;
            width: 5px;
        }
    }
    .oas-hd-info{
        float: right;
        padding-right: 26px;
        .oas-hd-detail{
            margin-right: 15px;
            float: left;
            margin-top: 5px;
            width: 30px;
            height: 30px;
            background: image-url($headernav_detail_log);        
        }        
        .oas-hd-user-img{
            position: relative;
            float: left;
            border-top: 0px;
            border-bottom: 0px;
            border-right: 0px;
            border-left: 1px solid;
            @include border-linear-gradient-to-bottom($hn-border-linear-gradient-left-first,$hn-border-linear-gradient-left-second,$hn-border-linear-gradient-left-third);
            >img{
                margin-left: 14px;
                display: inline-block;
                margin-top: 5px;
                width: 30px;
                height: 30px;
            }
            &:before{
                content:'';
                position: absolute;
                left: 0;
                width:1px;
                height: 40px;
                 @include background-linear-gradient-3-to-bottom($hn-border-linear-gradient-right-first,$hn-border-linear-gradient-right-second,$hn-border-linear-gradient-right-third);
            }
        }
        .oas-hd-username{
            margin-left: 6px;
            float: left;
            font-size: 14px;
            height: 40px;
            line-height: 40px;
            color: $hn-username-color;
        }
    }
    a{
        color: transparent;
        &:hover{
            color: transparent;
            text-decoration: none;
        }
    }
}