
@mixin size($width, $height) {
  	width: $width;
 	height: $height;
}
/*方形*/
@mixin square($size) {
  	width: $size;
 	height: $size;
}
@mixin location($top,$right,$bottom,$left){
	top:$top;
	right: $right;
	bottom: $bottom;
	left:$left;
}
@mixin pull-left(){
	float:left;
}

.oas-operate{
	display: none;
	position: absolute;
	z-index: 99995;
	padding: 15px;
	background-color: #fff;
	border:1px solid #a3b0c0;
	border-radius: 3px;
	a{
		text-decoration: none;
	}
	&:after {
		position:absolute;
		@include location(auto,-1px,-4px,-1px);
		z-index: 1;
		display:block;
		content:"";
		background: image-url($shadow);
		height: 4px;
		border-radius: 0 0 3px 3px;
	}
	/**箭头**/
	.operate-arrow{
		position: absolute;
		@include location(-15px,auto,auto,12px);
		z-index: 111;
		display: inline-block;
		@include size(20px,20px);
		background: url("../images/operate/bubble_arrow.png") no-repeat 0 0;
		&.opposite{
			@include location(-15px,12px,auto,auto);
		}
		&.right-arrow{
			background: url("../images/operate/bubble_arrow.png") no-repeat -20px 0;
			@include location(12px,-13px,auto,auto);
			&.opposite{
				@include location(auto,-13px,12px,auto);
			}
		}
		&.bottom-arrow{
			background: url("../images/operate/bubble_arrow.png") no-repeat -40px 0;
			@include location(auto,auto,-16px,12px);
			&.opposite{
				@include location(auto,12px,-16px,auto);
			}
		}
		&.left-arrow{
			background: url("../images/operate/bubble_arrow.png") no-repeat -60px 0;
			@include location(12px,auto,auto,-13px);
			&.opposite{
				@include location(auto,auto,12px,-13px);
			}
		}
	}
	/**简单选择操作列表**/
	&.opt-panel {
		padding:2px;
		li{
			padding:0 10px;
			height: 30px;
			line-height:30px;
			cursor: pointer;
			width: 160px;
			white-space: nowrap;
			&.zeroclipboard-is-hover{
				background-color:#497db9;
				& > a{
					color:#fff;
				}	
				& > .oasicon{
					color:#fff;
				}
			}
			&.oas-disable{
				background-color:transparent !important;
				&:hover{
					>a{
						cursor: not-allowed !important;
					}
					>.oasicon{
						cursor: not-allowed !important;
					}
					
				}
			}
			&:hover{
				background-color:#497db9;
				>a{
					color:#fff;
				}
				>.oasicon{
					color:#fff;
				}
			}
			a{
				@include pull-left;
				width:92px;
				white-space: nowrap;
			 	overflow: hidden;
			 	text-overflow:ellipsis;
			}
			a.oasicon{
				@include pull-left;
				margin: 7px 8px 0 0;
				font-size: 16px;
				@include square(16px);
			}
			a.oasicon-arrow-right{
				float: right;
				margin:7px 0 0 8px;
				font-size: 16px;
				@include square(16px);
			}
		}
		.expand-menu{
			border-top: 1px solid #e4e4e4;
			padding-top: 2px;
			margin-top: 2px;
			li{
				position: relative;
				width: 160px;
			}
		}
		.first-menu{
			li{
				position: relative;
				width: 160px;
			}
		}
		.second-menu{
			position: absolute;
			min-width: 100%;
			&.oas-operate{
				padding:2px;
			}
			&.open{
				display:block;
			}
			&.to-right{
				left: 101%;
			}
			&.to-left{
				right: 101%;
			}
			&.to-top{
				bottom: 0px;
			}
			&.to-bottom{
				top: 0px;
			}

		}
		.third-menu{
			position: absolute;
			min-width: 100%;
			&.oas-operate{
				padding:2px;
			}
			&.open{
				display:block;
			}
			&.to-right{
				left: 101%;
			}
			&.to-left{
				right: 101%;
			}
			&.to-top{
				bottom: 0px;
			}
			&.to-bottom{
				top: 0px;
			}
		}
	}
	.bubble-bd{
		overflow-y:auto; 
	}
	.bubble-hd{
		position: relative;
		border-bottom: 1px solid #e4e4e4;
		padding:0 20px 5px 0;
		margin-bottom:10px;
		.oasicon{
			position: absolute;
			right:0;
			top:4px;
		}
	}
	
}