@keyframes magnifierMoving {
  0%, 50%, 100% {
    -webkit-transform: translateY(-10px);
    -ms-transform: translateY(-10px);
    transform: translateY(-10px);  
  }
  12.5% ,62.5%{
    -webkit-transform: translateX(10px);
    -ms-transform: translateX(10px);
    transform: translateX(10px);   
  }
  25% ,75%{
    -webkit-transform: translateY(10px);
    -ms-transform: translateY(10px);
    transform: translateY(10px);
  }
  37.5% ,87.5%{
    -webkit-transform: translateX(-10px);
    -ms-transform: translateX(-10px);
    transform: translateX(-10px);
  }
}
@-webkit-keyframes magnifierMoving {
  0%, 50%, 100%{
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);  
  }
  12.5% ,62.5%{
    -webkit-transform: translateX(10px);
    transform: translateX(10px);   
  }
  25% ,75%{
    -webkit-transform: translateY(10px);
    transform: translateY(10px);
  }
  37.5% ,87.5%{
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px);
  }
}
// 小放大镜
@keyframes smallMagnifierMoving {
  0%, 50%, 100% {
    -webkit-transform: translateY(-5px);
    -ms-transform: translateY(-5px);
    transform: translateY(-5px);  
  }
  12.5% ,62.5%{
    -webkit-transform: translateX(5px);
    -ms-transform: translateX(5px);
    transform: translateX(5px);   
  }
  25% ,75%{
    -webkit-transform: translateY(5px);
    -ms-transform: translateY(5px);
    transform: translateY(5px);
  }
  37.5% ,87.5%{
    -webkit-transform: translateX(-5px);
    -ms-transform: translateX(-5px);
    transform: translateX(-5px);
  }
}
@-webkit-keyframes magnifierMoving {
  0%, 50%, 100%{
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);  
  }
  12.5% ,62.5%{
    -webkit-transform: translateX(5px);
    transform: translateX(5px);   
  }
  25% ,75%{
    -webkit-transform: translateY(5px);
    transform: translateY(5px);
  }
  37.5% ,87.5%{
    -webkit-transform: translateX(-5px);
    transform: translateX(-5px);
  }
}

.oas-no-data{
  position: relative;
  font-size: 0;
  width: 328px;
  margin: 0 auto;
  text-align: center;
  span{
  	&.oas-no-dataImg{
      display: inline-block;
      width: 237px;
      height: 114px;
  		opacity: 0.8;
      filter: alpha(opacity=80);
      background: image-url($nodata-bg-default);
  	}
  	&.oas-no-dataTip{
      display: block;
      margin-left: -18px;
      margin-top: 10px;
      font-size: 17px;
      color: $oas-no-dataTip-color;
  	}
  	&.oas-no-dataImg-magnifier{
      display: none;
      position: absolute;
      top: 35px;
      right: 142px;
      width: 84px;
      height: 75px;
      background: image-url($nodata-magnifier-default);
      @include animation(magnifierMoving 3s linear infinite);
  	}
  }
  &.oas-min-style{
    padding: 29px 0;
    width: 124px;
    span{
      &.oas-no-dataImg{
        width: 124px;
        height: 62px;
        background: image-url($nodata-bg-small);
      }
      &.oas-no-dataTip{
        font-size: 9px;
  	  }
      &.oas-no-dataImg-magnifier{
        position: absolute;
        top: 50px;
        right: 52px;
        width: 38px;
        height: 37px;
        background: image-url($nodata_magnifier_small);
        @include animation(smallMagnifierMoving 3s linear infinite);
  	  }
    }
  }
  &.oas-big-style{
    span{
      &.oas-no-dataImg{
        width: 328px;
        height:156px;
        background: image-url($nodata_bg_big);
      }
      &.oas-no-dataTip{
        font-size: 24px;
  	  }
      &.oas-no-dataImg-magnifier{
        position: absolute;
        top: 52px;
        right: 137px;
        width: 101px;
        height: 94px;
        background: image-url($nodata_magnifier_big);
  	  }
    }
  }
}

.oas-no-data{
  // 启用动画效果（放大镜）
  &.animation{
    span{
      &.oas-no-dataImg-magnifier{
        display: inline-block;
      }
    }  
  }

}

