.oas-cascader{
    font-size: 0px;
}
.oas-cascader-hd{
    position: relative;
    z-index: $zindex-cascader;
    width: 188px;
	height: 30px;
	padding: 0 30px 0 10px;
	line-height: 30px;
	border: 1px solid $cascader-border-color;
	background: $cascader-input-color;
	color: $cascader-text-placeholder-color;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
    border-radius: 2px;
    font-size: $cascader-fontsize;
    display: inline-block;
    &.oas-cascader-hasLabel{
        float: left;
    display: inline-block;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
    .select-value{
        color: $cascader-text-color;
    }
    .cascader-placeholder{
		color: $cascader-text-placeholder-color;
		&:hover{
			color: $cascader-text-placeholder-color;
		}
	}
    .oas-cascader-input{
        display: none;
    }
    .oasicon {
		position: absolute;
		right: 9px;
		top: 8px;
		font-size: 14px;
		color: $select-arrow-color;
		text-decoration: none;
    }
    .oasicon-search{
        &.active{
            color: $cascader-icon-hover-color;
        }
    }
    &:hover {
		border: 1px solid $cascader-border-hover-color;
		color: $cascader-text-color;
		background-color: $cascader-input-bgc;
		.oasicon {
            &:not(.oasicon-search){
                color: $cascader-arrow-color;
                &.oasicon-fail{
                    &:hover{
                        color: $cascader-icon-hover-color;
                    }
                }
            }
		}
    }
    &.disabled {
		cursor: not-allowed;
		border-color: $cascader-border-disabled-color;
		color: $cascader-text-disabled-color;
		background-color: $cascader-input-disabled-bgc;
		.oasicon {
			color: $cascader-arrow-disabled-color;
		}
		&:hover {
			border-color: $cascader-border-disabled-color;
			color: $cascader-text-disabled-color;
			background-color: $cascader-input-disabled-bgc;
		}
    }
    .oasicon-desc{
        transition: transform .2s ease-in;
        transform: rotate(0);
        &.close-animation{
            transform: rotate(180deg);
        }
    }
}

.oas-cascader{
    &.large{
        .oas-cascader-hd,.oas-cascader-label{
            height: 34px;
            line-height: 34px;
            .oasicon{
                font-size: 18px;
            }
        }
    }
    &.small{
        .oas-cascader-hd,.oas-cascader-label{
            height: 26px;
            line-height: 26px;
            .oasicon{
                font-size: 14px;
                top: 6px;
            }
        }
    }
} 

.oas-cascader-label{
    float: left;
    display: inline-block;
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid $cascader-border-color;
    border-right: none;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
    @include background-linear-gradient-to-bottom($cascader-label-from-color,$cascader-label-to-color);
    font-size: $cascader-fontsize;
}

.oas-cascader{
    &.open{
		.oas-cascader-hd{
			border-color: $cascader-border-active-color;
			box-shadow: 0 0 0 3px $cascader-bd-shadow-active-color; 
        }
    }
}

.oas-cascader-bd{
    z-index: $zindex-cascader; 
	margin: -1px 0 0;
	top: 100%;
	border-color: $cascader-border-active-color;
	border-radius: 2px;
	height: auto;
	box-shadow: 0 1px 6px $cascader-bd-shadow-color;
    background-color: $cascader-bd-bgc;
    font-size: $cascader-fontsize;
    font-size: 0;
    max-height: 180px;
    >div{
        font-size: 0;
        display: inline-block;
        max-height: 180px;
        >.oas-cascader-searchMenu{
            width: 188px;
            height: auto;
            >.oas-cascader-menu-item-search{
                .oas-cascader-menu-item-name{
                    max-width: 170px;
                }
            }
        }
    }
    .oas-cascader-menu{
        display: inline-block;
        min-width: 109px;
        height: 180px;
        overflow-y: auto;
        overflow-x: hidden;
        border-right: 1px solid $cascader-border-middle-color;
        font-size: 0;
        &:last-child{
            border-right: none;
        }
        .oas-cascader-menu-item,.oas-cascader-menu-item-search{
            position: relative;
            // padding: 0 22px 0 10px;
            height: 30px;
            line-height: 30px;
            cursor: pointer;
            font-size: 14px;
            .oas-cascader-menu-item-name{
                padding: 0 38px 0 10px;
                color: $cascader-text-color;
                display: inline-block;
                white-space: nowrap;
            }
            .oas-cascader-menu-item-left{
                position: absolute;
                right: 7px;
                line-height: 28px;
                color: $cascader-arrow-color;
            }
            &:hover{
                &:not(.disabled){
                    font-weight: normal;
                    .oas-cascader-menu-item-name{
                        color: $cascader-text-hover-color;
                    }
                    .oas-cascader-menu-item-left{
                        color: $cascader-arrow-color-hover;
                    }
                    background-color:$cascader-bd-focus-bgc;
                }
            }
            &.disabled{
                cursor: not-allowed;
                .oas-cascader-menu-item-name{
                    color: $cascader-arrow-disabled-color;
                    &:hover{
                        color: $cascader-arrow-disabled-color;
                    }
                }
            }
        }
    }
}
body{
   .oas-cascader-bd{
       min-width: 105px !important;
   } 
}

.oas-cascader-menu-item,.oas-cascader-menu-item-search{
    &.active{
        background-color: $cascader-bd-bgc-active;
        font-weight: bold;
    }
}
.oas-cascader-input-search{
    border: none;
    background: transparent;
    outline: 0;
}
.oas-cascader-search-value{
    font-style: normal;
    font-weight: bold;
}
.oas-cascader-nodata{
    width: 188px;
    height: 66px;
    line-height: 66px;
    text-align: center;
    font-size: 14px;
    color: $cascader-nodata-color;
}
