
//== Alerts
//
//##

$alert-padding:               10px 35px 10px 20px;
$alert-border-radius:         3px;
$alert-link-font-weight:      bold;

$alert-success-bg-from:       $green-1;
$alert-success-bg-to:         $green-2;
$alert-success-border:        $green-3;
$alert-success-text:          $green-6;
$alert-success-close:         $green-4;
$alert-success-close-hover:   $green-7;
$alert-success-shadow:        0 -1px 0 rgba(255,255,255,0.4) inset;

$alert-info-bg-from:          $blue-1;
$alert-info-bg-to:            $blue-2;
$alert-info-border:           $blue-3;
$alert-info-text:             $blue-6;
$alert-info-close:            $blue-4;
$alert-info-close-hover:      $blue-7;
$alert-info-shadow:           0 -1px 0 rgba(255,255,255,0.4) inset;

$alert-warning-bg-from:       $orange-1;
$alert-warning-bg-to:         $orange-2;
$alert-warning-border:        $orange-3;
$alert-warning-text:          $orange-6;
$alert-warning-close:         $orange-4;
$alert-warning-close-hover:   $orange-7;
$alert-warning-shadow:        0 -1px 0 rgba(255,255,255,0.4) inset;

$alert-danger-bg-from:        $red-1;
$alert-danger-bg-to:          $red-2;
$alert-danger-border:         $red-3;
$alert-danger-text:           $red-6;
$alert-danger-close:          $red-4;
$alert-danger-close-hover:    $red-7;
$alert-danger-shadow:         0 -1px 0 rgba(255,255,255,0.4) inset;