//
// oas-grid
// --------------------------------------------------

.oas-grid{
	position: relative;
	margin-bottom: 10px;
	table, td, th {
		vertical-align: middle;
		table-layout: fixed;
	}
	table{
		width: 100%;
		text-align: left;
		font-weight: normal;
	}

	.oas-grid-head {
		padding-bottom: 10px;
		.oas-grid-optBar {
			.oas-grid-option {
				float: left;
				.btn{
					float: left;
					margin-right: 5px;
				}
			}
			.oas-grid-chart-select{
				float: right;
				margin-right: 5px;
				.btn{
					padding: 4px 10px;
				}
			}
			.oas-grid-setting {
				float: right;
				.oas-grid-search {
					float: left;
					&.focus{
						input {
							border-color: $oas-grid-filter-input-border;
						}
					}
					/*2016-5-30 add 兼容ie8*/
					.input-group-addon{
						width: 53px\0;
					}
				}
			}
		}
	}

	.oas-grid-thead {
		position: relative;     
		height: 31px;
		color: $oas-grid-text-color;
		border: 1px solid $oas-grid-border;
		@include background-linear-gradient-to-bottom($oas-grid-thead-bg,$oas-grid-thead-bg-to);
		z-index: 2;
		>div:first-child{
			th:first-child .oas-grid-th {
				border-left: none;
				&:after{
					display: none;
				}
			}
		}
		th{
			padding: 8px 0;
			line-height: 18px;
		}
		.oas-grid-th{
			border-left: 1px solid $oas-grid-thead-right-split-line;
			&:after{
				position: absolute;
				top: 0;
				left: -2px;
				bottom:  0;
				content: "";
				border-left: 1px solid $oas-grid-thead-left-split-line;
			}
		}

	}

	.oas-grid-filter{
		position: relative;
		border: 1px solid $oas-grid-border;
		border-top-width: 0;
		background-color: #fff;
		.grid-front-fixed,
		.grid-main,
		.grid-finale-fixed{
			th{
				height: 36px;
			}      
			.oas-grid-th{
				padding: 0 5px;
				border-left-color: transparent;
			}
		}
	}

	.oas-grid-tbody {
		border: 1px solid $oas-grid-border;
		border-top-width: 0;
		position: relative;
		overflow: hidden;
	}

	.oas-grid-footer{
		.oas-grid-pagin{
			margin-top: 10px;
			margin-bottom: 0;
			.pagin-btn.next{
				margin-right: 0;
			}
			
		}
	}

	.grid-front-fixed {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 1;
		.oas-grid-checkbox,
		.oas-grid-expand {
			width: 40px;
			.oas-grid-td {
				height: 22px;
				line-height: 22px;
			}         
		}
	}

	.grid-main {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		overflow: hidden;
		table {
			width: 100%;
		}
	}

	.grid-finale-fixed {
		position: absolute;
		top: 0;
		right: 0;
		.oas-grid-operator {
			width: 100px;
		}
	}

	thead{
		th {
			padding: 3px 0;
			line-height: 28px;
			height: 36px;
			font-weight: bold;
			&.is-sorted{
				@include background-linear-gradient-to-bottom($oas-grid-thead-sorted-bg,$oas-grid-thead-sorted-bg-to);
			}
			a.sort-icon,a.filter-icon {
				position: relative;
				margin-left: 5px;			  
				top: 2px;
				font-size: 14px;
				text-decoration: none;
				color: $oas-grid-thead-icon;
				&:hover{
					color: $oas-grid-thead-icon-to;
				}
			}
			.col-line{
				position: absolute;
				left: -2px;
				border-left: 3px solid transparent;
				top: 0;
				bottom: 0;
			}
		}
	}
	
	tbody{

		background-color: $oas-grid-tbody-bg;

		.oas-grid-td {
			word-break: break-all;
		}
		tr{
			&.oas-grid-odd{
				td{
					background-color: $oas-grid-tbody-bg-odd;
				}
			}
			&.oas-grid-hover{
				td{
					background-color: $oas-grid-tbody-bg-hover;
				}
			}
			&.oas-grid-active,
			&.oas-grid-selected{
				td{
					background-color: $oas-grid-tbody-bg-active;
				}
			}
			&.oas-grid-disabled{
				td{
					background-color: $oas-grid-tbody-bg-disabled;
				}
			}
			&:first-child{
				td{
					border-top-width: 0;
				}
			}
		}
		td {
			padding: 6px 0;
			border-top: 1px solid $oas-grid-border;
			height: 36px;
			&.oas-grid-operator{
				padding: 5px 0;
			}
			.label{
				margin-right: 4px;
			}
		
		}
	}

	.oas-grid-th {
		position: relative; 
	}
	.oas-grid-th,
	.oas-grid-td{
		padding-left: 10px;
		padding-right: 10px;
	}

	.oas-grid-scroll-v{
		display: none;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		overflow-x: hidden;
		overflow-y: scroll;
		>div{
			border-right: 1px solid transparent;
		}

	}
	.oas-grid-scroll-h {
		margin-left: 0;
		margin-right: 0;
		overflow-x: auto;
		overflow-y: hidden;
		> div {
			border-top: 1px solid transparent;
		}
	}

	.grid-front-fixed-shadow{
		box-shadow: $oas-grid-front-fixed-shadow;
	}
	.grid-finale-fixed-shadow{

		box-shadow: $oas-grid-finale-fixed-shadow;
		th:first-child .oas-grid-th{
			border-left-color: transparent;
			&:after{
				border-left-color: transparent;
			}
		}
	}

	.oas-grid-row{
		display: none;
		background-color: $oas-grid-tbody-level-bg;
		&.oas-grid-level-0{
			display: table-row;
			background-color: $oas-grid-tbody-bg;
		}
		&.oas-grid-open-active{
			.oas-grid-row-expand-icon{
				&:after{
					background: image-url($grid_minus_normal);
				}
				&:hover{
					&:after{
						background: image-url($grid_minus_hover);
					}
				}
			}
		}
		&.oas-grid-has-child .oas-grid-row-expand-icon,
		&.oas-grid-has-content .oas-grid-row-expand-icon{
			visibility: visible;
		}
	}
	.oas-grid-row-expand{
		display: none;
		background-color: $oas-grid-tbody-level-bg;
	}

	.oas-grid-row-expand-icon{
		position: relative;
		top: 2px;
		display: inline-block;
		margin-right: 8px;
		width: 14px;
		height: 14px;
		text-align: center;
		border: 1px solid $btn-default-border;
		border-radius: 2px;
		@include background-linear-gradient-to-bottom($btn-default-bg,$btn-default-bg-to);	
		cursor: pointer;
		visibility: hidden;
		&:after{
			content: "";
			position: absolute;
			top: -1px;
			right: -1px;
			bottom: -1px;
			left: -1px;
			background: image-url($grid_plus_normal);
		}
		&:hover{
			@include background-linear-gradient-to-bottom($btn-default-bg-hover,$btn-default-bg-hover-to);
  			border-color: $btn-default-border-hover;
			&:after{
				background: image-url($grid_plus_hover);
			}
		}    
	}

	.oas-grid-dropdown-filter{
		position: absolute;
		top: 0;
		border: 1px solid $oas-grid-border;
		border-radius: 4px;
		background-color: #fff;
		box-shadow: 3px 3px 8px rgba(63,63,80,.15);
		z-index: 3;
		.grid-filter-select-area{
			padding-bottom: 10px;
			max-height: 250px;
			overflow-y: auto;
			li{
				cursor: pointer;
				label{
					padding: 5px 10px 10px;
					margin: 0;
					min-width: 98px;
				}
				&:hover{
					background-color: $oas-grid-tbody-bg-hover;
					label{
						color: $oas-grid-text-color;
					}
				}
				&.filter-disabled{
					background-color: #fff;
					cursor: not-allowed;
				}
			}
		}
		.grid-filter-operator-area{
			border-top: 1px solid $oas-grid-border;
			text-align: center;
			a{
				float: left;
				width: 50%;
				height: 30px;
				line-height: 30px;
				text-decoration: none;
			}
		}

	}

	&.oas-grid-nowrap .oas-grid-td{
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&.oas-grid-has-scroll-v{
		
		.oas-grid-scroll-h{
			// overflow-x: scroll
		}
		&.oas-grid-multiple-header{
			.oas-grid-thead{
				.grid-main{
					border-right: 1px solid $oas-grid-border;
				}
			}
		}
		.oas-grid-tbody{
			overflow-y: scroll;
		}
		.oas-grid-footer{
			border: none;
		}	
	}

	&.oas-grid-has-border{
		tbody td,.oas-grid-filter th{
			border-left: 1px solid $oas-grid-border;   	 
		}
		.oas-grid-filter>div:first-child{
			th:first-child{
				border-left: none;			
			}
		}
		.oas-grid-tbody>div:first-child{
			td:first-child{
				border-left: none;			
			}
		}
	}

	&.oas-grid-multiple-header{
		.oas-grid-thead{
			.oas-grid-th{
				border-left-color: transparent;
				&:after{
					border-left-color: transparent;
				}
			}
			thead th{
				border-left: 1px solid $oas-grid-border;
				border-top: 1px solid $oas-grid-border;
				padding: 0;
				line-height: normal;
				height: 30px;
			}
			tr:first-child{
				th{
					border-top-color: transparent;
					&.is-sorted{
						border-top-color: $oas-grid-thead-sorted-bg;
					}
				}
			}
			>div:first-child{
				th:first-child{
					border-left-color: transparent;
				}
			}
		}
		
	}	

	&.oas-grid-no-data{
		.oas-grid-tbody{
			.oas-no-data{
				padding: 30px 0;
			}
		}                
	}


	
	//2017-07-06 增


	&.oas-grid-simple{
		overflow-x:auto;
		padding-bottom: 5px;
		margin-bottom: 5px;
		table{
			table-layout: auto;
		}
		thead{
			color: $oas-grid-text-color;
			@include background-linear-gradient-to-bottom($oas-grid-thead-bg,$oas-grid-thead-bg-to);
			border: 1px solid $oas-grid-border;
			
			th{	
				padding: 8px 0;
   				line-height: 18px;
				&:first-child .oas-grid-th {
					border-left: none;
					&:after{
						display: none;
					}
				}
			}
			.oas-grid-th{
				border-left: 1px solid  $oas-grid-thead-right-split-line;
				&:after{
					position: absolute;
					top: 0;
					left: -2px;
					bottom:  0;
					content: "";
					border-left: 1px solid $oas-grid-thead-left-split-line;
				}
			}
		}
		tbody{
			background-color: $oas-grid-tbody-bg;
			border: 1px solid $oas-grid-border;
			border-top-width: 0;
			tr{
				&:hover{
					td{
						background-color: $oas-grid-tbody-bg-hover;
					}
				}

				&.oas-grid-selected{
					td{
						background: $oas-grid-tbody-bg-active;
					}
				}
			}
		}
   }

   //增
   .label{
	   display: inline-block;
   }
}


