
//== Progress bars
//
//##

// Background color of the whole progress component
$progress-bg:                 $gray-3;
// Progress bar text color
$progress-bar-color:          #fff;
$progress-bar-nub-color:      $text-2;
// Variable for setting rounded corners on progress bar.
$progress-border-radius:      10px;

$progress-bar-valuenow-0:     $blue-7;

// Default progress bar color
$progress-bar-bg:             $blue-7;
// Success progress bar color
$progress-bar-success-bg:     $green-6;
// Warning progress bar color
$progress-bar-warning-bg:     $orange-6;
// Danger progress bar color
$progress-bar-danger-bg:      $red-6;
// Info progress bar color
$progress-bar-info-bg:        $blue-6;

$progress-line-height:        12px;