@mixin buttons-dropdown-menu($classname,$dp-color,$dp-border-color,$dp-bg-color,$dp-color-hover,$dp-shadow-color){
	
	#{$classname} ~ .dropdown-menu{
	   border:1px solid  $dp-border-color;
	   box-shadow: 0 2px 7px $dp-shadow-color;
	}

	#{$classname} ~ .dropdown-menu > li > a {
	  &:hover,
	  &:focus {
	    color:$dp-color-hover;
		background-color: $dp-bg-color;
	  }
	}

	// Active state
	#{$classname} ~ .dropdown-menu > .active > a {
	  &,
	  &:hover,
	  &:focus {
	    color: $dp-color-hover;
		background-color: $dp-bg-color;
	  }
	}

}

@mixin btn-group-border($classname,$border-color,$border-color-left,$border-color-right: false){
	.btn-group{
	    #{$classname}{
	        + #{$classname} {
	            border-left-color: $border-color-left !important;
	            @if($border-color-left != false){
		            &:after{
		            	content: "";
		            	position: absolute;
		            	top: 0;
		            	left:0;
		            	width: 1px;
		            	bottom: 0;
		            	background-color: $border-color-right !important;
		            }
		        }
	        }

	       
	    }

	}

	#{$classname} + .btn-group,
	.btn-group + .btn-group{
		#{$classname}{
    		// border-left-color: $border-color-left !important;
            @if($border-color-left != false){
	            &:after{
	            	content: "";
	            	position: absolute;
	            	top: 0;
	            	left:0;
	            	width: 1px;
	            	bottom: 0;
	            	background-color: $border-color-right !important;
	            }
	        }
			&:first-child{
				@if($border-color-left != false){
					 &:after{
	            	 	content: none;
					 }
				}
			}
    	}
	}

	.btn-group-justified{
		&.btn-group{
			#{$classname}{
				+ #{$classname} {
					@if($border-color-left != false){
						&:after{
							content: none;
						}
					}
				}
				+ .btn-group{
					>.btn{
						border-left-color: $border-color-left !important;
					}
					
				}
			}
			.btn-group{
				+ .btn-group{
					>.btn{
						border-left-color: $border-color-left !important;
					}
				}
			} 
		}
	}	
}