/* radio,checkbox 美化样式*/
// hover微动效样式
@keyframes rc-hover-color {
  from {
    border-color: $rc-border-color;
  }
  to {
    border-color: $rc-main-color;
  }
}

// oasis2.0 新增 2017.9.11
.beauty-check {
  input[type="radio"], input[type="checkbox"] {
    position: absolute;
    display: none;
  }
  input[type="radio"][disabled], input[type="checkbox"][disabled] {
    cursor: not-allowed;
  }

  input[type="radio"] + span,
  input[type="checkbox"] + span {
    position: relative;
    display: block;
    padding-left: 20px;
    cursor: pointer;
    vertical-align: middle;

    &:hover {
      &:before {
        animation-duration: 0.4s;
        animation-fill-mode: both;
        animation-name: rc-hover-color;
      }
    }
    &:before {
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -7px;
      display: inline-block;
      width: $checkbox-size;
      height: $checkbox-size;
      content: '';
      border: 1px solid $rc-border-color;
    }
    &:after {
      position: absolute;
      // display: none; //
      content: '';
    }
  }

  input[type="radio"][disabled] + span,
  input[type="checkbox"][disabled] + span {
    cursor: not-allowed;
    color: $rc-disabled-color;
    &:hover,
    &:before,
    &:after {
      cursor: not-allowed;
    }
    &:hover {
      &:before {
        border: 1px solid $rc-disabled-color;
        animation-name: none;
      }
    }
    &:before {
      border-color: $rc-disabled-color;
    }
  }

  input[type="radio"]:checked + span,
  input[type="checkbox"]:checked + span {
    &:before {
      animation-name: none;
    }
    &:after {
      display: block;
    }
  }


  input[type="radio"] + span {
    &:before {
      border-radius: 50%;
    }
    &:after {

      top: 50%;
      margin-top: -3px;
      left: 4px;
      width: $radio-dot-size;
      height: $radio-dot-size;
      border-radius: 50%;

      background: $rc-main-color;
      -webkit-transform: scale(0);
      transform: scale(0);

      border: 1px solid transparent;
      @include background-linear-gradient-to-bottom($radio-checked-bg-from, $radio-checked-bg-to);
      border-color: $radio-checked-border;
      @include box-shadow(0 1px 0 rgba(255, 255, 255, 0.4) inset);

      -webkit-transition: transform .3s ease-out;
      transition: transform .3s ease-out;
    }
  }
  // radio 已选样式
  input[type="radio"]:checked + span {
    &:before {
      border: 1px solid $rc-main-color;
    }
    &:after {
      -webkit-transform: scale(1);
      transform: scale(1);
      -webkit-transition: transform .3s ease-out;
      transition: transform .3s ease-out;
    }
  }
  // radio 已选禁用样式
  input[type="radio"]:checked[disabled] + span {
    color: $rc-disabled-checked-color;
    &:before {
      border: 1px solid $rc-disabled-checked-color;
    }
    &:after {
      border-color: $rc-disabled-checked-color;
      @include background-linear-gradient-to-bottom($checkbox-disabled-checked-bg-from, $checkbox-disabled-checked-bg-to);
    }
  }

  // 正常checkbox
  input[type="checkbox"] + span {
    &:before {
      border-radius: $checkbox-border-radius;
      @include background-linear-gradient-to-bottom($checkbox-bg-from, $checkbox-bg-to);
      box-shadow: 0 1px 0 $checkbox-inset-color inset;
      // transition: background .5s ease-out;
    }
    &:after {
      top: 50%;
      margin-top: -3px;
      left: 3px;
      box-sizing: border-box;
      width: 8px;
      height: 6px;
      background: image-url($form-checkbox);
      -webkit-transform: scale(0);
      transform: scale(0);
      -webkit-transition: transform .3s ease-out;
      transition: transform .3s ease-out;
    }
  }
  // 已选checkbox
  input[type="checkbox"]:checked + span {
    &:before {
      border: 1px solid $rc-main-color;
      @include background-linear-gradient-to-bottom($checkbox-checked-bg-from, $checkbox-checked-bg-to);
      box-shadow: 0 1px 0 $checkbox-checked-inset-color inset;
      // transition: background .3s ease-out;
    }
    &:after {
      -webkit-transform: scale(1);
      transform: scale(1);
      -webkit-transition: transform .3s ease-out;
      transition: transform .3s ease-out;
    }
  }
  // 半选checkbox
  input[type="checkbox"] + span.half {
    &:before {
      border: 1px solid $rc-main-color;
      @include background-linear-gradient-to-bottom($checkbox-checked-bg-from, $checkbox-checked-bg-to);
      box-shadow: 0 1px 0 $checkbox-checked-inset-color inset;
      // transition: background .3s ease-out;
    }
    &:after {
      -webkit-transform: scale(1);
      transform: scale(1);
      margin-top: -2px;
      background: image-url($form_half_checkbox);
      -webkit-transition: transform .3s ease-out;
      transition: transform .3s ease-out;
    }
  }

  // 禁用checkbox
  input[type="checkbox"][disabled] + span {
    &:before {
      border: 1px solid $rc-disabled-color;
      background: #fff;
    }
  }

  // 已选禁用checkbox
  input[type="checkbox"]:checked[disabled] + span {
    color: $rc-disabled-checked-color;
    &:before {
      border: 1px solid $rc-disabled-checked-color;
      @include background-linear-gradient-to-bottom($checkbox-disabled-checked-bg-from, $checkbox-disabled-checked-bg-to);
      box-shadow: 0 1px 0 $checkbox-disabled-checked-inset-color inset;
    }
  }
}

// 带清除按钮的input
.form-clear {

  position: relative;
  .form-control {
      padding-right: 24px;
  }
  i.oasicon {
    cursor: pointer;
    position: absolute;
    font-size: 16px;
    top: 50%;
    margin-top: -8px;
    right: 10px;
    z-index: 10;
    color: $gray-6;
    &.oasicon-fail{
      &:hover {
        color: $blue-6;
      }
    }
  }
}
