// 修改tree在select中的样式
.oas-tree-select {
	&.disabled {
		.select-hd {
			cursor: not-allowed;
			&:hover {
				border: 1px solid $ts-border-disabled;
			}
			.oas-select-multiple-input-selected {
				background-color: $hd-selected-bg-disabled;
			}
		}
	}
	.select-hd {
		color: $text-1;
		line-height: 28px;
		max-height: 86px;
		.oas-select-multiple-ul {
			margin-bottom: 0;
			.oas-select-multiple-input-selected {
				background: $hd-selected-bg;
				&.disabled {
					cursor: not-allowed;
					background: $hd-selected-bg-disabled;
					i.oasicon{
						color: $hd-selected-close;
					}
				}
			}
		}
		.oasicon {
			top: 6px;
			font-size: 16px;
			&.oasicon-fail {
				display: none;
				top: 6px;
				color:$ts-single-close;
				&:hover {
					color: $ts-single-close-hover;
				}
			}
			&.oas-select-multiple-delete {
				font-size: 12px;
				color: $hd-selected-close;
				&:hover {
					color: $hd-selected-close-hover;
				}
				&:active {
					color: $hd-selected-close-active;
				}
			}
		}	
	}
	.oas-select-bd {
		.select-bd{
			.treeContainer {
				// overflow-x: hidden;
			}
			li > a {
				padding-right: 5px;
				height: 24px;
				line-height: 24px;
				border-radius: 3px;
				&:hover {
					background: $bd-selected-bg-hover;
					span,i {
						color: #fff;
					}
				}
				&.current {
					background: $bd-selected-bg;
					font-weight: bold;
					border: 0;
					span,i {
						color: $text-1!important;
					}
				}
			}
			.searchInput {
				margin: 5px auto;
			}
		}
	}
}

