.oas-calendar {
	font-size: 14px;
	width: 240px;
	border: 1px solid $calender-border-color;
	background: $calender-bgc;
	box-shadow: 0 1px 6px rgba( 0, 0, 0, .2);
	border-radius: 3px;
	z-index: $zindex-calendar;
	&.dynamic{
		position: absolute;
	}
	&.noShow{
		border: none;
		margin: 0;
	}
	.oas-calendar-date-panel{
		position: relative;
	}
	a{
		color: $calender-a-color;
		&:hover{
			cursor: pointer;
			text-decoration: none;
		}
	}
	table{
		cursor: pointer;
	}
}
.oas-calendar-hd{
	padding: 0 9px;
	width: 240px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	border-bottom: 1px solid $calender-border-color;
	>.oas-calendar-hd-wrapper{
		position: relative;
		height: 30px;
		.oas-calendar-year-month{
			.oas-calendar-month,.oas-calendar-year{
				display: inline-block;
				padding: 0 2px;
				font-weight: 700;
				color: $calender-year-month-select-color;
				cursor: pointer;
				&:hover{
					text-decoration: none;
					color: $calender-year-month-select-color-hover;
				}
			}
		}
	}

}
.oas-calendar-doubble-left{
	display: inline-block;
	cursor: pointer;
	.oasicon{
		position: absolute;
		left: 0;
		top: 8px;	
		font-size: 14px;
		color: $calender-icon-color;
		cursor: pointer;
		&:hover{
			color: $calender-icon-color-hover;
		}
	}
}
.oas-calendar-left{
	display: inline-block;
	.oasicon{
		position: absolute;
		left: 26px;
		top: 8px;	
		font-size: 14px;
		color: $calender-icon-color;
		cursor: pointer;
		&:hover{
			color: $calender-icon-color-hover;
		}
	}
}
.oas-calendar-doubble-right{
	cursor: pointer;
	.oasicon{
		position: absolute;
		right: 0;
		top: 8px;	
		font-size: 14px;
		color: $calender-icon-color;
		cursor: pointer;
		&:hover{
			color: $calender-icon-color-hover;
		}
	}
}
.oas-calendar-right{
	.oasicon{
		position: absolute;
		right: 26px;
		top: 8px;	
		font-size: 14px;
		color: $calender-icon-color;
		cursor: pointer;
		&:hover{
			color: $calender-icon-color-hover;
		}
	}
}
.oas-calendar-bd{
	padding: 10px 5px 5px 5px;
	.oas-calendar-day-table{
		width: 100%;
		>thead{
			.oas-calendar-row{
				.week-name{
					line-height: 24px;
					padding: 5px 0;
					width: 26px;
					color: $calender-date-color;
					text-align: center;
				}
			}
		}
		>tbody{
			>tr{
				&.weekInchose{
					background-color: $calender-date-bgc-hover;
					>.oas-calendar-day{
						&:not(.disable-item) {
								&:nth-of-type(1){
									background-color: $calender-date-bgc-selected;
									.oas-calendar-date{
										color: $calender-date-color-selected;
									}	 
								}
								&:nth-of-type(7){
									background-color: $calender-date-bgc-selected;
									.oas-calendar-date{
										color: $calender-date-color-selected;
										background-color: $calender-date-bgc-selected;
									}	
								}
						}
					}
				}
				>td{
					padding: 5px 0;
					cursor: pointer;
					&.pre-month-day{
						.oas-calendar-date{
							color: $calender-day-pre-or-next-month-color;
						}	
					}
					&.next-month-day{
						.oas-calendar-date{
							color: $calender-day-pre-or-next-month-color;
						}	
					}
					&.current-month-day{
						&.selectedDay{
							&:not(.disable-item){	
								.oas-calendar-date{
									color: $calender-date-color-selected;
									background-color: $calender-date-bgc-selected;
								}
							}	
						}
					}
					&.oas-today{
						.oas-calendar-date{
							position: relative;
							border: 1px solid $calender-date-bgc-selected;
						}
					}

					&.disable-item{
						.oas-calendar-date{
							cursor: not-allowed;
							color: $calender-date-color-disabled;
							background-color: $calender-date-bgc-disabled;
							&:hover{
								color: $calender-date-color-disabled;
								background-color: $calender-date-bgc-disabled;
								cursor: not-allowed;
							}
						}	
					}
					.oas-calendar-date{
						display: block;
						margin: 0 auto;
						border-radius: 2px;
						width: 28px;
						height: 26px;
						line-height: 26px;
						background: transparent;
						text-align: center;
						color: $calender-date-color;
						&:hover{
							color: $calender-date-color-hover;
							background-color: $calender-date-bgc-hover;
							cursor: pointer;
						}
					}
				}
			}
		}
	}
	.current-month-day{
		&.maySelected{
			.oas-calendar-date{
				color: $calender-date-color-hover;
				background-color: $calender-date-bgc-hover;
			}
		}
	}
}
.oas-calendar-footer{
	line-height: 30px;
	border-top: 1px solid $calender-border-color;
	.oas-calendar-extend{
		padding: 0 5px;
		border-bottom: 1px solid $calender-border-color;
		.oas-calendar-footer-btn{
			text-align: center;
			display: block;
			.oas-calendar-today-btn{
				display: inline-block;
				text-align: center;
				color: $calender-date-a-color;
				padding: 0 10px;
				&:hover{
					color: $calender-date-a-color-hover;
					text-decoration: none;
					cursor: pointer;
				}
				&:active{
					color: $calender-date-a-color-active;
				}
			}
		}
	}
	.oas-calendar-operator{
		width: 238px;
		padding: 0 5px;
		height: 30px;
		background-color: $calender-calendar-operator-bgc;
		>div{
			float: right;
			.oas-calendar-today-btn,.oas-calendar-nowTime-btn{
				display: inline-block;
				text-align: center;
				color: $calender-date-a-color;
				&:hover{
					color: $calender-date-a-color-hover;
					text-decoration: none;
					cursor: pointer;
				}
				&:active{
					color: $calender-date-a-color-active;
				}
			}
			.oas-calendar-makesure{
				margin-left: 5px;
				margin-top: -3px;
				padding: 0 8px;
				height: 24px;
				line-height: 10px;
				font-size: 14px;
				color: $calender-makesure-button-color;
			}
		}
		&.oas-calendar-oneButton{
			>div{
				float: none;
				text-align: center;
				.oas-calendar-makesure{
					display: none;
				}
			}
		}
	}
}

.oas-calendar-month-panel-wrapper{
	position: relative;
	height: 100%;
	.oas-calendar-month-panel-header{
		border-bottom: 1px solid $calender-border-color;
		.oas-calendar-month-panel-pre-year-btn{
			left: 9px;
		}
		.oas-calendar-month-panel-next-year-btn{
			right: 9px;
		}
		.oas-calendar-month-panel-year-select{
			display: inline-block;
			padding: 0 2px;
			font-weight: 700;
			color: $calender-year-month-select-color;
			cursor: pointer;
			&:hover{
				text-decoration: none;
				color: $calender-year-month-select-color-hover;
			}
		}
	}
}
.oas-calendar-month-panel-body{
	height: 295px;
	.oas-calendar-month-panel-table{
		table-layout: fixed;
		width: 100%;
		height: 100%;
		.oas-calendar-month-panel-cell{
			.oas-calendar-month-panel-month{
				border-radius: 3px;
				display: inline-block;
				width: 66px;
				height: 46px;
				line-height: 46px;
				text-align: center;
				background-color: transparent;
				&:hover{
					color: $calender-date-color-hover;
					background-color: $calender-date-bgc-hover;
				}
			}
			&.selectedMonth{
				.oas-calendar-month-panel-month{
					color: $calender-date-color-selected;
					background-color: $calender-date-bgc-selected;
				}
			}
			&.disable-item{
				.oas-calendar-month-panel-month{
					cursor: not-allowed;
					color: $calender-date-color-disabled;
					background-color: $calender-date-bgc-disabled;
					&:hover{
						color: $calender-date-color-disabled;
						background-color: $calender-date-bgc-disabled;
						cursor: not-allowed;
					}
				}
			}
		}
	}
}


.oas-calendar-date-panel{
	.oas-calendar-month-panel{
		display: none;
		position: absolute;
		top: 1px;
		right: 2px;
		bottom: 0px;
		left: 0px;
		z-index: 10;
		border-radius: 3px;
		background: $calender-calendar-month-panel-bgc;
	}
}
.oas-calendar-monthPickerModel{
	.oas-calendar-month-panel{
		width: 240px;
		height: 325px;
		text-align: center;
		.oas-calendar-month-panel-header{
			height: 31px;
			line-height: 31px;
		}
	}
}

.oas-calendar-date-panel,.oas-calendar-monthPickerModel{
	.oas-calendar-year-panel{
		display: none;
		position: absolute;
		top: 1px;
		right: 0;
		bottom: 0;
		left: 0;	
		z-index: 10;
		border-radius: 3px;
		background: $calender-calendar-year-panel-bgc;
	}
}
.oas-calendar-year-panel-wrapper{
	position: relative;
	height: 100%;
	.oas-calendar-year-panel-header{
		height: 31px;
		line-height: 31px;
		text-align: center;
		border-bottom: 1px solid $calender-border-color;
		.oas-calendar-year-panel-pre-year-btn{
			left: 9px;
		}
		.oas-calendar-year-panel-next-year-btn{
			right: 9px;
		}
		.oas-calendar-year-panel-year-select{
			display: inline-block;
			padding: 0 2px;
			font-weight: 700;
			color: $calender-year-month-select-color;
		}
	}
}
.oas-calendar-year-panel-body{
	text-align: center;
	height: 295px;
	.oas-calendar-year-panel-table{
		table-layout: fixed;
		width: 100%;
		height: 100%;
		.oas-calendar-year-panel-cell{
			.oas-calendar-year-panel-year{
				border-radius: 3px;
				display: inline-block;
				width: 66px;
				height: 46px;
				line-height: 46px;
				text-align: center;
				background-color: transparent;
				&:hover{
					color: $calender-date-color-hover;
					background-color: $calender-date-bgc-hover;
				}
			}
			&.selectedYear{
				.oas-calendar-year-panel-year{
					color: $calender-date-color-selected;
					background-color: $calender-date-bgc-selected;
				}
			}
			&.pre-ten-year,&.next-ten-year{
				.oas-calendar-year-panel-year{
					color: $calender-day-pre-or-next-month-color;
				}
			}
			&.disable-item{
				.oas-calendar-year-panel-year{
					cursor: not-allowed;
					color: $calender-date-color-disabled;
					background-color: $calender-date-bgc-disabled;
					&:hover{
						color: $calender-date-color-disabled;
						background-color: $calender-date-bgc-disabled;
						cursor: not-allowed;
					}
				}
			}
		}
	}
}
.oas-calendar-yearPickerModel{
	.oas-calendar-year-panel{
		width: 240px;
		height: 325px;
		text-align: center;
	}
}

.oas-calendar-time-pick-header{
	position: relative;
	border-bottom: 1px solid $calender-border-color;
	display: table;
	width: 100%;
	padding: 3px 2px;
	.oas-calendar-pick-wrap{
		position: relative;
		display: table-cell;
		padding: 0 2px;
		.oas-input{
			position: relative;
			display: inline-block;
			width: 100%;
			.oas-input-inner{
				background-color: $calender-oas-input-inner-bgc;
				border-radius: 3px;
				padding: 0 5px;
				color: $calender-input-inner-color;
				display: inline-block;
				width: 100%;
				background-image: none;
				border: 1px solid $calender-border-color;
				outline: none;
				&.oas-calendar-focus{
					border-color: $calender-border-color-focus;
				}
			}
		}
		.oas-time-panel{
			width: 150px;
			margin: 5px 0;
		}
	}
}

.oas-time-panel{
	visibility: hidden;
	position: absolute;
    z-index: 99;
	background-color: $calender-time-panel-bgc;
	border: 1px solid $calender-border-color;
	box-shadow: 0 2px 4px rgba(0,0,0,.12);
	border-radius: 2px;
	.oas-time-panel-body{
		&::before{
			content: '';
			position: absolute;
			top: 67px;
			width: 100%;
			height: 25px;
			// @include background-linear-gradient-to-right($calender-time-active-bgc-from, $calender-time-active-bgc-to);
			background-color: $calender-time-active-bgc;
		}
		.oas-time-spinner{
			position: relative;
			width: 100%;
			overflow: hidden;
			.oas-time-spinner-wrap{
				display: inline-block;
    			overflow: hidden;
				width: 33%;
				max-height: 150px;
				.oas-time-pick{
					position: relative;
    				top: 4px;
					>div{
						height: 30px;
						line-height: 30px;
						text-align: center;
						// &.swiper-slide-active{
						// 	color: $calender-swiper-slide-active-color;
						// }
						&.show-chose{
							color: $calender-swiper-slide-active-color;
						}
					}
				}
			}
			&:before{
				content: ":";
				position: absolute;
				color: $calender-swiper-slide-active-color;
				top: 68px;
    			left: 33%;
			}
			&:after{
				content: ":";
				position: absolute;
				color: $calender-swiper-slide-active-color;
				top: 68px;
    			left: 66%;
			}
		}
	}
	.oas-time-panel-footer{
		height: 31px;
		line-height: 30px;
		text-align: right;
		padding: 0 8px;
		border-top: 1px solid $calender-border-color;
		.oas-time-cancel{
			color: $calender-time-cancel;
			padding: 0 3px;
			cursor: pointer;
		}
		.oas-time-confirm{
			color: $calender-time-confirm;
			padding: 0 3px;
			cursor: pointer;

		}
	}
}

.oas-input-wrapper{
	position: relative;
	display: inline-block;
	.oasicon{
		cursor: pointer;	
	}
}

.oas-calendar-hidden{
	display: none;
}
.oas-calendar-wrapper{
	position: relative;
}
.oas-calendar{
	&.oas-doubleMonthCalendar{
		width: 487px;
		.oas-calendar-wrapper{
			position: relative;
			display: inline-block;
			width: 240px;
		}
		.oas-calendar-footer{
			.oas-calendar-operator {
				&.oas-calendar-twoButton{
					width: 485px;
				}
			}
		}
		.oas-calendar-month-panel-body{
			height: 263px;
		}
		.oas-calendar-year-panel-body{
			height: 263px;
		}
		.oas-calendar-time-pick-header .oas-calendar-pick-wrap .oas-day-input{
			.oas-input-inner{
				&.oas-calendar-focus{
					border-color: $calender-border-color ;
				}
			}
		}
	}
	.swiper-slide{
		text-align: center;
	}
}
.oas-calendar-double-panel-input-wrapper{
	border: 1px solid $calender-input-border-color;
	width: 371px;
	border-radius: 1px;
	.oas-calendar-startTime{
		display: inline-block;
		width: 180px;
		height: 28px;
		border: none;
		padding-left: 10px;
		text-align: left;
	}
	.oas-calendar-endTime{
		display: inline-block;
		width: 180px;
		height: 28px;
		border:none;
		padding-left: 10px;
		text-align: left;
	}
}
.oas-input-wrapper{
	&.large{
		.oas-calendar-startTime,.oas-calendar-endTime{
			height: 32px;
			line-height: 32px;
		}
		.oas-calendar-icon{
			top: 9px;
		}
	}
	&.small{
		.oas-calendar-startTime,.oas-calendar-endTime{
			height: 24px;
			line-height: 24px;
		}
		.oas-calendar-icon{
			top: 5px;
		}
	}
}
.oas-calendar-disabled{
	cursor: not-allowed !important;
}

.oas-calendar-extend{
	.oas-calendar-shortcut{
		padding: 0 10px;
		display: inline-block;
		// text-align: center;
		color: $calender-date-a-color;
		&:hover{
			color: $calender-date-a-color-hover;
			text-decoration: none;
			cursor: pointer;
		}
		&:active{
			color: $calender-date-a-color-active;
		}
	}
}
.oas-calendar-weekModel{
	.selectedDay{
		.oas-calendar-date{
			color: $calender-date-color !important;
			background: transparent !important;
		}
		&:not(.pre-month-day){
			&:hover{
				.oas-calendar-date{
					color: $calender-date-color !important;
					background: transparent !important;
				}
			}
		}
	}
	.oas-calendar-year-panel-body{
		height: 264px;
	}
	.oas-calendar-month-panel-body{
		height: 264px;
	}
}
.oas-input-wrapper{
	.oas-calendar-icon{
		position: absolute;
		right: 8px;
		top: 8px;
		color: $calender-icon-color;
	}
	>input{
		width: 177px;
		padding-left: 10px;
		&.input-lg{
			width: 224px;
		}
	}
	.oas-calendar-clear{
		display: none;
		&:hover{
			color: $calender-delete-color-hover;
		}
	}
}
.oas-calendar-range-separator{
	width: 9px;
	display: inline-block;
}