/**
* 滚动条样式添加 2017.9.11
*/

/*滚动条整体部分*/
::-webkit-scrollbar{
    width: 12px;
    height: 12px;
    padding: 10px 0;
    background-color: $gray-3;
}
/*滚动条的轨道*/
::-webkit-scrollbar-track{
    width: 12px;
    height: 12px;
    
    background-color: $gray-3;
}

/*滚动条的滑块按钮*/
::-webkit-scrollbar-thumb{
    width: 8px;
    min-height: 50px;
    border-radius: 12px;
    background-color: $gray-6;
    &:hover {
        background-color: $blue-5;
    }
}
/*滚动条的上下两端的按钮*/
::-webkit-scrollbar-button{
    display: none;
}

// IE8
html,body {
  scrollbar-face-color:$gray-3; /*滚动条3D表面（ThreedFace）的颜色*/
  scrollbar-highlight-color:$gray-3; /*滚动条3D界面的亮边（ThreedHighlight）颜色*/
  scrollbar-shadow-color:$blue-5; /*滚动条3D界面的暗边（ThreedShadow）颜色*/
//   scrollbar-3dlight-color:#eeeeee; /*滚动条亮边框颜色*/
//   scrollbar-arrow-color:#000; /*滚动条方向箭头的颜色 */
  scrollbar-track-color:$gray-6; /*滚动条的拖动区域(TrackBar)颜色*/
//   scrollbar-darkshadow-color:#fff; /*滚动条暗边框（ThreedDarkShadow）颜色*/ 
}