//
// Pagination (multiple pages)
// --------------------------------------------------
.pagination {
  display: inline-block;
  padding-left: 0;
  margin: $line-height-computed 0;
  border-radius: $pagination-border-radius-normal;

  > li {
    display: inline; // Remove list-style and block-level defaults
    float:left;
    > a,
    > span {
      position: relative;
      float: left; // Collapse white-space
      padding: $pagination-padding-normal-vertical $pagination-padding-normal-horizontal;
      line-height: $pagination-line-height-normal;
      text-decoration: none;
      color: $pagination-color;
      background-color: $pagination-bg;
      border: 1px solid $pagination-border;
      margin-left: -1px;
    }
    &:first-child {
      > a,
      > span {
        margin-left: 0;
        @include border-left-radius($pagination-border-radius-normal);
      }
    }
    &:last-child {
      > a,
      > span {
        @include border-right-radius($pagination-border-radius-normal);
      }
    }
  }

  > li > a,
  > li > span { 
    &:hover{
      z-index: 3;
      color: $pagination-hover-color;
      border-color: $pagination-hover-border;
      background-color: $pagination-hover-bg;
    }
    &:focus {
      z-index: 3;
      color: $pagination-focus-color;
      @include background-linear-gradient-to-bottom($pagination-bg-hover-from, $pagination-bg-hover-to);
      border-color: $pagination-hover-border;
    }
  }

  > .active > a,
  > .active > span {
    &,
    &:hover,
    &:focus {
      z-index: 2;
      color: $pagination-active-color;
      @include background-linear-gradient-to-bottom($pagination-bg-hover-from, $pagination-bg-hover-to);
      border-color: $pagination-active-border;
      cursor: default;
    }
  }

  > .disabled {
    > span,
    > span:hover,
    > span:focus,
    > a,
    > a:hover,
    > a:focus {
      color: $pagination-disabled-color;
      background-color: $pagination-disabled-bg;
      border-color: $pagination-disabled-border;
      cursor: $cursor-disabled;
    }
  }
}



// oas-pagin
// --------------------------------------------------
.oas-pagin{
  margin:20px 0;
  .pagination{
      margin:0;
      float:right;
      li{
        margin-right:8px;
        >a,
        >span{
          border-radius:$pagination-border-radius-small;
          &.ellipsis{
            padding-left:0;
            padding-right:0;
            border:1px solid transparent;
          }
        }       
      }
      .page-go{
          width:90px;
          height: 34px;
          border-radius:$pagination-border-radius-small;
      }
      &.simple,&.selectPagin{
        margin-top: 2px;
        .page-go{
          font-size: 12px;
          width: 65px;
          height: 24px;
        }
        .prev,.next{
          >a{
            padding: 0;
            width: 24px;
            height: 24px;
            text-align: center;
            line-height: 27px;
          }
          &:not(.disabled){
            >a{
              color: $pagination-simple-chevron-color;
              &:hover{
                color: $pagination-simple-chevron-color-hover;
              }
            }
          }
        }
      }
  }
  .btn-group{
    float:right;
    margin:0 10px 0 0;
  }
  .pagin-text{
    float:left;
    padding: $pagination-padding-normal-vertical 10px;
    color:$pagination-text-color;
    span{
      white-space: nowrap;
    }
  }

  &.pagination-sm {
    .page-go{
        height:30px;
    }
    .btn-group{
      .btn{
        @include button-size($pagination-padding-small-vertical, $pagination-padding-small-horizontal, $pagination-font-size-small, $pagination-line-height-small, $pagination-border-radius-small);
      }
    }
    .pagination{
      &.simple,&.selectPagin{
        margin-top: 2px;
        .page-go{
          font-size: 12px;
          width: 65px;
          height: 22px;
        }
        .prev,.next{
          >a{
            padding: 0;
            width: 22px;
            height: 22px;
            text-align: center;
            line-height: 24px;
          }
          &:not(.disabled){
            >a{
              color: $pagination-simple-chevron-color;
              &:hover{
                color: $pagination-simple-chevron-color-hover;
              }
            }
          }
        }
      }
    }
  }
  &.pagination-lg {
    .page-go{
        height:36px;
    }
    .btn-group{
      .btn{
        @include button-size($pagination-padding-large-vertical, $pagination-padding-large-horizontal, $pagination-font-size-large, $pagination-line-height-large, $pagination-border-radius-large);
      }
    }
    .pagination{
      &.simple,&.selectPagin{
        margin-top: 2px;
        .page-go{
          font-size: 12px;
          width: 65px;
          height: 28px;
        }
        .prev,.next{
          >a{
            padding: 0;
            width: 28px;
            height: 28px;
            text-align: center;
            line-height: 28px;
          }
          &:not(.disabled){
            >a{
              color: $pagination-simple-chevron-color;
              &:hover{
                color: $pagination-simple-chevron-color-hover;
              }
            }
          }
        }
      }
    }
  }

}

// Sizing
// --------------------------------------------------

// Large
.pagination-lg {
  @include pagination-size($pagination-padding-large-vertical, $pagination-padding-large-horizontal, $pagination-font-size-large, $pagination-line-height-large);
  li{
    >a{
      height: 36px;
      >i{
        margin-top: 2px;
      }
    }
  }
}

// Small
.pagination-sm {
  @include pagination-size($pagination-padding-small-vertical, $pagination-padding-small-horizontal, $pagination-font-size-small, $pagination-line-height-small);
  li{
    >a{
      height: 30px;
    }
  }
}




.pagination{
  > li{
    &.next,&.prev{
      > a{
        text-decoration: none;
        color: $pagination-text-color;
        background-color: $pagination-bg;
        background-image: none;
        border: 1px solid $pagination-border;
        &:hover{
          text-decoration: none;
          color: $pagination-pre-next-hover-color;
          background-color: $pagination-bg;
          background-image: none;
          border: 1px solid $pagination-hover-border;
        }
        &:active{
          text-decoration: none;
          color: $pagination-pre-next-hover-color;
          background-color: $pagination-bg;
          background-image: none;
          border: 1px solid $pagination-active-border;
        }
      }
      &.disabled{
        > a{
          text-decoration: none;
          color: $pagination-disabled-color;
          background-color: $pagination-disabled-bg;
          background-image: none;
          border: 1px solid $pagination-disabled-border;
          &:hover{
            text-decoration: none;
            color: $pagination-disabled-color;
            background-color: $pagination-disabled-bg;
            background-image: none;
            border: 1px solid $pagination-disabled-border;
          }
      }

      }
    }
  } 
  .btn-group{
  .oas-pagin-button{
      background-color: $pagination-bg;
      background-image: none;
      border-radius: 3px;
      color: $pagination-color;
      &:hover{
        color: $pagination-button-hover-color;
        border-radius: 3px;
        @include background-linear-gradient-to-bottom($pagination-bg-hover-from, $pagination-bg-hover-to);
      }
      &:active{
        color: $pagination-button-active-color;
        border-radius: 3px;
        @include background-linear-gradient-to-bottom($pagination-bg-hover-from, $pagination-bg-hover-to);
      }
  }
  .dropdown-menu{
    >li{
      >a{
        background-color: $pagination-bg;
        background-image: none;
        &:hover{
          color: $pagination-button-hover-color;
          @include background-linear-gradient-to-bottom($pagination-bg-hover-from, $pagination-bg-hover-to);
        }
        &:active{
          color: $pagination-button-active-color;
          @include background-linear-gradient-to-bottom($pagination-bg-hover-from, $pagination-bg-hover-to);
        }
      }
    }
  }
}
} 