
.oas-kv-group{
	padding: 10px;
	margin: 10px;
	border: 1px solid $gray-5;
	.key{
		width: 98px;
		font-size: 14px;
		text-align: right;
		float: left;
		line-height: 30px;
		color: $text-2;
	}
	.input-item{
		height: 30px;
		float: left;
		margin-right: 10px;
		margin-bottom: 10px;
		.oas-input-wrapper{
			float: left;
		}
		.search-input{
			float: left;
			width:250px;
			height: 30px;
		}
	}
	.group-time{
		width: 706px;
		height: 30px;
		margin-right: 10px;
		margin-bottom: 10px;
		float: left;
		.time-item{
			float: left;
		}
		.time-input{
			float: left;
			width:214px;
			height: 30px;
		}
		.time-icon{
			display:inline-block;
			float: left;
			width: 36px;
			height: 30px;
			border-width: 1px 1px 1px 0;
			border-style: solid;
			border-color: $gray-6;
			text-align: center;
			line-height: 30px;
		}
		.start-time{
			margin-right: 10px;
		}
	}
	
	.city-select-three{
		width:348px;
		height: 30px;
		margin-bottom: 10px;
		float:left;
		.province,.city,.district{
			width: 80px !important;
			float: left;
		}
		.area-item:not(:last-child){
			margin-right: 5px;
		}
	}
	.city-select-two{
		width: 349px;
		height: 30px;
		margin-bottom: 10px;
		float: left;
		.province,.city,.district{
			width: 123px !important;
			float: left;
		}
		.area-item:not(:last-child){
			margin-right: 5px;
		}
	}

	.button-group{
		padding-top: 10px;
		border-top: 1px solid $gray-4;
		clear:both;
	}
		
}