

.oas-timeline{
	
	overflow-y: auto;
	.oas-timeline-row{

		&:first-child{
			.row-hd-line{
			    top: 45px;	    
			    bottom: 0px;
			}
		}

		.oas-timeline-row-hd{
			.row-hd-content{
				min-height: 30px;
			}
		}
		.oas-timeline-row-bd{
			&.last-content{
				.row-bd-line{
				    top: 0;		    
				    height: 38px;
				}
				&:after{
					position: absolute;
					top: 38px;
					bottom: 1px;
				    left: $timeline-left - 1;				   				    
				    border-right: 2px dotted $timeline-point-line-color;
				    content: "";
				}
			}
		}

		&.row-open{
			.oas-timeline-row-bd{
				display: block;
			}
		}
		&.row-close{
			.oas-timeline-row-bd{
				display: none;
			}
		}
		
	}
	.oas-timeline-row-hd{
		
		position: relative;
		padding: 15px 0 15px $timeline-left*2;
		
		.row-hd-line{
		    top: 0;	    
		    bottom: 0px;
		}		

		.row-hd-point{
			position: absolute;
			top: 15px;
			left: 0;
			width: $timeline-left*2;
			color: #fff;
			text-align: center;
			font-size: 0;
			z-index: 2;
			
			span{
				font-size: 15px;
				display: inline-block;
				height: 30px;
				line-height: 30px;				
				vertical-align: middle;
				&.point-text{
					padding: 0 5px;
					background-color: $timeline-point-color;
				}
				&.point-left,
				&.point-right{
					width: 0;
					height: 0;
					border: solid transparent;
					border-width: 15px 9px 15px 9px;					
				}

				&.point-left{
					border-right-color: $timeline-point-color;
				}

				&.point-right{
					border-left-color: $timeline-point-color;
				}
				&.point-icon{
					display: none;
					position: absolute;
					top: 100%;
					left: 50%;
					margin-top: 5px;
					margin-left: -6px;
					width: 12px;
					height: 12px;				
					background: image-url($timeline_minus);
					border-radius: 50%;
				    background-color: $timeline-point-color;
				    cursor: pointer;
				    &.row-close{
						background: image-url($timeline_plus);
						background-color: $timeline-point-color;
				    }
				    &:hover{
				    	background-color: $blue-7;
				    }

				}

			}
		}

	}
	.oas-timeline-row-bd{

		position: relative;
		/*padding: 15px 0;*/
		
		.row-bd-content{
			padding: 15px 0  15px 96px;
		}
		.row-bd-content-inner{
			padding: 10px 0;
		}

		.row-bd-line{
		    top: 0;	    
		    bottom: 0px;
		}

		.row-bd-point{
		    position: absolute;
		    z-index: 3;
		    left: 1px;
		    top: 25px;
		    width: 85px;
		    height: 20px;
		    color: $timeline-point-text-color;
		    line-height: 20px;
		    font-size: 0;
		    text-align: right;

		    span{
		    	font-size: 14px;
		    	display: inline-block;
		    	vertical-align: middle;
		    	&.point-time{
		    		width: 65px;
		    	}
		    	&.point-icon{
		    		position: relative;
		    		width: 12px;
		    		height: 12px;
		    		margin-left: 8px;
		    		border-radius: 50%;
		    		border: 1px solid $timeline-point-color;
		    		background-color: $timeline-point-color;
		    		&:after{
		    			position: absolute;
		    			top: 1px;
		    			right: 1px;
		    			bottom: 1px;
		    			left: 1px;
		    			content: "";
		    			border-radius: 50%;
		    			background-color: #fff;
		    		}
		    	}
		    }
		}

		&:hover{
			.row-bd-point{
				.point-icon{
					background-color: #fff;
					&:after{
		    			background-color: $timeline-point-color;
		    		}
				}
			}
		}
	}

	.oas-timeline-loading{
		position: relative;
		height: 50px;
		
		
		.loading-text{
			padding: 15px 0 15px 96px;
			a{
				text-decoration: none;
				color: $timeline-point-color;
				
			}
			
		}

		.loading-line{
			position: absolute;
			top: 1px;
		    left: $timeline-left - 1;		    				    
		    height: 15px;
		    border-right: 2px dotted $timeline-point-line-color;
		}

		.loading-point{
			position: absolute;
		    z-index: 3;
		    left: 1px;
		    top: 15px;
		    width: 85px;
		    height: 20px;
		    color: $timeline-point-text-color;
		    line-height: 20px;
		    font-size: 0;
		    text-align: right;
		    span{
		    	font-size: 14px;
		    	display: inline-block;
		    	vertical-align: middle;
		    	&.point-icon-loading{
		    		display: none;
		    	}
			}
			
		}

		&.timeline-more,
		&.timeline-end{

			.loading-point{
				span{
					&.point-icon.point-icon-more{
						position: relative;
			    		width: 12px;
			    		height: 12px;
			    		margin-left: 8px;
			    		border-radius: 50%;
			    		border: 1px solid $timeline-point-color;
			    		background-color: $timeline-point-color;
			    		&:after{
			    			position: absolute;
			    			top: 1px;
			    			right: 1px;
			    			bottom: 1px;
			    			left: 1px;
			    			content: "";
			    			border-radius: 50%;
			    			background-color: #fff;
			    		}
					}
				}
			}
		}

		&.timeline-more{
			.loading-text{
				a:hover{
					color: $timeline-point-color-hover;
				}
				a:active{
					color: $timeline-point-color-active;
				}
			}
		}
		&.timeline-loading{
			.loading-point{
				span{
					&.point-icon{
						&.point-icon-more{
							display: none;
						}
						&.point-icon-loading{
							display: inline-block;
							color: $timeline-point-color;
							font-size: 14px;
						}
					}
				}
			}
		}
		&:hover{
			.loading-point{
				span{
					&.point-icon.point-icon-more{
						background-color: #fff;
						&:after{
			    			background-color: $timeline-point-color;
			    		}
					}
				}
			}
		}
	}

	.row-hd-line,
	.row-bd-line{
		position: absolute;
	    left: $timeline-left - 1;		    
	    border-right: 2px solid $timeline-point-line-color;
	}

	&.timeline-center{
		.oas-timeline-row-hd{
			padding-left: 50%;
			.row-hd-content{
				margin-left: 90px;
			}
			.row-hd-point{
				left: 50%;
				margin-left: -$timeline-left;
			}
		}
		.oas-timeline-row-bd{

			.row-bd-point{
				margin-left: - ($timeline-left - 1);
				left: 50%;
			}
			&.last-content{
				&:after{
					margin-left: -1px;
				    left: 50%;				   				    
				}
			}
			&.row-bd-odd{
				.row-bd-content{
					padding-left: 0;
					padding-right: 50%;
					.row-bd-content-inner{
						margin-right: 15px;
						text-align: right;
					}
				}
				.row-bd-point{
					margin-left: -6px;
					text-align: left;
					span{
						&.point-time{
							float: right;
						}
						&.point-icon{
							margin-left: 0;
							margin-right: 8px;
						}

					}
				}
				
			}
			&.row-bd-even{
				.row-bd-content{
					padding-left: 50%;
					.row-bd-content-inner{
						margin-left: 15px;
					}
					
				}
			}
		}
		.oas-timeline-loading{
			.loading-text{
				margin-left: 15px;
				padding-left: 50%;
			}
			.loading-line{
				margin-left: -1px;
				left: 50%;
			}
			.loading-point{
				margin-left: - ($timeline-left - 1);
				left: 50%;
			}
		}
		.row-hd-line,
		.row-bd-line{
			margin-left: -1px;
			left: 50%;
		}
	}

	&.timeline-has-toggle{
		.oas-timeline-row-hd{
			.row-hd-point span.point-icon{
				display: block;
			}
		} 
	}
	
}