
//
// layout
// --------------------------------------------------

#oas-bd{

	#oas-layout-left{
		float: left;
		width: $layout-padding-left;
		margin-left: - $layout-padding-left;
	}
	#oas-content{
		position: static;
	    float: left;
	    width: 100%;
		margin: 0;
		padding: 0;
	}
	#oas-layout-right{
		float: right;
		width: $layout-padding-right;
		margin-right:-$layout-padding-right;
	}

	&.has-both-bar{
		padding: 0 $layout-padding-right 0 $layout-padding-left;
		@include layoutStyle("both","lg",$layout-lg);
		@include layoutStyle("both","sm",$layout-sm);
		@include layoutStyle("both","xm",$layout-xm);
	}
	&.has-left-bar{
		padding: 0 0 0 $layout-padding-left;
		@include layoutStyle("left","lg",$layout-lg);
		@include layoutStyle("left","sm",$layout-sm);
		@include layoutStyle("left","xm",$layout-xm);
	}
	&.has-right-bar{
		padding: 0 $layout-padding-right 0 0;
		@include layoutStyle("right","lg",$layout-lg);
		@include layoutStyle("right","sm",$layout-sm);
		@include layoutStyle("right","xm",$layout-xm);
	}

} 

/*一屏内的*/
.oas-one-screen{
	#oas-layout-left,#oas-content,#oas-layout-right{
	 	overflow:auto;
	}
}

