
//
// Labels
// --------------------------------------------------

.label {
  position: relative;
  display: inline;
  padding: .3em .6em .3em;
  font-size: 75%;
  font-weight: normal;
  line-height: 1;
  color: $label-color;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25em;

  // [converter] extracted a& to a.label

  // Empty labels collapse automatically (not available in IE8)
  &:empty {
    display: none;
  }

  // Quick fix for labels in buttons
  .btn & {
    position: relative;
    top: -1px;
  }
  &.label-default{
    color: $label-default-color;       
  } 
}

// Add hover effects, but only for links
a.label {
  &:hover,
  &:focus {
    color: $label-link-hover-color;
    text-decoration: none;
    cursor: pointer;
  }
  &.label-default,
  &.label-default:hover,
  &.label-default:focus {
       color: $label-default-color;       
  }    
  .label-close{
    position: absolute;
    top:-7px;
    right:-7px;
    width:16px;
    height:16px;
    background: image-url($label_close);
    cursor: pointer;
    &:hover{
      background: image-url($label_close_hover);
    };
  }
  //关闭标签
  &.label-delete{
     .oasicon{
        top:2px;
        font-size: .9em;
        margin-left: .5em;
     }
  }
}

// Colors
// Contextual variations (linked labels get darker on :hover)

.label-default {
  @include label-variant($label-default-bg, $label-default-bg-hover,$label-default-icon);
}

.label-primary {
  @include label-variant($label-primary-bg, $label-primary-bg-hover,$label-primary-icon);
}

.label-success {
  @include label-variant($label-success-bg, $label-success-bg-hover,$label-success-icon);
}

.label-info {
  @include label-variant($label-info-bg, $label-info-bg-hover,$label-info-icon);
}

.label-warning {
  @include label-variant($label-warning-bg, $label-warning-bg-hover,$label-warning-icon);
}

.label-danger {
  @include label-variant($label-danger-bg, $label-danger-bg-hover,$label-danger-icon);
}