//
// loading
// --------------------------------------------------


.oas-loading{
  font-size: 0;
  width: 136px;
  margin: 0 auto;
  padding: 30px 0;
  text-align: center;
  span{
  	display: inline-block;
  	vertical-align: middle;
  	height: 40px;
  	line-height: 40px;
    font-size: 14px;
    color: $blue-6;
    word-break: normal;
  	&.oas-loadingImg{
  		width:40px;
  		margin-right:10px;
  		background: url("../images/loading/Loading.gif") no-repeat;
  	}
  }
  &.oas-max-style{
    width: 150px;
    padding: 50px 0;  
    span{
      height: 54px;
      line-height: 54px;
      &.oas-loadingImg{
        width: 54px;
        background: url("../images/loading/Loading_max.gif") no-repeat; 
      }

    }
  }
  &.oas-min-style{
    width: 116px;
    padding: 10px 0;  
    span{
      height: 18px;
      line-height: 18px;
      &.oas-loadingImg{
        width: 18px;
        background: url("../images/loading/Loading_min.gif") no-repeat; 
      }

    }
  }
}