//
// tabsF
// --------------------------------------------------

.oas-tabs{
  &.styleF{
    width: 150px;
    .tabs-operator-btn{
      position: relative;
      height: 30px;
      line-height: 30px;
      background-color: $tabsF_operator_normal_bgColor;
      border: 1px solid $tabsF_item_normal_borderColor;
      cursor: pointer;
      &.pre{
        border-bottom-width: 0; 
      }
      &.next{
        border-top-width: 0; 
      }
      .triangle{
        position: absolute;
        top:50%;
        left:50%;
        width: 0;
        height: 0;
        margin-top: -6px;
        margin-left:-6px;
        border:6px solid transparent;
        &.up{
          margin-top: -9px;
          border-bottom:6px solid $tabsF_operator_normal_iconColor;
        }
        &.down{
          margin-top: -3px;
          border-top:6px solid $tabsF_operator_normal_iconColor;
        }     
      }
      &:hover{
        background-color: $tabsF_operator_current_bgColor;
        .triangle{
          &.up{
            border-bottom-color:$tabsF_operator_current_iconColor;
          }
          &.down{
            border-top-color:$tabsF_operator_current_iconColor;
          }
        }
      }
    }
    li{
      position: relative;
      a{
        display: block;
        height: 40px;
        padding:0 15px;
        line-height: 38px;
        background-color: $tabsF_item_normal_bgColor;
        border: 1px solid $tabsF_item_normal_bgColor;
        border-bottom-width: 0; 
        color: $tabsF_item_normal_fontColor;
        text-decoration: none;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
        .tab-icon,.tab-name{
          vertical-align: middle;
        }
        .tab-icon{
          top:0;
          margin-right: 7px;
          color: $tabsF_item_normal_iconColor;
        }
      }
      &:first-child{
        a{
          border-top-width: 0;
        }
      }
      &:hover{
        a{
          background-color: $tabsF_item_hover_bgColor;
          color: $tabsF_item_hover_fontColor;
          .tab-icon{
            color: $tabsF_item_hover_fontColor;
          }
        }
      }
      &.current{
        a{
          @include background-linear-gradient-to-bottom($tabsF_item_current_bgColor-from, $tabsF_item_current_bgColor-to);
          border-color: $tabsF_item_normal_borderColor;
          color: $tabsF_item_current_fontColor;
          .tab-icon{
            color: $tabsF_item_current_fontColor;
          }
        }
        &:after{
          content: '';
          position: absolute;
          top: 13px;
          right: 0;
          width: 0;
          height: 0;
          border:7px solid transparent;
          z-index: 1;
          border-right-color: $tabsF_item_current_iconColor;
        }
      }
    }
  }
}