
//== List group
//
//##

// Background color on `.list-group-item`
$list-group-bg:                 #fff;
// `.list-group-item` border color
$list-group-border:             $gray-5;
$list-group-active-border:      #bdbdbd;
// List group border radius
$list-group-border-radius:      $border-radius-base;

// Background color of single list items on hover
$list-group-hover-bg-hover:     $blue-2;
$list-group-hover-bg-focus:     $blue-1;
$list-group-text-color:         $text-1;

// Text color of disabled list items
$list-group-disabled-color:     $text-6;
// Background color of disabled list items
$list-group-disabled-bg:        $gray-2;
// Text color for content within disabled list items
$list-group-disabled-text-color: $text-6;


//success
$list-group-success-bg:			$green-1;
$list-group-success-hover-bg:	$green-2;
$list-group-success-color:		$green-6;

//info
$list-group-info-bg:			$red-1;
$list-group-info-hover-bg:		$red-2;
$list-group-info-color:			$red-6;

//warning
$list-group-warning-bg:			$yellow-1;
$list-group-warning-hover-bg:	$yellow-2;
$list-group-warning-color:		$yellow-6;

//danger
$list-group-danger-bg:			$blue-1;
$list-group-danger-hover-bg:	$blue-2;
$list-group-danger-color:		$blue-6;
