// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

@mixin button-variant($color, $bg, $border, $hover-color, $hover-bg, $hover-border, $active-color, $active-bg, $bg-to: false, $hover-bg-to: false, $active-bg-to: false, $clicked-color: false, $shadow-opacity: false, $hover-shadow-opacity: false, $active-shadow-opacity: false) {
  color: $color;
  @include background-linear-gradient-to-bottom($bg,$bg-to);
  border-color: $border;

  @if($shadow-opacity != false and $support-gradient == true){
    box-shadow: 0 1px 0 rgba(255,255,255,$shadow-opacity) inset;
  }

  &:focus,
  &.focus {
    color: $active-color ;
      @include background-linear-gradient-to-bottom($active-bg,$active-bg-to);
        border-color: $active-bg;
    &:hover{
      color: $active-color;
      @include background-linear-gradient-to-bottom($active-bg,$active-bg-to);
          border-color: $active-bg;
    }
    @if($hover-shadow-opacity != false and $support-gradient == true){
        box-shadow: 0 1px 0 rgba(255,255,255,$hover-shadow-opacity) inset;
    }
  }
  &:hover {
    color:  $hover-color;
    @include background-linear-gradient-to-bottom($hover-bg,$hover-bg-to);
        border-color: $hover-border;
    @if($hover-shadow-opacity != false and $support-gradient == true){
        box-shadow: 0 1px 0 rgba(255,255,255,$hover-shadow-opacity) inset;
    }
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $active-color;
    @include background-linear-gradient-to-bottom($active-bg,$active-bg-to);
        border-color: $active-bg;
    @if($active-shadow-opacity != false and $support-gradient == true){
        box-shadow: 0 1px 0 rgba(255,255,255,$active-shadow-opacity) inset;
    }

    &:hover,
    &:focus,
    &.focus {
      color: $active-color;;
      @include background-linear-gradient-to-bottom($active-bg,$active-bg-to);
          border-color: $active-bg;
      @if($active-shadow-opacity != false and $support-gradient == true){
        box-shadow: 0 1px 0 rgba(255,255,255,$active-shadow-opacity) inset;
      }
    }
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      color: $btn-disabled-color;
      @include background-linear-gradient-to-bottom($btn-disabled-bg,$btn-disabled-bg-to);
			border-color: $btn-disabled-border-color;
    }
  }

  .badge {
    color: $bg;
    background-color: $color;
  }

  &.btn-clicked:after{
		border-color: $active-bg;
	}

	&.btn-clicked:before{
		content: "";
		position: absolute;;
		top: -1px;
		right: -1px;
		bottom: -1px;
		left: -1px;
		border-radius: inherit;
		border: 0 solid $clicked-color;
		opacity: .5;
		-webkit-animation: buttonEffect .6s;
		animation: buttonEffect .6s;
		display: block;
		z-index: 2;
	}


}

// Button sizes
@mixin button-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius) {
  padding: $padding-vertical $padding-horizontal;
  font-size: $font-size;
  line-height: $line-height;
  border-radius: $border-radius;
  .oasicon,
  .glyphicon{
    font-size: $font-size;
  }
}

// btn animation

@-webkit-keyframes buttonEffect{
  to{
    opacity: 0;
    top: -6px;
    right: -6px;
    bottom: -6px;
    left: -6px;
    border-width: 6px;
  }
}

@keyframes buttonEffect{
  to{
    opacity: 0;
    top: -6px;
    right: -6px;
    bottom: -6px;
    left: -6px;
    border-width: 6px;
  }
}
