
//== Buttons
//
//## For each of Bootstrap's buttons, define text, background and border color.


$btn-font-weight:                	normal;
$btn-shadow-active:                 inset 0 3px 5px rgba(0,0,0,.125);

$btn-default-color:              	$text-1;
$btn-default-bg:                 	$gray-1;
$btn-default-border:             	$gray-5;
$btn-default-color-hover:        	#fff;
$btn-default-bg-hover:			 	$blue-5;
$btn-default-border-hover:       	$blue-6;
$btn-default-bg-to:              	$gray-3;
$btn-default-bg-hover-to:        	$blue-7;
$btn-default-clicked-color:         $blue-8;
$btn-default-shadow-opacity:     	1;
$btn-default-shadow-opacity-hover:  .4;
$btn-default-shadow-opacity-active: .4;

$btn-default-badge-color:           #fff;
$btn-default-badge-bg:              $red-6; 

