.oasGridTree{
	width: 100%;
	thead{
		tr{
			th{
				padding: 8px 10px;
				border: 1px solid $gridTree-border-color;
				font-size: 14px;
				color: $gridTree-thead-color;
				font-weight: bold;
				@include background-linear-gradient-to-bottom($gridTree-thead-bgc-from,$gridTree-thead-bgc-to);
				line-height: 17px;
				height: 17px;
				text-align: left;
			}
		}
	}
	tbody{
		tr{
			cursor: pointer;
			td{
				padding: 8px 10px;
				border: 1px solid $gridTree-border-color;
				font-size: 14px;
				color: $gridTree-tbody-color;
				line-height: 17px;
				height: 17px;
				text-align: left;
				&.td-root{
					a{
						text-decoration: none;
						vertical-align: middle;
						i[use="icon"]{
							font-size: 18px;
							vertical-align: middle;
							color: $gridTree-tbody-color-deep;
						}
						.toggle-icon{
							display: inline-block;
							vertical-align: middle;
							width: 10px;
							height: 10px;
							background: image-url($arrow);
							&.hide{
								display: none;
							}
							&:hover{
								background: image-url($arrow_hover);
							};
						}
						span{
							vertical-align: middle;
							font-size: 14px;
							color: $gridTree-tbody-color-deep;
						}
					}
				}
				&.td-first{
					a{
						text-decoration: none;
						vertical-align: middle;
						i[use="icon"]{
							font-size: 14px;
							vertical-align: middle;
							color: $gridTree-tbody-color;
						}
						.toggle-icon{
							display: inline-block;
							width: 10px;
							height: 10px;
							vertical-align: middle;
							background: image-url($arrow);
							&.hide{
								display: none;
							}
							&:hover{
								background: image-url($arrow_hover);
							};
						}
						span{
							vertical-align: middle;
							font-size: 12px;
							color: $gridTree-tbody-color;
							&.deep{
								color: $gridTree-tbody-color-deep;
							}
						}
					}
				}
			}
			&.open{
				td{
					&.td-root{
						a{
							.toggle-icon{
								background: image-url($arrow_toggle);
								&:hover{
									background: image-url($arrow_toggle_hover);
								};
							}
						}
					}
					&.td-first{
						a{
							.toggle-icon{
								background: image-url($arrow_toggle);
								&:hover{
									background: image-url($arrow_toggle_hover);
								};
							}
						}
					}
				}
			}
			&.tr-hide{
				display: none;
			}
		}
	}
	.oas-gridTree-tbody{
		.oas-gridTree-hover{
			background-color: $gridTree-hover-bgc;
		}
		.oas-gridTree-active{
		    background: $gridTree-active-bgc;
		}
	}
}
.oas-gridTree-head{
	position: relative;
    padding-bottom: 10px;
    background-color: $gridTree-head-bgc;
    z-index: 2;
}
.oas-gridTree-operator{
	color: $gridTree-operator;
	&:hover{
		color: $gridTree-operator-hover;
	}
}
