//
// bottomBar
// --------------------------------------------------


.oas-bottomBar{
    position: fixed;
    padding: 6px 10px;
    left: 10px;
    right: 10px;
    z-index: 99980;
    bottom: 10px;
    background-color: $bottomBar-bg;
    border: 1px solid $bottomBar-border;
    box-shadow: $bottomBar-shadow;
    font-size: 0;
    .oas-bottomBar-btn{
      margin-right: 10px;
    }
    .oas-bottomBar-tip{
      font-size: 14px;
      color: $bottomBar-helper-color;
      vertical-align: middle;
    }
}