
//== Formtip
//
//##

// formtip max width
$formtip-max-width:           		200px;
// formtip text color
$formtip-color:               		#fff;
// formtip background color
$formtip-bg:                  		$input-border-focus;
$formtip-opacity:             		.9;
$formtip-bg-error:			  		$brand-danger;

// formtip arrow width
$formtip-arrow-width:         		7px;
// formtip arrow color
$formtip-arrow-color:         		$input-border-focus;
$formtip-arrow-wrap-color:    		$input-border-focus;
$formtip-border-width:        		1px;
$formtip-border-color:        		$input-border-focus;
$formtip-border-style:        		solid; 

$formtip-border-color-error:        $brand-danger;
$formtip-arrow-color-error:         $brand-danger;
$formtip-arrow-wrap-color-error:    $brand-danger; 
