$pagination-color:                          $text-1;
$pagination-bg:                             #fff;
$pagination-border:                         $gray-6;

$pagination-button-hover-color:             #fff;
$pagination-button-active-color:            #fff;
$pagination-hover-color:                    $blue-6;
$pagination-focus-color:                    #fff;
$pagination-hover-bg:                       #fff;
$pagination-pre-next-hover-color:           $blue-6;
$pagination-bg-hover-from:                  $blue-6;
$pagination-bg-hover-to:                    $blue-6;
$pagination-hover-border:                   $blue-6;

$pagination-active-color:                   #fff;
$pagination-active-border:                  $blue-5;

$pagination-disabled-color:                 $text-6;
$pagination-disabled-bg:                    $gray-2;
$pagination-disabled-border:                $gray-5;

//文本颜色
$pagination-text-color:				        $text-4;
$pagination-simple-chevron-color:	        $text-3;
$pagination-simple-chevron-color-hover:	    $blue-6;



$pagination-padding-large-vertical:         6px;
$pagination-padding-normal-vertical:        6px;
$pagination-padding-small-vertical:         5px;

$pagination-padding-large-horizontal:       13px;
$pagination-padding-normal-horizontal:      12px;
$pagination-padding-small-horizontal:       11px;

$pagination-font-size-large:                16px;
$pagination-font-size-normal:               14px;
$pagination-font-size-small:                12px;

$pagination-line-height-large:              1.3333333;
$pagination-line-height-normal:             1.438571429;
$pagination-line-height-small:              1.5;

$pagination-border-radius-large:            4px;
$pagination-border-radius-normal:           3px;
$pagination-border-radius-small:            2px;

// 为保证pager文件不出错暂时保留
$pagination-hover-bg:                       $gray-lighter;
$pagination-active-bg:                      $link-hover-color;


