
//== Panels
//
//##

$panel-bg:                    #fff;
$panel-text:                  $text-1;
$panel-body-padding:          10px 10px;
$panel-heading-padding:       5px 10px;
$panel-footer-padding:        $panel-heading-padding;
$panel-border-radius:         $border-radius-base;

// Border color for elements within panels
$panel-inner-border:          $gray-4;
$panel-footer-bg:             $gray-2;

$panel-default-text:          $panel-text;
$panel-default-border:        $gray-5;
$panel-default-heading-bg:    $gray-3;

$panel-primary-text:          #fff;
$panel-primary-border:        $blue-5;
$panel-primary-heading-bg:    $blue-6;

$panel-success-text:          $green-6;
$panel-success-border:        $green-3;
$panel-success-heading-bg:    $green-2;

$panel-info-text:             $blue-6;
$panel-info-border:           $blue-3;
$panel-info-heading-bg:       $blue-2;

$panel-warning-text:          $orange-6;
$panel-warning-border:        $orange-3;
$panel-warning-heading-bg:    $orange-2;

$panel-danger-text:           $red-6;
$panel-danger-border:         $red-3;
$panel-danger-heading-bg:     $red-2;