.oas-number-roll {
	.oas-number {
		float:left;
		position: relative;
		overflow: hidden;
		>.oas-number-text {
			position: relative;
			z-index:2;
		}
		.oas-other {
			position: absolute;
			top:0;
			left: 0;
			visibility: visible;
			&.hide {
				visibility: hidden;
			}
			&.show {
				visibility: visible;
			}
		}
		.oas-number-content {
			&.hide {
				display:block !important;
				visibility: hidden;
			}
			&.show {
				visibility: visible;
			}
		}
	}
	.oas-number-text {
		line-height: 100%;
	}
}