//
// Help
// --------------------------------------------------


// Contextual colors

// Alignment
.text-left           { text-align: left; }
.text-right          { text-align: right; }
.text-center         { text-align: center; }
.text-justify        { text-align: justify; }
.text-nowrap         { white-space: nowrap; }

// Transformation
.text-lowercase      { text-transform: lowercase; }
.text-uppercase      { text-transform: uppercase; }
.text-capitalize     { text-transform: capitalize; }

.text-muted {
  color: $helper-text-muted;
}

.text-disabled {
  color: $brand-disable;
  &:hover {
    cursor:$cursor-disabled;
  }
}

@include text-emphasis-variant('.text-primary', $helper-state-primary-text);

@include text-emphasis-variant('.text-success', $helper-state-success-text);

@include text-emphasis-variant('.text-info', $helper-state-info-text);

@include text-emphasis-variant('.text-warning', $helper-state-warning-text);

@include text-emphasis-variant('.text-danger', $helper-state-danger-text);

// Contextual backgrounds
// For now we'll leave these alongside the text classes until v4 when we can
// safely shift things around (per SemVer rules).
.bg-primary,.bg-success,.bg-info,.bg-warning,.bg-danger {
  // Given the contrast here, this is the only class to have its color inverted
  // automatically.
  color: $helper-state-other-color;
}

.bg-primary{
	color:$helper-state-primary-color;
}

@include bg-variant('.bg-primary', $helper-state-primary-bg);

@include bg-variant('.bg-success', $helper-state-success-bg);

@include bg-variant('.bg-info', $helper-state-info-bg);

@include bg-variant('.bg-warning', $helper-state-warning-bg);

@include bg-variant('.bg-danger', $helper-state-danger-bg);


