
//== guide
//

.oas-guide.styleA{
	margin-bottom: 10px;
	ul{
		font-size: 0;
    	white-space: nowrap;
		li{
			position: relative;
			display: inline-block;
			font-size: 14px;
			height: $guide-height;
			min-width: 100px;
			padding-right: 40px;
			background: url("../images/guide_bg.png") no-repeat right top;
			// background: image-url($guide_bg);
			&.last{
				background-position: left top;
				&:after{
					position:absolute;
					content: "";
					color:$guide-last-after-color;
					width: 19px;
					height: 28px;
					right: -19px;
					top:0;
					background: image-url($guide_last);
				}
				&.oas-guide-active{
					background-position: left bottom;
				}
			}
			.guide-number{
				float: left;
				margin:5px 10px 0 20px;
				width: 18px;
				height: 18px;
				line-height: 18px;
				text-align: center;
				font-weight: bold;
				background: image-url($guide_dot);
				color:$guide-number;
				font-family: arial;
				.oasicon{
					display: none;
				}
			}
			.guide-text{
				float: left;
				color: $guide-color;
				height: $guide-height;
				line-height: $guide-height;
				max-width:220px;
				overflow:hidden;
				white-space:nowrap;
				text-overflow:ellipsis;
			}
			&.oas-guide-active{
				background-position: right bottom;
				.guide-text{
					color: $guide-color-active;
				}
				.guide-number{
					color: $guide-number-active;
					background: image-url($guide_dot_active);
				}
				&.last{
					&:after{
						color:$guide-last-after-color-active;
						background: image-url($guide_last_active);
					}
				}
			}
			&.guide-active-prev{
				background-position: right center;
			}
			&.oas-guide-complete{
				.guide-number{
					span{
						display: none;
					}
					.oasicon{
						display: inline-block;
					}
				}
			}
		}
	}
}

.oas-guide.styleB{
	margin-bottom: 10px;
	ul{
		font-size: 0;
    	white-space: nowrap;
		height: 55px;
		li{
			position: relative;
			display: inline-block;
			font-size: 14px;
			height: 20px;
			width: 185px;
			margin-right: 2px;
			border-bottom:4px solid $guide-styleB-bg;
			.guide-number{
				position: absolute;
				width: 18px;
				height: 18px;
				line-height: 18px;
				text-align: center;
				color:$guide-number;
				font-weight: bold;
				left: 50%;
				bottom: -10px;
				margin-left: -9px;
				// background: image-url($guideB_circle);
				border-radius: 10px;
    			background: $gray-6;
				.oasicon{
					display: none;
				}
			}
			.guide-text{
				position: absolute;
				left: 0;
				right: 0;
				bottom: -40px;
				text-align: center;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				color:$guide-color;
			}
			&.oas-guide-active{
				border-color: $guide-styleB-bg-active;
				.guide-number{
					width: 26px;
					height: 26px;
					line-height: 26px;
					margin-left:-13px;
					bottom: -15px;
					// background: image-url($guideB_circle_active);
					border-radius: 13px;
    				background: $blue-6;
				}
			}
			&.oas-guide-complete{
				.guide-number{
					// background: image-url($guideB_circle_compelete);
					border-radius: 13px;
    				background: $blue-6;
					span{
						display: none;
					}
					.oasicon{
						display: inline-block;
					}
				}
			}
		}
	}
}