.oas-horizontal-menu{
    display: inline-block;
    height: 46px;
    background-color: $oas-hmenu;
    >ul{
        list-style: none;
        display: inline-block;
        &.first-level-nav{
            >li{
                position: relative;
                float: left;
                text-align: center;
                min-width: 106px;
                min-width: 67px\9;
                height: 46px;
                padding: 0 20px 0 22px; 
                line-height: 46px;
                border-bottom: 3px solid transparent;
                cursor: pointer;
                >a{
                    >span{
                        font-size: 16px;
                        color: $hmenu-fistlevel-color;
                    }
                    &::before{
                        content: '';
                        position: absolute;
                        top:0;
                        right:0;
                        bottom:0;
                        left:0;
                    }
                }
                // &:before{
                //     content:'';
                //     position: absolute;
                //     top: 0;
                //     left: -1px;
                //     width:1px;
                //     height: 46px;
                //     background-color: $hmenu-border-left-bgc;
                //     // @include background-linear-gradient-3-to-bottom($hmenu-border-linear-gradient-left-first,$hmenu-border-linear-gradient-left-second,$hmenu-border-linear-gradient-left-third);
                // }
                // &:after{
                //     content:'';
                //     position: absolute;
                //     top: 0;
                //     left: 0px;
                //     width:1px;
                //     height: 46px;
                //     background-color: $hmenu-border-right-bgc;
                //     // @include background-linear-gradient-3-to-bottom($hmenu-border-linear-gradient-right-first,$hmenu-border-linear-gradient-right-second,$hmenu-border-linear-gradient-right-third);
                // }
                &:nth-of-type(1){
                    &:before{
                        width:0px;
                    }
                    &:after{
                        width:0px;
                    }
                }
                &.active{
                    border-bottom-color: $hmenu-fistlevel-border-bottom-color-active;
                    >a{
                        >span{
                            color: $hmenu-fistlevel-color-active;
                        }
                    }
                }
                &:hover{
                    border-bottom-color: $hmenu-fistlevel-border-bottom-color-active;
                    @include background-linear-gradient-to-bottom($hmenu-fistlevel-hover-bgc-from, $hmenu-fistlevel-hover-bgc-to);
                    >ul{
                        display: block;
                    }          
                }
            }
        }
    }
    .second-level-nav{
        position: absolute;
        left: 0;
        top: 46px;
        display: none; 
        min-width: 106px;
        box-shadow: 0 1px 6px rgba(0,0,0,.2);
        background-color: $hmenu-secondlevel-bgc;
        z-index: 99999;
        border: 1px solid;
        border-color: transparent;
        border-color: $hn-ie-border-color;
        >li{
            position: relative;
            padding: 0 10px;
            height: 46px;
            line-height: 46px;
            text-align: center;
            font-size: 14px;
            color: $hmenu-secondlevel-color;
            white-space: nowrap;
            >a{
                &::before{
                    content: '';
                    position: absolute;
                    top:0;
                    right:0;
                    bottom:0;
                    left:0;
                }
                >span{
                    color: $hmenu-secondlevel-color;
                }
            }
            &:hover{
                >a{
                    >span{
                        color: $hmenu-secondlevel-color-active;
                    }
                }
                >ul{
                    display: block;
                }
            }
            &.active{
                >a{
                    >span{
                        color: $hmenu-secondlevel-color-active;
                    }
                }
                >ul{
                    display: block;
                }
            }
        } 
    }
    .third-level-nav{
        position: absolute;
        left: 100%;
        margin-left: 2px;
        top: 0;
        display: none; 
        min-width: 106px;
        box-shadow: 0 1px 6px rgba(0,0,0,.2);
        background-color: $hmenu-thirdlevel-gbc;
        z-index: 999;
        border: 1px solid;
        border-color: transparent;
        border-color: $hn-ie-border-color;
        >li{
            position: relative;
            padding: 0 10px;
            height: 46px;
            line-height: 46px;
            text-align: center;
            font-size: 14px;
            color: $hmenu-thirdlevel-color;
            white-space: nowrap;
            >a{
                &::before{
                    content: '';
                    position: absolute;
                    top:0;
                    right:0;
                    bottom:0;
                    left:0;
                }
                >span{
                    color: $hmenu-thirdlevel-color;
                }
            }
            &:hover{
                >a{
                    >span{
                        color: $hmenu-thirdlevel-color-active;
                    }
                }
                background-color: $hmenu-thirdlevel-hover-bgc;
                // @include background-linear-gradient-to-right($hmenu-thirdlevel-hover-bgc-from, $hmenu-thirdlevel-hover-bgc-to);
            }
            &.active{
                >a{
                    >span{
                        color: $hmenu-thirdlevel-color-active;
                    }
                }
                background-color: $hmenu-thirdlevel-hover-bgc;
                // @include background-linear-gradient-to-right($hmenu-thirdlevel-hover-bgc-from, $hmenu-thirdlevel-hover-bgc-to);
            }
        }
        // 扩大区域
        &:before{
            content:'';
            position: absolute;
            left: -5px;
            height: 100%;
            width: 5px;
        }
    }
    a{
        color: transparent;
        &:hover{
            color: transparent;
            text-decoration: none;
        }
    }
}