.oas-select { 
	display: inline-block;
	vertical-align: middle;
	font-size: 14px;
	color: $select-text-color;
	&.open{
		.select-hd{
			border-color: $select-border-active-color;
			box-shadow: 0 0 0 3px $select-bd-shadow-active-color; 
			&.select-search-input.oas-select-nomatch{
				box-shadow: 0 0 0 3px $select-bd-input-shadow-active-color; 
				border-color: $select-bd-input-active-border-color;
				&:hover{
					border-color: $select-bd-input-active-border-color;
				}
				&:focus{
					border-color: $select-bd-input-active-border-color;
					outline: none;
				}
			}
		}
	}
	.select-hd{
		.select-value,.option-value{
			// float: left;
		}
		.select-comment,.option-comment{
			// float: right;
			color: $select-option-comment-color;
			// &:hover{
			// 	color: $select-option-comment-color-hover;
			// }
		}
		.option-comment{
			font-weight: normal;
			&:hover{
				font-weight: normal;
			}
		}
		&.select-search-input.oas-select-nomatch{
			box-shadow: 0 0 0 3px $select-bd-input-shadow-active-color; 
			border-color: $select-bd-input-active-border-color;
			&:hover{
				border-color: $select-bd-input-active-border-color;
			}
			&:focus{
				border-color: $select-bd-input-active-border-color;
				outline: none;
			}
		}
	}
	.pagin-text{
		padding-left: 6px;
	}
	.oasicon-search{
		cursor: pointer;
		position: absolute;
		font-size: 16px;
		top: 7px;
		right: 10px;
		z-index: 100;
		color: $select-icon-color;
		&.active{
			color: $select-icon-color-active;
		}
	}
