	// 	.multiple-hd{
	// 		min-height: 26px;
	// 		line-height: 24px;
	// 		.select-placeholder{
	// 			height: 24px;
	// 		}
	// 	}
	// }
}
.select-hd {
	position: relative;
	z-index: $zindex-select-hd;
	height: 30px;
	padding: 0 30px 0 10px;
	line-height: 30px;
	border: 1px solid $select-border-color;
	background: $select-input-color;
	color: $select-text-placeholder-color;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	border-radius: 2px;
	.oasicon-desc{
        transition: transform .2s ease-in;
        transform: rotate(0);
        &.close-animation{
            transform: rotate(180deg);
        }
    }
	.select-placeholder{
		color: $select-text-placeholder-color;
		&:hover{
			color: $select-text-placeholder-color;
		}
	}
	&.select-search-input{
		width: 100%;
		color: $select-text-color;
	}
	&:hover {
		border: 1px solid $select-border-hover-color;
		color: $select-text-color;
		background-color: $select-input-bgc;
		.oasicon {
			color: $select-arrow-color;
		}
	}
	
	&.selected {
		color: $select-text-color;
	}

	&.disabled {
		cursor: not-allowed;
		border-color: $select-border-disabled-color;
		color: $select-text-disabled-color;
		background-color: $select-input-disabled-bgc;
		.oasicon {
			color: $select-arrow-disabled-color;
		}
		&:hover {
			border-color: $select-border-disabled-color;
			color: $select-text-disabled-color;
			background-color: $select-input-disabled-bgc;
		}
	}


	p {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
	}
	.oasicon {
		position: absolute;
		right: 9px;
		top: 9px;
		font-size: 14px;
		color: $select-arrow-color;
		text-decoration: none;
	}
}
.oas-select{
	.oas-select-bd{
		min-width: inherit;
	}
	&.large{
		.select-hd{
			&:not(.multiple-hd){
				height: 34px;
				line-height: 34px;
				.oasicon{
					font-size: 16px;
				}
			}
		}
		.oasicon{
			top: 9px;
			font-size: 16px;
		}
	}
	&.small{
		.select-hd{
			&:not(.multiple-hd){
				height: 26px;
				line-height: 26px;
				.oasicon{
					top: 7px;
					font-size: 12px;
				}
			}
		}
		.oasicon{
			top: 7px;
			font-size: 12px;
		}
	}
}
.oas-select-bd {
	z-index: $zindex-select-bd; 
	display: none;
	margin: -1px 0 0;
	top: 100%;
	border-color: $select-border-active-color;
	border-radius: 2px;
	// overflow-x: hidden;
	// overflow-y: auto;
	height: auto;
	box-shadow: 0 1px 6px $select-bd-shadow-color;
	background-color: $select-bd-bgc;
	.select-bd{
		overflow-x: hidden;
		overflow-y: auto;
		max-height: inherit;
		.oas-select-searching{
			text-align: center;
			height: 32px;
			line-height: 32px;
			color: $select-searching-color;
			.oas-select-searching-icon{
				top: 3px;
			}
			.oas-select-searching-text{
				margin-left: 5px;
			}
		}
		.oas-select-multiple-selected{
			background-color: $select-multiple-option-chosen;
			font-weight: bold;
		}
	}
	li {
		line-height: 30px;
		color: $select-text-color;
	}
	.select-optgroup-li {
		strong {
			display: block;
			cursor: text;
			padding: 0 10px;
			color: $select-bd-group-color;
			font-weight: normal;
		}
	}
	.select-bd-li {
		padding: 0 10px;
		overflow: hidden;
  		white-space: nowrap;
		text-overflow: ellipsis;
		&.focus{
			cursor: pointer;
		}
		.option-value{
			// float: left;
			// max-width: 70%;
			// display: inline-block;
			// overflow: hidden;
		}
		.option-comment{
			margin-left: 5px;
			// float: right;
			// max-width: 20%;
			// display: inline-block;
			// overflow: hidden;
			font-weight: normal;
			color: $select-option-comment-color;
			&:hover{
				font-weight: normal;
				color: $select-option-comment-color-hover;
			}
		}
		&.focus{
			.option-comment{
				font-weight: normal;
				color: $select-option-comment-color-hover;
			}
		}
		&.disabled{
			>span{
			    color: $select-bd-disabled-color !important;
			}
		}
	}
	.select-bd-li.focus, .select-bd-li.selected {
		color: $select-bd-focus-color;
		background-color: $select-bd-focus-bgc;
	}
	.select-bd-li.disabled {
		cursor: not-allowed !important;
		color: $select-bd-disabled-color;
	}
	.select-optgroup-li{
		.select-bd-li {
			padding: 0 10px 0 20px;
			cursor: pointer;
		}
	}
	
	.oas-select-ft{
		height: 36px;
		border-top: 1px solid $select-border-color;
	}
	.oas-select-noData{
		height: 32px;
		line-height: 32px;
		padding: 0 8px;
		color: $select-text-color;
	}
}
.oas-select-special{
	color: $select-special-color !important;
	&:hover{
		color: $select-special-color-hover !important;
	}
	&.focus{
		color: $select-special-color-hover !important;
	}
}

.oas-select-ft{
	.oas-select-ft-pagin{
		position: relative;
		margin: 5px 0;
		height: 26px;	
		.pagination {
			float: none;
			position: absolute;
			top: 0px;
  			right: 5px;
		}
	}
}
.oas-select-search-value{
	font-style: normal;
	font-weight: bold;
}
.oas-select-multiple-ul{
	.oas-select-multiple-input-selected{
		position: relative;
		float: left;
		height: 20px;
		line-height: 20px;
		border-radius: 3px;
		color: $select-text-color;
		margin-top: 8px;
		margin-left: 3px;
		padding: 0 20px 0 6px;
		list-style: none;
		background-color: $select-multiple-input-selected-color;
		>i{
			&.oasicon-delete,&.oasicon-fail{
				color: $select-multiple-delete-color;
				top: 4px;
				right: 2px;
				width: 15px;
				height: 15px;
				&:hover{
					color: $select-multiple-delete-color-hover;
				}
				&:active{
					color: $select-multiple-delete-color-active;
				}
			}
			&.oasicon-fail{
				top: 3px;
				font-size: 14px;
			}
		}
		
	}
}
.oas-select{
	.select-hd{
		&.multiple-hd{
			height: auto;
			min-height: 30px;
			max-height: 114px;
			padding-right: 10px;
			overflow-y: auto;
			white-space: normal;
			.oas-select-hideInput-li{
				max-width: 100%;
				float: left;
				height: 20px;
				margin-top: 8px;
				margin-left: 3px;
				&:focus{
					outline: 0;
				}
				.oas-select-hideInput{
					display: block;
					width: .7em;
					max-width: 100%;
					border-width: 0;
					color: $select-text-color;
					font-size: 100%;
					height: 100%;
					line-height: 100%;
					background: transparent;
					outline: 0;
					border-radius: 4px;	
				}
			}
			.select-placeholder{
				position: absolute;
			}
			&.oas-select-nomatch{
				box-shadow: 0 0 0 3px $select-bd-input-shadow-active-color; 
				border-color: $select-bd-input-active-border-color;
				&:hover{
					border-color: $select-bd-input-active-border-color;
				}
				&:focus{
					border-color: $select-bd-input-active-border-color;
					outline: none;
				}
			}
			.select-placeholder{
				height: 28px;
				margin-left: 3px;
			}
		}
	}
