
/*C级分栏*/
@mixin tabsStyleC-variant($tabsC_type,$tabsC_borderWidth,$tabsC_borderColor,$tabsC_shadowWidth,$tabsC_shadowColor,$tabsC_item_height,$tabsC_item_paddingVertical,
	$tabsC_item_paddingHorizontal,$tabsC_item_iconFontDiff,$tabsC_item_diff,$tabsC_item_fontSize,$tabsC_item_iconSize,
	$tabsC_item_normal_iconColor,$tabsC_item_normal_fontColor,$tabsC_item_normal_bgColor,$tabsC_item_current_iconColor,
  $tabsC_item_current_fontColor,$tabsC_item_current_bgColor-from,$tabsC_item_current_bgColor-to,$tabsC_item_normal_operatorColor,
  $tabsC_item_hover_operatorColor,$tabsC_item_image_normal,$tabsC_item_image_current,$tabsC_circle_color,$tabsC_item_padding_vertical_large,
  $tabsC_item_padding_vertical_middle){

  &.#{$tabsC_type}{
    // border-bottom: $tabsC_borderWidth solid $tabsC_borderColor;
    // box-shadow: 0 $tabsC_shadowWidth 0 $tabsC_shadowColor inset;
    box-shadow: 0 1px 4px $tabs-c-a-shadow-color;
    .tab-items{
      font-size: 0;
      li{
        display: inline-block;
        *display: inline;
        *zoom: 1;
        margin-right: $tabsC_item_diff;
        border-bottom: $tabsC_borderWidth solid transparent;
        background-color: $tabsC_item_normal_bgColor;
         
        a{
         display: block;
         padding:$tabsC_item_paddingVertical $tabsC_item_paddingHorizontal $tabsC_item_paddingVertical - 1;
         text-decoration: none;
         .oasicon{
            font-size: $tabsC_item_iconSize;
            color: $tabsC_item_normal_iconColor;
            vertical-align: middle;
            margin-right: $tabsC_item_iconFontDiff;
            top: 0;
         }
         .tabs-name{
           position: relative;
            font-size: $tabsC_item_fontSize;
            color: $tabsC_item_normal_fontColor;
            vertical-align: middle; 
            &.hasNotification{
              &:after{
                position: absolute;
                content: '';
                display: inline-block;
                width: 6px;
                height: 6px;
                top: 2px;
                right: -6px;
                border-radius: 6px;
                background-color: $tabsC_circle_color;
              }
            }
         }
        .badge{
          margin-left: 12px;
        }
        }
        &.tab-item-last{
          position: relative;
        }
        &.current{
          border-bottom: $tabsC_borderWidth solid $tabsC_borderColor;
          // background-color: $tabsC_item_current_bgColor;
          @include background-linear-gradient-to-bottom($tabsC_item_current_bgColor-from, $tabsC_item_current_bgColor-to);
          a{
            .oasicon{
              color: $tabsC_item_current_fontColor;
            }
            .tabs-name{
              color: $tabsC_item_current_iconColor;
            }
            cursor:default;
          }
          &.tab-item-last{
            &:after{
              // background: sprite($tabs_icon, $tabsC_item_image + "_current");
              // background: image-url("$"+$tabsC_item_image + "_current");
              background: image-url($tabsC_item_image_current);
            }
          }
        }
      }
    }
    .expand-content{
      float: right;
      margin-right: 10px;
      font-size: 0;
      padding-top: 2px;
      a{
        float: right;
        padding:$tabsC_item_paddingVertical 0 $tabsC_item_paddingVertical - 1;
        color: $tabsC_item_normal_operatorColor;
        .oasicon{
          float: left;
          top: 0;
          font-size: $tabsC_item_iconSize;
          vertical-align: middle;
        }
        .operator-text{
          display: inline-block;
          font-size: $tabsC_item_fontSize;
          height: $tabsC_item_fontSize + 2;
          line-height: $tabsC_item_fontSize + 2;
          vertical-align: middle;
        }
        &:first-child{
          .operator-text{
            border-right-width: 0;
          }
        }
        &.menu-setting{
          .oasicon{
            margin-right: $tabsC_item_iconFontDiff;
          }
          .operator-text{
            padding-right: 10px;
          }
        }
        &.menu-assist{
          text-align: right;
          .oasicon{
            margin-left: $tabsC_item_iconFontDiff;
          }
          .operator-text{
            padding-left: 10px;
          }
        }
        &:hover{
        	color: $tabsC_item_hover_operatorColor;
        }
      }
    } 
    &.large{
      .tab-items{
        font-size: 14px;
        a{
          padding:$tabsC_item_padding_vertical_large $tabs-b-item-padding-horizontal;
          >i{
            font-size: 16px;
          }
          .tabs-name{
            font-size: 14px;
          }
          .badge{
            font-size: 14px;
          }
        }
      }
      .expand-content{
        padding-top: 20px;
        .operator-text{
          font-size: 14px;
          height: 16px;
          line-height: 16px;
        }
        i{
          font-size: 16px;
        }
      }
    }
    &.middle{
      .tab-items{
        font-size: 14px;
        a{
          padding:$tabsC_item_padding_vertical_middle $tabs-b-item-padding-horizontal;
          >i{
            font-size: 16px;
          }
          .tabs-name{
            font-size: 14px;
          }
          .badge{
            font-size: 14px;
          }
        }
      }
      .expand-content{
        padding-top: 12px;
        .operator-text{
          font-size: 14px;
          height: 16px;
          line-height: 16px;
        }
        i{
          font-size: 16px;
        }
      }
    }
  }

}
/*D级分栏*/
