     $tabsD_item_fontSize,$tabsD_item_iconSize,$tabsD_item_iconFontDiff,$tabsD_item_normal_iconColor,
     $tabsD_item_normal_fontColor,$tabsD_item_current_iconColor,$tabsD_item_current_fontColor,
     $tabsD_assit_fontSize,$tabsD_assit_fontColor,$tabsD_item_paddingVertical,
     $tabsD_item_normal_operatorColor,$tabsD_item_hover_operatorColor,$tabsD_circle_color,$tabsD_circle_color_default,$tabsD_borderColor){
  &.#{$tabsD_type}{
    font-size: 0;
    // border-bottom: $tabsD_borderWidth solid $tabsD_borderColor;
    .tab-items{
      display: inline-block;
      *display: inline;
      *zoom: 1;
      font-size: 0;
      vertical-align: middle;
      border-left:$tabsD_borderWidth solid $tabsD_borderColor;
      li{
        position: relative;
        display: inline-block;
        *display: inline;
        *zoom: 1;
        padding: 0 $tabsD_A_item_marginRight 0 $tabsD_item_marginLeft;
