            font-size: $tabsD_item_iconSize;
            vertical-align: middle;
            margin-right: $tabsD_item_iconFontDiff;
            color: $tabsD_item_normal_iconColor;
            top: 0;
          }
          .tabs-name{
            position: relative;
            font-size: $tabsD_item_fontSize;
            vertical-align: middle;
            color: $tabsD_item_normal_fontColor;
            &.hasNotification{
              &:after{
                position: absolute;
                content: '';
                display: inline-block;
                width: 6px;
                height: 6px;
                top: 2px;
                right: -6px;
                border-radius: 6px;
                background-color: $tabsD_circle_color_default;
              }
            }
          }
        }
        &.current{
          // border-bottom: 2px solid $tabsD_borderColor_current;
          // &:after{
          //       position: absolute;
          //       content: '';
          //       width: 100%;
          //       height: 2px;
          //       background-color: $tabsD_borderColor_current;
          //       left: 0;
          // }
          a{
            cursor:default;
            .oasicon{
              color: $tabsD_item_current_iconColor;
            }
            .tabs-name{
              color: $tabsD_item_current_fontColor;
              font-weight: bold;
            }
          }
          .tabs-name{
            &.hasNotification{
              &:after{
                background-color: $tabsD_circle_color;
              }
            }
          }
          .badge{
            background-color: $tabsD_circle_color;
          }
