        .operator-text{
          display: inline-block;
          font-size: $tabsD_item_fontSize;
          height: $tabsD_item_fontSize;
          line-height: $tabsD_item_fontSize + 2;
          vertical-align: middle;
        }
        &:first-child{
          .operator-text{
            border-right-width: 0;
          }
        }
        &.menu-setting{
          .oasicon{
            margin-right: $tabsD_item_iconFontDiff;
          }
          .operator-text{
            padding-right: 10px;
          }
        }
        &.menu-assist{
          text-align: right;
          .oasicon{
            margin-left: $tabsD_item_iconFontDiff;
          }
          .operator-text{
            padding-left: 10px;
          }
        }
        &:hover{
        	color: $tabsD_item_hover_operatorColor;
        }
      }
    }
  }
}
