/*C级分栏*/
@mixin layoutStyle($layoutType,$layoutSize,$size){
    
    @if $layoutType == "both"{

        &.layout-#{$layoutSize}{
            
            padding-left: $size;
            padding-right: $size;
            #oas-layout-left{
                width: $size;
                margin-left: - $size;
            }
            #oas-layout-right{
                width: $size;
                margin-right: - $size;
            }
       
        }
    }@else{
        &.layout-#{$layoutSize}{
            padding-#{$layoutType}: $size;
            #oas-layout-#{$layoutType}{
                width: $size;
                margin-#{$layoutType}: - $size;
            }
        }       
    } 
    
}